<template>
  <div class="watchlist-panel">
    <div class="header-container">
      <h1>Real-Time Stock Tracker</h1>
      <div class="nav-links">
        <router-link to="/signals" class="nav-link"> Signals</router-link>
      </div>
      <div class="timestamp-display" :class="timestampStatusClass">
        <span v-if="isTimestampStale" class="alert-triangle"></span>
        <span class="timestamp-text">{{ displayTimestamp }}</span>
      </div>
    </div>
    
    <!-- Alerts Panel -->
    <div class="alerts-panel" v-if="todayAlerts.length > 0 || todayScalpData.length > 0">
      <div class="alerts-container">
        <div class="alerts-section" v-if="todayAlerts.length > 0">
          <div class="alert-items-container">
            <div 
              class="alert-item" 
              v-for="alert in todayAlerts.slice(-10)" 
              :key="alert.message_id" 
              :class="getAlertAgeClass(alert.timestamp)"
              :title="alert.data?.original_message || 'No original message'"
            >
              <span class="alert-source">{{ alert.source || 'Unknown' }}</span>
              <span class="alert-time">{{ formatAlertTimeShort(alert.timestamp) }}</span>
              <span class="alert-symbol">{{ alert.data?.symbol || 'N/A' }}</span>
              <span class="alert-action">{{ alert.data?.action || 'N/A' }}</span>
              <span class="alert-price">${{ alert.data?.price || 'N/A' }}</span>
              <span v-if="alert.data?.raw_action" class="alert-raw-action">raw: {{ alert.data.raw_action }}</span>
              <span v-if="alert.data?.level" class="alert-level">level: {{ alert.data.level }}</span>
              <span v-if="alert.data?.data?.instrument_type" class="alert-instrument">{{ alert.data.data.instrument_type }}</span>
              <span v-if="getPositionSizePercent(alert)" class="alert-position">{{ getPositionSizePercent(alert) }}%</span>
            </div>
          </div>
        </div>
        <div class="alerts-section" v-if="todayScalpData.length > 0">
          <div class="alert-items-container">
            <div 
              class="scalp-item" 
              v-for="scalp in todayScalpData.slice(-10)" 
              :key="scalp.message_id" 
              :class="getAlertAgeClass(scalp.timestamp)"
            >
              <span class="scalp-source">{{ scalp.source || 'Unknown' }}</span>
              <span class="scalp-time">{{ formatAlertTimeShort(scalp.timestamp) }}</span>
              <span class="scalp-symbol">{{ scalp.data?.symbol || 'N/A' }}</span>
              <span class="scalp-signal">{{ scalp.data?.signal || 'N/A' }}</span>
              <span class="scalp-data">{{ scalp.data?.scalp || 'N/A' }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Add Symbol to Watchlist and Mode Toggle -->
    <div class="controls-container">
      <button type="button" @click="bringAllWindowsForward" class="bring-forward-btn" title="Bring all windows to front">
         Bring All Forward
      </button>
      <form @submit.prevent="addSymbol" class="add-symbol-form">
        <input
          type="text"
          v-model="newSymbol"
          placeholder="Enter symbol (e.g., AAPL)"
          @keyup.enter.prevent="addSymbol"
          @dblclick.prevent="selectAllText"
          class="symbol-input"
          ref="symbolInput"
        />
        <button type="submit" :disabled="!newSymbol.trim()" class="add-btn">+</button>
        <button type="button" @click="toggleMode" :class="{ active: timeMachineMode }" class="mode-btn-compact">
          {{ timeMachineMode ? '' : '' }}
        </button>
        <div v-if="addSymbolMessage" :class="['message-inline', addSymbolStatus]">
          {{ addSymbolMessage }}
        </div>
      </form>
    </div>
    
    <!-- Time Machine Controls -->
    <div class="time-machine-controls" v-if="timeMachineMode">
      <div class="time-controls">
        <input type="date" v-model="selectedDate" @change="loadAvailableTimestamps" />
        <input 
          type="range" 
          v-model="currentTimestampIndex" 
          :max="availableTimestamps.length - 1" 
          @input="seekToTimestamp"
          class="time-slider"
        />
        <div class="playback-controls">
          <button @click="play" :disabled="isPlaying" class="play-btn"></button>
          <button @click="pause" :disabled="!isPlaying" class="pause-btn"></button>
          <select v-model="playbackSpeed" class="speed-select">
            <option value="1">1x</option>
            <option value="5">5x</option>
            <option value="10">10x</option>
            <option value="60">60x</option>
          </select>
        </div>
      </div>
    </div>
    
    <div class="info">
      Divergences: BD = Bullish Divergence, BR = Bearish Divergence | BB% = Position within Bollinger Bands | CMF = Chaikin Money Flow | VWAP = Distance from VWAP
    </div>
    
    <div class="table-container">
      <table>
        <thead>
          <tr class="group-header">
            <th rowspan="2" class="col-symbol">Symbol</th>
            <th rowspan="2" class="col-price">Price</th>
            <th rowspan="2" class="col-percent">%</th>
            <th rowspan="2" class="col-macd-bar">MACD</th>
            <th colspan="5">Relative Strength Index (RSI)</th>
            <th rowspan="2" class="col-ema-bar">9/21</th>
            <th rowspan="2" class="col-ema-bar">21/50</th>
            <!-- <th colspan="5">MACD Div</th>
            <th colspan="5">RSI Div</th> -->
            <th rowspan="2" class="col-cmf-bar">Chakin Money Flow (CMF)</th>
            <th rowspan="2" class="col-vwap-bar">VWAP</th>
            <th rowspan="2" class="col-squeeze-bar">Squeeze</th>
            <th rowspan="2" class="col-volume">Vol%</th>
            <th colspan="5">Signal</th>
            <th colspan="5">Stochastics</th>
            <th colspan="5">BB%</th>
          </tr>
          <tr>
            <th class="col-compact">1m</th>
            <th class="col-compact">2m</th>
            <th class="col-compact">3m</th>
            <th class="col-compact">5m</th>
            <th class="col-compact">15m</th>
            <!-- Removed orphaned MACD Div timeframes -->
            <!-- Removed orphaned RSI Div timeframes -->
            <!-- Removed orphaned Squeeze timeframes -->
            <!-- Removed orphaned Signal timeframes -->
            <th class="col-compact">1m</th>
            <th class="col-compact">2m</th>
            <th class="col-compact">3m</th>
            <th class="col-compact">5m</th>
            <th class="col-compact">15m</th>
            <th class="col-compact">1m</th>
            <th class="col-compact">2m</th>
            <th class="col-compact">3m</th>
            <th class="col-compact">5m</th>
            <th class="col-compact">15m</th>
            <th class="col-compact">1m</th>
            <th class="col-compact">2m</th>
            <th class="col-compact">3m</th>
            <th class="col-compact">5m</th>
            <th class="col-compact">15m</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="symbol in sortedSymbols" :key="symbol">
            <td class="col-symbol">
              <button @click="removeSymbol(symbol)" class="remove-btn" title="Remove from watchlist">
                
              </button>
              <span class="symbol-with-badge">
                {{ symbol }}
                <span v-if="getSymbolSource(symbol) === 'smb' || getSymbolSource(symbol) === 'ttg'" 
                      class="alert-badge" 
                      :class="getSymbolSource(symbol)">
                  {{ getSymbolSource(symbol).toUpperCase() }}
                </span>
              </span>
            </td>
            <td class="col-price">
              <span :class="getPriceChangeClass(symbol)">{{ getPriceDirection(symbol) }}</span>
              {{ getPrice(symbol) }}
            </td>
            <td class="col-percent" :class="getPriceChangeClass(symbol)">{{ getPricePercent(symbol) }}</td>
            
            <!-- MACD horizontal bar -->
            <td class="col-macd-bar">
              <div class="macd-horizontal-bar">
                <div 
                  v-for="(period, index) in indicatorPeriods" 
                  :key="'macd-segment-' + period"
                  class="macd-segment"
                  :class="getMacdDotClass(symbol, period)"
                  :style="{ width: '20%', left: (index * 20) + '%' }"
                ></div>
              </div>
            </td>
            
            <!-- RSI columns -->
            <td class="col-compact" v-for="period in indicatorPeriods" :key="'rsi-' + period" :class="getRsiClass(symbol, period)" v-html="getRsiValue(symbol, period)">
            </td>
            
            <!-- 9/21 EMA horizontal bar -->
            <td class="col-ema-bar">
              <div class="ema-horizontal-bar">
                <div 
                  v-for="(period, index) in indicatorPeriods" 
                  :key="'ema921-segment-' + period"
                  class="ema-segment"
                  :class="getEmaDotClass(symbol, period, '9', '21')"
                  :style="{ width: '20%', left: (index * 20) + '%' }"
                ></div>
              </div>
            </td>
            
            <!-- 21/50 EMA horizontal bar -->
            <td class="col-ema-bar">
              <div class="ema-horizontal-bar">
                <div 
                  v-for="(period, index) in indicatorPeriods" 
                  :key="'ema2150-segment-' + period"
                  class="ema-segment"
                  :class="getEmaDotClass(symbol, period, '21', '50')"
                  :style="{ width: '20%', left: (index * 20) + '%' }"
                ></div>
              </div>
            </td>
            
            <!-- MACD Divergence columns -->
            <!-- <td class="col-compact" v-for="period in indicatorPeriods" :key="'macd-div-' + period">
              {{ getDivergence(symbol, period, 'macd') }}
            </td> -->
            
            <!-- RSI Divergence columns -->
            <!-- <td class="col-compact" v-for="period in indicatorPeriods" :key="'rsi-div-' + period">
              {{ getDivergence(symbol, period, 'rsi') }}
            </td> -->
            
            <!-- CMF horizontal bar -->
            <td class="col-cmf-bar">
              <div class="cmf-horizontal-bar">
                <div 
                  v-for="(period, index) in indicatorPeriods" 
                  :key="'cmf-segment-' + period"
                  class="cmf-segment"
                  :style="getCMFSegmentStyle(symbol, period, index)"
                >
                  <span class="cmf-value">{{ getCMFShort(symbol, period) }}</span>
                </div>
              </div>
            </td>
            
            <!-- VWAP horizontal bar -->
            <td class="col-vwap-bar">
              <div class="vwap-horizontal-bar">
                <div 
                  v-for="(period, index) in indicatorPeriods" 
                  :key="'vwap-segment-' + period"
                  class="vwap-segment"
                  :style="getVWAPSegmentStyle(symbol, period, index)"
                >
                  <span class="vwap-value">{{ getVWAPShort(symbol, period) }}</span>
                </div>
              </div>
            </td>
            
            <!-- Squeeze horizontal bar -->
            <td class="col-squeeze-bar">
              <div class="squeeze-horizontal-bar">
                <div 
                  v-for="(period, index) in indicatorPeriods" 
                  :key="'squeeze-segment-' + period"
                  class="squeeze-segment"
                  :style="getSqueezeSegmentStyle(symbol, period, index)"
                ></div>
              </div>
            </td>
            
            <!-- Volume % -->
            <td class="col-volume" :class="getVolumeClass(symbol)">{{ getVolumePercent(symbol) }}</td>
            
            <!-- Signal columns -->
            <td class="col-compact" v-for="period in indicatorPeriods" :key="'signal-' + period">
              {{ getSignal(symbol, period) }}
            </td>
            
            <!-- Stochastic columns -->
            <td class="col-compact" v-for="period in indicatorPeriods" :key="'stoch-' + period" :class="getStochasticClass(symbol, period)" v-html="getStochasticValue(symbol, period)">
            </td>
            
            <!-- BB% columns -->
            <td class="col-compact" v-for="period in indicatorPeriods" :key="'bb-' + period">
              {{ getBollingerBands(symbol, period) }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    
    <div class="info" style="margin-top: 15px;">
      <strong>Key:</strong> BD = Bullish Divergence (price makes lower low, indicator makes higher low) | BR = Bearish Divergence (price makes higher high, indicator makes lower high) | 
      BB% &lt; 20% = Oversold, &gt; 80% = Overbought | CMF &gt; 0 = Accumulation, &lt; 0 = Distribution | VWAP shows distance from VWAP (mean reversion opportunities)
    </div>

    <!-- Connection Status -->
    <div class="connection-status">
      <span :class="connectionStatusClass">
        {{ connectionStatus }}
      </span>
    </div>
  </div>
</template>

<script>
// Constants
const INDICATOR_PERIODS = ['1min', '2min', '3min', '5min', '15min'];

export default {
  name: "WatchList",
  data() {
    return {
      indicatorsMap: {},          // All indicator data by symbol
      watchlist: [],              // Current watchlist symbols
      indicatorsWs: null,         // WebSocket connection
      connectionStatus: 'Disconnected',
      reconnectDelay: 3000,
      isReconnecting: false,
      debugCounter: 0,            // Simple reactivity test
      wsConnectionId: null,       // Unique ID for each WebSocket connection
      lastSymbolCount: null,      // Track symbol count changes
      pendingAdds: new Set(),     // Symbols being added (optimistic)
      pendingRemoves: new Set(),  // Symbols being removed (optimistic)
      
      // Time machine functionality
      timeMachineMode: false,
      selectedDate: '', // Will be set in mounted()
      availableTimestamps: [],
      currentTimestampIndex: 0,
      isPlaying: false,
      playbackSpeed: 1,
      playbackInterval: null,
      sliderDebounceTimeout: null,
      latestDataTimestamp: null,
      currentDisplayTimestamp: null,
      
      // Add symbol functionality
      newSymbol: '',
      addSymbolMessage: '',
      addSymbolStatus: '',
      
      // Alerts functionality
      alertsWs: null,
      alertsData: {
        alerts: [],
        scalp_data: [],
        last_updated: null
      }
    };
  },
  computed: {
    sortedSymbols() {
      // Get all symbols from indicators data, filter out empty symbols
      const symbols = Object.keys(this.indicatorsMap)
        .filter(symbol => symbol && symbol.trim() !== '');
      
      // Sort by alert status first (alerts on top), then alphabetically
      return symbols.sort((a, b) => {
        const aSource = this.getSymbolSource(a);
        const bSource = this.getSymbolSource(b);
        
        // Check if symbols are from alerts (smb or ttg)
        const aIsAlert = aSource === 'smb' || aSource === 'ttg';
        const bIsAlert = bSource === 'smb' || bSource === 'ttg';
        
        // If one is alert and other is not, alert comes first
        if (aIsAlert && !bIsAlert) return -1;
        if (!aIsAlert && bIsAlert) return 1;
        
        // If both are alerts or both are not alerts, sort alphabetically
        return a.localeCompare(b);
      });
    },
    connectionStatusClass() {
      return {
        'status-connected': this.connectionStatus === 'Connected',
        'status-connecting': this.connectionStatus === 'Connecting...',
        'status-disconnected': this.connectionStatus === 'Disconnected'
      };
    },
    
    currentTimestamp() {
      return this.availableTimestamps[this.currentTimestampIndex] || 0;
    },
    
    displayTimestamp() {
      const timestamp = this.timeMachineMode ? this.currentDisplayTimestamp : this.latestDataTimestamp;
      if (!timestamp) return 'No data';
      
      const date = new Date(timestamp * 1000);
      const now = new Date();
      
      // If timestamp is very recent (within 5 seconds), show "Live" instead of exact time
      const ageInSeconds = (now.getTime() - date.getTime()) / 1000;
      if (!this.timeMachineMode && ageInSeconds < 5) {
        return 'Live';
      }
      
      // Format date and time in Eastern timezone with local timezone in parentheses
      const easternTime = date.toLocaleString('en-US', {
        timeZone: 'America/New_York',
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false
      });
      
      const localTime = date.toLocaleTimeString('en-US', {
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false
      });
      
      return `${easternTime} EDT (${localTime} local)`;
    },
    
    isTimestampStale() {
      if (!this.latestDataTimestamp || this.timeMachineMode) return false;
      
      const now = Date.now() / 1000;
      const age = now - this.latestDataTimestamp;
      return age > 10; // More than 10 seconds old
    },
    
    timestampStatusClass() {
      return {
        'timestamp-fresh': !this.isTimestampStale,
        'timestamp-stale': this.isTimestampStale
      };
    },
    
    indicatorPeriods() {
      return INDICATOR_PERIODS;
    },
    
    todayAlerts() {
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      const todayTimestamp = today.getTime() / 1000;
      
      return this.alertsData.alerts.filter(alert => {
        if (!alert.timestamp) return false;
        const alertDate = new Date(alert.timestamp * 1000);
        alertDate.setHours(0, 0, 0, 0);
        const alertTimestamp = alertDate.getTime() / 1000;
        return alertTimestamp === todayTimestamp;
      });
    },
    
    todayScalpData() {
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      const todayTimestamp = today.getTime() / 1000;
      
      return this.alertsData.scalp_data.filter(scalp => {
        if (!scalp.timestamp) return false;
        const scalpDate = new Date(scalp.timestamp * 1000);
        scalpDate.setHours(0, 0, 0, 0);
        const scalpTimestamp = scalpDate.getTime() / 1000;
        return scalpTimestamp === todayTimestamp;
      });
    }
  },
  mounted() {
    this.selectedDate = this.getTodaysDate();
    this.fetchWatchlist();
    this.initIndicatorsWebSocket();
    this.initAlertsWebSocket();
  },
  beforeUnmount() {
    if (this.indicatorsWs) {
      this.indicatorsWs.close();
    }
    if (this.alertsWs) {
      this.alertsWs.close();
    }
    if (this.sliderDebounceTimeout) {
      clearTimeout(this.sliderDebounceTimeout);
    }
    this.pause(); // Clear any playback intervals
  },
  methods: {
    // Time Machine functionality
    toggleMode() {
      // Clear any pending slider debounce
      if (this.sliderDebounceTimeout) {
        clearTimeout(this.sliderDebounceTimeout);
        this.sliderDebounceTimeout = null;
      }
      
      this.timeMachineMode = !this.timeMachineMode;
      
      if (this.timeMachineMode) {
        // Switch to time machine mode
        this.pause(); // Stop any playback
        if (this.indicatorsWs) {
          this.indicatorsWs.close();
        }
        this.connectionStatus = 'Time Machine Mode';
        this.loadAvailableTimestamps();
      } else {
        // Switch back to live mode
        this.pause();
        this.connectionStatus = 'Disconnected';
        this.initIndicatorsWebSocket();
      }
    },
    
    async loadAvailableTimestamps() {
      try {
        // Use dedicated bulk timestamps endpoint - more efficient than per-symbol call
        const response = await fetch(`http://${window.location.hostname}:8000/api/timemachine/timestamps?date=${this.selectedDate}`);
        
        if (response.ok) {
          const data = await response.json();
          this.availableTimestamps = data.timestamps;
          this.currentTimestampIndex = 0;
          
          if (this.availableTimestamps.length > 0) {
            await this.updateWatchlistData();
          }
        }
      } catch (err) {
        console.error("Failed to load timestamps:", err);
      }
    },
    
    seekToTimestamp() {
      // Clear any existing timeout
      if (this.sliderDebounceTimeout) {
        clearTimeout(this.sliderDebounceTimeout);
      }
      
      // Set a new timeout to call the API after user stops moving slider
      this.sliderDebounceTimeout = setTimeout(async () => {
        if (this.timeMachineMode && this.availableTimestamps.length > 0) {
          await this.updateWatchlistData();
        }
      }, 300); // Wait 300ms after user stops moving slider
    },
    
    play() {
      if (!this.timeMachineMode || this.availableTimestamps.length === 0) return;
      
      this.isPlaying = true;
      this.playbackInterval = setInterval(() => {
        if (this.currentTimestampIndex < this.availableTimestamps.length - 1) {
          this.currentTimestampIndex++;
          this.updateWatchlistData();
        } else {
          this.pause(); // Stop at end
        }
      }, 1000 / this.playbackSpeed);
    },
    
    pause() {
      this.isPlaying = false;
      if (this.playbackInterval) {
        clearInterval(this.playbackInterval);
        this.playbackInterval = null;
      }
    },
    
    async updateWatchlistData() {
      if (!this.timeMachineMode || !this.currentTimestamp) {
        return;
      }
      
      try {
        const response = await fetch(`http://${window.location.hostname}:8000/api/timemachine/bulk?timestamp=${this.currentTimestamp}`);
        
        if (response.ok) {
          const data = await response.json();
          this.updateSymbolsWithHistoricalData(data.symbols);
          
          // Update timestamp for time machine mode
          if (data.timestamp) {
            this.currentDisplayTimestamp = data.timestamp / 1000; // Convert from ms to seconds
          }
        }
      } catch (err) {
        console.error("Failed to update watchlist data:", err);
      }
    },
    
    updateSymbolsWithHistoricalData(historicalSymbols) {
      // Convert historical data to same format as live data
      if (!historicalSymbols) {
        return;
      }
      
      const updatedIndicatorsMap = {};
      
      for (const [symbol, periods] of Object.entries(historicalSymbols)) {
        // Skip empty or invalid symbols
        if (!symbol || symbol.trim() === '') {
          continue;
        }
        
        updatedIndicatorsMap[symbol] = {};
        
        for (const [period, data] of Object.entries(periods)) {
          if (data) {
            updatedIndicatorsMap[symbol][period] = {
              timestamp: data.timestamp,
              last_updated: new Date().toISOString(),
              indicators: {
                bar: data.ohlc,
                indicator: data.indicators
              }
            };
          }
        }
      }
      
      this.indicatorsMap = updatedIndicatorsMap;
      this.debugCounter++;
    },
    
    formatTime(timestamp) {
      if (!timestamp) return '--:--';
      const date = new Date(timestamp * 1000);
      return date.toLocaleTimeString('en-US', { 
        hour12: false, 
        hour: '2-digit', 
        minute: '2-digit' 
      });
    },
    
    async addSymbol() {
      if (!this.newSymbol.trim()) return;
      
      const symbol = this.newSymbol.trim().toUpperCase();
      
      try {
        const response = await fetch(`http://${window.location.hostname}:8000/api/indicators/symbol`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ symbol: symbol })
        });
        
        if (response.ok) {
          await response.json();
          this.newSymbol = ''; // Clear the input
          
          // Mark as pending add to prevent WebSocket from removing it
          this.pendingAdds.add(symbol);
          
          // Add empty entry to table immediately for better UX
          this.indicatorsMap[symbol] = {
            source: 'daily_watchlist',
            periods: {}
          };
        } else {
          const errorData = await response.json();
          this.addSymbolMessage = errorData.detail || `Failed to add ${symbol}`;
          this.addSymbolStatus = 'error';
          
          // Auto-clear error message after 5 seconds
          setTimeout(() => {
            this.addSymbolMessage = '';
            this.addSymbolStatus = '';
          }, 5000);
        }
      } catch (err) {
        console.error('Error adding symbol:', err);
        this.addSymbolMessage = 'Network error. Please try again.';
        this.addSymbolStatus = 'error';
        
        setTimeout(() => {
          this.addSymbolMessage = '';
          this.addSymbolStatus = '';
        }, 5000);
      }
    },
    
    async removeSymbol(symbol) {
      if (!confirm(`Are you sure you want to remove ${symbol} from the watchlist?`)) {
        return;
      }
      
      // Log removal timestamp and current symbol count
      const removalTimestamp = new Date().toISOString();
      const currentSymbolCount = Object.keys(this.indicatorsMap).length;
      console.log(` REMOVE CLICKED: Symbol=${symbol}, Time=${removalTimestamp}, CurrentSymbolCount=${currentSymbolCount}`);
      
      try {
        const response = await fetch(`http://${window.location.hostname}:8000/api/indicators/symbol/${symbol}`, {
          method: 'DELETE',
        });
        
        if (response.ok) {
          await response.json();
          
          // Mark as pending remove to prevent WebSocket from adding it back
          this.pendingRemoves.add(symbol);
          
          // Remove from frontend immediately for better UX
          delete this.indicatorsMap[symbol];
        } else {
          const errorData = await response.json();
          this.addSymbolMessage = errorData.detail || `Failed to remove ${symbol}`;
          this.addSymbolStatus = 'error';
          
          // Auto-clear error message after 5 seconds
          setTimeout(() => {
            this.addSymbolMessage = '';
            this.addSymbolStatus = '';
          }, 5000);
        }
      } catch (err) {
        console.error('Error removing symbol:', err);
        this.addSymbolMessage = 'Network error. Please try again.';
        this.addSymbolStatus = 'error';
        
        setTimeout(() => {
          this.addSymbolMessage = '';
