    formatAlertTime(timestamp) {
      if (!timestamp) return '--:--';
      const date = new Date(timestamp * 1000);
      
      const easternTime = date.toLocaleTimeString('en-US', {
        timeZone: 'America/New_York',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false
      });
      
      const localTime = date.toLocaleTimeString('en-US', {
        hour: '2-digit',
        minute: '2-digit', 
        second: '2-digit',
        hour12: false
      });
      
      return `${easternTime} EDT (${localTime} local)`;
    },
    
    formatAlertTimeShort(timestamp) {
      if (!timestamp) return '--:--';
      const date = new Date(timestamp * 1000);
      
      const easternTime = date.toLocaleTimeString('en-US', {
        timeZone: 'America/New_York',
        hour: '2-digit',
        minute: '2-digit',
        hour12: false
      });
      
      const localTime = date.toLocaleTimeString('en-US', {
        hour: '2-digit',
        minute: '2-digit',
        hour12: false
      });
      
      return `${easternTime} (${localTime})`;
    },
    
    formatAlertData(data) {
      if (!data) return 'No data';
      
      // Convert data object to readable format
      const entries = Object.entries(data);
      if (entries.length === 0) return 'Empty';
      
      // Join key-value pairs
      return entries.map(([key, value]) => `${key}: ${value}`).join(', ');
    },
    
    getPositionSizePercent(alert) {
      // Try to extract position_size_percent from various possible locations
      if (alert.data?.position_size_percent) {
        return alert.data.position_size_percent;
      }
      if (alert.data?.position_size) {
        return alert.data.position_size;
      }
      if (alert.data?.data?.position_size_percent) {
        return alert.data.data.position_size_percent;
      }
      return null;
    },
    
    getAlertAgeClass(timestamp) {
      if (!timestamp) return 'alert-old';
      
      const now = Date.now() / 1000; // Current time in seconds
      const age = now - timestamp; // Age in seconds
      const tenMinutes = 10 * 60; // 10 minutes in seconds
      
      return age <= tenMinutes ? 'alert-recent' : 'alert-old';
    },
    
    mapPeriodKey(period) {
      // Direct mapping - periods now match data keys exactly
      return period;
