    async addToWatchlist(symbol) {
      if (this.isInWatchlist(symbol)) return;
      
      try {
        const response = await fetch(`http://${window.location.hostname}:8000/api/watchlist`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ symbols: symbol }),
        });
        
        if (response.ok) {
          const data = await response.json();
          this.watchlist = data.watchlist;
          console.log(`Added ${symbol} to watchlist`);
        } else {
          throw new Error(`Add to watchlist failed: HTTP ${response.status}`);
