    initIndicatorsWebSocket() {
      if (this.isReconnecting) return;
      
      // Close any existing connection first
      if (this.indicatorsWs && this.indicatorsWs.readyState !== WebSocket.CLOSED) {
        console.log(' Closing existing WebSocket before creating new one');
        this.indicatorsWs.close();
      }
      
      this.connectionStatus = 'Connecting...';
      const wsUrl = `ws://${window.location.hostname}:8000/ws/indicators`;
      this.wsConnectionId = Math.random().toString(36).substring(7);
      console.log(` Creating new WebSocket connection to ${wsUrl} with ID: ${this.wsConnectionId}`);
      this.indicatorsWs = new WebSocket(wsUrl);

      this.indicatorsWs.onopen = () => {
        console.log(`Indicators WebSocket connected (ID: ${this.wsConnectionId})`);
        this.connectionStatus = 'Connected';
        this.isReconnecting = false;
      };

      this.indicatorsWs.onmessage = (evt) => {
        try {
          const msg = JSON.parse(evt.data);
          
          if (msg.type === "INDICATORS_UPDATE") {
            // Minimal logging - only log when symbol count changes
            const wsSymbols = Object.keys(msg.data || {});
            if (!this.lastSymbolCount || this.lastSymbolCount !== wsSymbols.length) {
              console.log(` WS: Count ${this.lastSymbolCount || 0}  ${wsSymbols.length} symbols`);
              this.lastSymbolCount = wsSymbols.length;
            }
            
            // In Vue 3, we need to handle updates carefully
            const newData = msg.data || {};
            
            // Get current symbols for comparison
            const currentSymbols = Object.keys(this.indicatorsMap || {});
            
            // Handle optimistic updates - don't let WebSocket override pending operations
            const finalData = { ...newData };
            
            // Don't remove symbols that are pending adds (user just added them)
            for (const symbol of this.pendingAdds) {
              if (!finalData[symbol]) {
                finalData[symbol] = {
                  source: 'daily_watchlist',
                  periods: {}
                };
              } else {
                // Backend confirmed the add, remove from pending
                this.pendingAdds.delete(symbol);
              }
            }
            
            // Don't add back symbols that are pending removals (user just removed them)
            for (const symbol of this.pendingRemoves) {
              if (finalData[symbol]) {
                delete finalData[symbol];
              } else {
                // Backend confirmed the removal, remove from pending
                this.pendingRemoves.delete(symbol);
              }
            }
            
            // Log actual changes after optimistic handling
            const finalSymbols = Object.keys(finalData);
            const removedSymbols = currentSymbols.filter(s => !finalSymbols.includes(s));
            const addedSymbols = finalSymbols.filter(s => !currentSymbols.includes(s));
            
            if (removedSymbols.length > 0) {
              console.log(` Removed: ${removedSymbols.join(', ')}`);
            }
            if (addedSymbols.length > 0) {
              console.log(` Added: ${addedSymbols.join(', ')}`);
            }
            
            // Apply the final data with optimistic updates respected
            this.indicatorsMap = finalData;
            
            this.debugCounter++; // Simple reactivity test
            
            // Update timestamp for real-time data freshness indicator
            if (msg.latest_pubsub_timestamp) {
              this.latestDataTimestamp = msg.latest_pubsub_timestamp;
            } else {
              console.log("No latest_pubsub_timestamp in message");
            }
          }
        } catch (error) {
          console.error("Error parsing indicators message:", error);
          console.error("Raw message data:", evt.data);
          console.error("Message length:", evt.data.length);
          // Close and reconnect on parse error
          console.log("Closing WebSocket due to parse error and reconnecting...");
          this.indicatorsWs.close();
        }
      };

      this.indicatorsWs.onerror = (err) => {
        console.error("Indicators WebSocket error:", err);
        console.error("WebSocket error details:", {
          type: err.type,
          target: err.target,
          timeStamp: err.timeStamp
        });
        this.connectionStatus = 'Disconnected';
      };

      this.indicatorsWs.onclose = (evt) => {
        console.log("Indicators WebSocket closed:", evt.code, evt.reason);
        console.log("WebSocket close details:", {
          code: evt.code,
          reason: evt.reason,
          wasClean: evt.wasClean,
          timeStamp: evt.timeStamp
        });
        this.connectionStatus = 'Disconnected';
        
        // IMPORTANT: Do NOT clear indicatorsMap on disconnect
        // We want to preserve the state between reconnections
        
        if (!this.isReconnecting && !this.timeMachineMode) {
          this.isReconnecting = true;
          console.log(`Reconnecting indicators WebSocket in ${this.reconnectDelay}ms...`);
          setTimeout(() => {
            console.log("Attempting to reconnect indicators WebSocket...");
            this.isReconnecting = false; // Reset flag before reconnecting
            this.initIndicatorsWebSocket();
          }, this.reconnectDelay);
        }
      };
    },

