    getBollingerBands(symbol, period) {
      const symbolData = this.indicatorsMap[symbol];
      const periodKey = this.mapPeriodKey(period);
      
      // Get price from the correct location
      const price = symbolData?.periods?.[periodKey]?.indicators?.bar?.close;
      const upper = symbolData?.periods?.[periodKey]?.indicators?.indicator?.bands?.bollinger20_2?.upper;
      const lower = symbolData?.periods?.[periodKey]?.indicators?.indicator?.bands?.bollinger20_2?.lower;
      
      if (price !== undefined && upper !== undefined && lower !== undefined) {
        const p = parseFloat(price);
        const u = parseFloat(upper);
        const l = parseFloat(lower);
        const percent = ((p - l) / (u - l)) * 100;
        const result = `${percent.toFixed(0)}%`;
        return result;
      }
      return '-';
