    getVWAPDistance(symbol, period) {
      const symbolData = this.indicatorsMap[symbol];
      const periodKey = this.mapPeriodKey(period);

      // Get price from the correct location
      const price = symbolData?.periods?.[periodKey]?.indicators?.bar?.close;
      const vwap = symbolData?.periods?.[periodKey]?.indicators?.indicator?.volumeIndicators?.vwapRetail;
      
      if (price !== undefined && vwap !== undefined) {
        const distance = ((parseFloat(price) - parseFloat(vwap)) / parseFloat(vwap)) * 100;
        const sign = distance >= 0 ? '+' : '';
        return `${sign}${distance.toFixed(1)}%`;
      }
      return '-';
    },
    
    getVWAPShort(symbol, period) {
      const symbolData = this.indicatorsMap[symbol];
      const periodKey = this.mapPeriodKey(period);

      // Get price from the correct location
      const price = symbolData?.periods?.[periodKey]?.indicators?.bar?.close;
      const vwap = symbolData?.periods?.[periodKey]?.indicators?.indicator?.volumeIndicators?.vwapRetail;
      
      if (price !== undefined && vwap !== undefined) {
        const distance = ((parseFloat(price) - parseFloat(vwap)) / parseFloat(vwap)) * 100;
        if (Math.abs(distance) < 0.05) {
          return '0';  // Very small values show as 0
        }
        return distance.toFixed(1);  // One decimal place, no + sign or %
      }
      return '-';
    },
    
    getVWAPSegmentStyle(symbol, period, index) {
      const symbolData = this.indicatorsMap[symbol];
      const periodKey = this.mapPeriodKey(period);

      // Get price from the correct location
      const price = symbolData?.[periodKey]?.indicators?.bar?.close;
      const vwap = symbolData?.[periodKey]?.indicators?.indicator?.volumeIndicators?.vwapRetail;
      
      if (price !== undefined && vwap !== undefined) {
        const distance = ((parseFloat(price) - parseFloat(vwap)) / parseFloat(vwap)) * 100;
        
        // Determine color and intensity based on VWAP distance (exact same as CMF)
        let backgroundColor;
        if (Math.abs(distance) < 0.05) {
          // Near VWAP - neutral light color (very close to VWAP)
          backgroundColor = '#f8f9fa';
        } else if (distance > 0.05) {
          // Above VWAP - green (same scaling as CMF but for VWAP percentages)
          const intensity = Math.min(Math.abs(distance) / 2.0, 1); // Scale to 2% threshold
          const redValue = Math.floor(220 - (220 * intensity)); // Start light, get darker
          const greenValue = Math.floor(255 - (47 * intensity)); // Start very light green, get darker
          const blueValue = Math.floor(220 - (88 * intensity)); 
          backgroundColor = `rgb(${redValue}, ${greenValue}, ${blueValue})`;
        } else if (distance < -0.05) {
          // Below VWAP - red (same scaling as CMF but for VWAP percentages)
          const intensity = Math.min(Math.abs(distance) / 2.0, 1); // Scale to 2% threshold
          const redValue = Math.floor(255 - (47 * intensity)); // Start very light red, get darker
          const greenValue = Math.floor(220 - (164 * intensity)); // Start light, get darker
          const blueValue = Math.floor(220 - (124 * intensity));
          backgroundColor = `rgb(${redValue}, ${greenValue}, ${blueValue})`;
        } else {
          // Fallback for edge cases
          backgroundColor = '#f8f9fa';
        }
        
        return {
          backgroundColor,
          width: '20%',
          left: (index * 20) + '%'
        };
      }
      
      // No data - gray
      return {
        backgroundColor: '#f8f9fa',
        width: '20%',
        left: (index * 20) + '%'
      };
    },
    
    getSqueezeDotClass(symbol, period) {
