    async bringAllWindowsForward() {
      // Call /focus endpoint on all 6 window servers (ports 10001-10006)
      const focusPromises = [];

      for (let windowNum = 1; windowNum <= 6; windowNum++) {
        const port = 10000 + windowNum;
        const promise = fetch(`http://localhost:${port}/focus`, {
          method: 'POST'
        }).catch(err => {
          console.error(`Failed to focus window ${windowNum}:`, err);
        });
        focusPromises.push(promise);
      }

      // Wait for all focus requests to complete
      await Promise.all(focusPromises);
      console.log('Brought all windows to front');
    },
  },
};
</script>

