<template>
  <div class="orders-panel">
    <h3>
      Orders
      <span class="order-filters">
        <label>
          <input type="checkbox" v-model="selectedStatuses.All" /> All
        </label>
        <label>
          <input type="checkbox" v-model="selectedStatuses.Open" /> Open
        </label>
        <label>
          <input type="checkbox" v-model="selectedStatuses.Closed" /> Closed
        </label>
        <label>
          <input type="checkbox" v-model="selectedStatuses.Canceled" /> Canceled
        </label>
        <label>
          <input type="checkbox" v-model="selectedStatuses.Rejected" /> Rejected
        </label>
      </span>
    </h3>
    <table>
      <thead>
        <tr>
          <th>Symbol</th>
          <th>Order ID</th>
          <th>Side</th>
          <th>Qty</th>
          <th>Price</th>
          <th>Status</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="(order, index) in filteredOrders" :key="order.order_id || index">
          <tr>
            <td :class="{ 'current-symbol': order.symbol === currentSymbol }">{{ order.symbol }}</td>
            <td>{{ order.order_id }}</td>
            <td>{{ order.action }}</td>
            <td>{{ order.quantity_ordered }}</td>
            <td class="price-column">
              <div><strong>{{ order.display_price }}</strong></div>
              <br>
              <div 
                v-if="order.action === 'Sell' || order.action === 'BuyToCover'"
                class="order-pl" 
                :class="{ 
                  positive: order.pl > 0, 
                  negative: order.pl < 0, 
                  breakeven: order.pl === 0 
                }"
              >
                <div>
                  {{ order.pl > 0 
                      ? 'Min Profit' 
                      : (order.pl < 0 
                          ? 'MaxL' 
                          : 'Breakeven'
                        ) }}
                </div>
                <div :class="{ 'current-symbol': order.symbol === currentSymbol }">
                  {{ (order.pl > 0 ? '+' : (order.pl < 0 ? '-' : '')) + '$' + Math.abs(order.pl).toFixed(2) }}
                </div>
              </div>
            </td>
            <td>{{ order.status }}</td>
            <td class="actions-cell">
              <!-- Simple Cancel button for open orders that don't have the full action layout -->
              <button 
                v-if="isOpenOrder(order) && !(order.symbol === currentSymbol && order.details && (order.details.TrailingStop != null || (order.details.MarketActivationRules?.length || 0) > 0))" 
                class="button btn-cancel" 
                @click="cancelOrder(order)"
              >
                Cancel
              </button>
              
              <div v-if="order.symbol === currentSymbol && order.details && (order.details.TrailingStop != null || (order.details.MarketActivationRules?.length || 0) > 0)" class="orders-action-layout">
                <!-- 2x3 Button Table -->
                <table class="button-table">
                  <tr>
                    <td><button class="button btn-cancel" @click="cancelOrder(order)">Cancel</button></td>
                    <td><button class="button btn-breakeven" @click="setEmaTrail(order, 'breakeven')">Breakeven</button></td>
                    <td><button class="button btn-flatten" @click="closeOrder(order, 1)">Flatten</button></td>
                  </tr>
                  <tr>
                    <td><button class="button btn-stop-ema-6" @click="setEmaTrail(order, 'ema6')">SL: 6ema</button></td>
                    <td><button class="button btn-stop-ema-9" @click="setEmaTrail(order, 'ema9')">SL: 9ema</button></td>
                    <td><button class="button btn-stop-ema-21" @click="setEmaTrail(order, 'ema21')">SL: 21ema</button></td>
                  </tr>
                </table>
                
                <!-- Input fields and buttons -->
                <div class="input-row">
                  <input type="number" v-model.number="manualStopLoss[order.order_id]" :placeholder="`SL for ${order.symbol}`" class="manual-input-field"/>
                  <button class="button manual-input-btn" @click="submitManualStopLoss(order)" :disabled="Number(order.quantity_ordered) === 0 || !manualStopLoss[order.order_id]">Set-SL</button>
                </div>
                
                <div class="input-row">
                  <input type="number" v-model.number="trailingStopLoss[order.order_id]" :placeholder="`Trail $ for ${order.symbol}`" class="manual-input-field"/>
                  <button class="button manual-input-btn" @click="submitTrailingStopLoss(order)" :disabled="Number(order.quantity_ordered) === 0 || !trailingStopLoss[order.order_id]">Trl-SL</button>
                </div>
              </div>
            </td>
          </tr>
          <!-- If it's a bracket order, render child rows for each leg -->
          <template v-if="order.action === 'Bracket'">
            <tr v-for="(leg, legIndex) in order.legs" :key="order.order_id + '-' + legIndex">
              <td style="padding-left: 2rem;">{{ leg.Symbol }}</td>
              <td></td>
              <td>{{ leg.BuyOrSell }}</td>
              <td>{{ leg.QuantityOrdered }}</td>
              <td><!-- no entry on child --></td>
              <td>{{ formatPrice(leg.LimitPrice) }}</td>
              <td><!-- Optionally, show parent's status or leave blank --></td>
              <td><!-- No actions on child orders --></td>
            </tr>
          </template>
        </template>
      </tbody>
    </table>
  </div>
</template>

<script>
// Define status sets as provided.
const OPEN_STATUSES = new Set([
  "ACK","ASS","BRC","BRF","BRO","CHG","CND","COR","CSN","DIS",
  "DOA","DON","ECN","EXE","FPR","LAT","OPN","OSO","OTHER","PLA",
  "REC","RJC","RPD","RSN","STP","STT","SUS","UCN"
]);
const CANCELED_STATUSES = new Set(["CAN","EXP","OUT","RJR","SCN","TSC","UCH"]);
const REJECTED_STATUSES = new Set(["REJ"]);
const FILLED_STATUSES = new Set(["FLL","FLP"]);

export default {
  name: "OrdersPanel",
  props: {
    currentSymbol: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      orders: [],
      ordersWs: null,
      isMounted: true,
      timer: null,
      selectedStatuses: {
        All: false,
        Open: true,
        Closed: false,
        Canceled: false,
        Rejected: false,
      },
      manualStopLoss:  {},      // { [order_id]: number }
      trailingStopLoss: {},     // { [order_id]: number }
    };
  },
  computed: {
    filteredOrders() {
      // 1) filter by status
      const filtered = this.selectedStatuses.All
        ? this.orders.slice()
        : this.orders.filter(order => {
            const s = (order.status || "").toUpperCase();
            const allowed = new Set();
            if (this.selectedStatuses.Open)    OPEN_STATUSES.forEach(x => allowed.add(x));
            if (this.selectedStatuses.Closed)  FILLED_STATUSES.forEach(x => allowed.add(x));
            if (this.selectedStatuses.Canceled)CANCELED_STATUSES.forEach(x => allowed.add(x));
            if (this.selectedStatuses.Rejected)REJECTED_STATUSES.forEach(x => allowed.add(x));
            // unrecognized => treat as Open
            if (!allowed.has(s) && s === "") return this.selectedStatuses.Open;
            return allowed.has(s);
          });
      // 2) currentSymbol orders first
      return filtered.sort((a, b) => {
        if (a.symbol === this.currentSymbol && b.symbol !== this.currentSymbol) return -1;
        if (a.symbol !== this.currentSymbol && b.symbol === this.currentSymbol) return 1;
        return 0;
      });
    },
  },
  mounted() {
    this.connectWebSocket();
    
    // Add cleanup on page refresh/close
    this.handleBeforeUnload = () => {
      if (this.ordersWs && this.ordersWs.readyState === WebSocket.OPEN) {
        this.ordersWs.close();
      }
    };
    window.addEventListener('beforeunload', this.handleBeforeUnload);
  },
  beforeUnmount() {
    this.isMounted = false;
    if (this.ordersWs) this.ordersWs.close();
    if (this.timer) clearTimeout(this.timer);
    
    // Remove the beforeunload listener
    if (this.handleBeforeUnload) {
      window.removeEventListener('beforeunload', this.handleBeforeUnload);
    }
  },
  methods: {
    connectWebSocket() {
      const wsUrl = `ws://${window.location.hostname}:8000/ws/orders_stream`;
      this.ordersWs = new WebSocket(wsUrl);
      this.ordersWs.onopen = () => {
        if (this.timer) clearTimeout(this.timer);
      };
      this.ordersWs.onmessage = ({ data }) => {
        try {
          const d = JSON.parse(data);
          if (d.orders) this.orders = d.orders;
        } catch (e) {
          console.error("Orders parse error", e);
        }
      };
      this.ordersWs.onclose = () => {
        if (this.isMounted) this.timer = setTimeout(this.connectWebSocket, 5000);
      };
    },
    formatPrice(val) {
      return val != null ? Number(val).toFixed(2) : "-";
    },
    isOpenOrder(order) {
      const status = (order.status || "").toUpperCase();
      return OPEN_STATUSES.has(status) || status === "";
    },
    async cancelOrder(order) {
      try {
        const res = await fetch(`http://${window.location.hostname}:8000/api/cancel_order`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ order_id: order.order_id })
        });
        const json = await res.json();
        if (!res.ok) throw new Error(json.detail||"cancel failed");
        order.status = "CAN";
      } catch (e) {
        console.error("Cancel error", e);
      }
    },
    closeOrder(order, portion) {
      const totalQty      = order.quantity_ordered;
      const absQty        = Math.abs(totalQty);
      const qtyToFlatten  = Math.floor(absQty * portion);
      if (qtyToFlatten < 1) {
        console.warn(`Nothing to flatten for ${order.symbol}`);
        return;
      }
      console.log(`Closing ${qtyToFlatten} of ${totalQty} (${portion * 100}%) shares of ${order.symbol}`);
      fetch(`http://${window.location.hostname}:8000/api/flattenPosition`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          symbol: order.symbol,
          order_id: order.order_id,
          flatten_type: "panic",
          quantity: qtyToFlatten
        })
      })
      .then(response => {
        if (!response.ok) throw new Error(`API error: ${response.statusText}`);
        return response.json();
      })
      .then(data => {
        console.log('Flatten response:', data);
      })
      .catch(err => {
        console.error('Error flattening position:', err);
      });
    },
    setEmaTrail(order, indicator) {
      console.log(`Setting EMA-based trailing stop for ${order.symbol}`);
      fetch(`http://${window.location.hostname}:8000/api/setStopLoss`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          symbol: order.symbol,
          order_id: order.order_id,
          indicator: indicator,
        })
      })
      .then(response => {
        if (!response.ok) {
          throw new Error(`API error: ${response.statusText}`);
        }
        return response.json();
      })
      .then(data => {
        console.log('StopLoss set:', data);
      })
      .catch(err => {
        console.error('Error setting EMA-based stoploss:', err);
      });
    },
    submitManualStopLoss(order) {
      const price = this.manualStopLoss[order.order_id];
      if (!price || isNaN(price)) {
        console.warn("Invalid stop loss price");
        return;
      }
      console.log(`Setting manual stop loss for ${order.symbol} at ${price}`);
      fetch(`http://${window.location.hostname}:8000/api/setStopLoss`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          symbol: order.symbol,
          order_id: order.order_id,
          stop_price: price,
          indicator: "manual"
        }),
      })
      .then((response) => {
        if (!response.ok) throw new Error(`API error: ${response.statusText}`);
        return response.json();
      })
      .then((data) => {
        console.log("Manual StopLoss set:", data);
      })
      .catch((err) => {
        console.error("Error setting manual stoploss:", err);
      });
    },
    submitTrailingStopLoss(order) {
      const trail = this.trailingStopLoss[order.order_id];
      if (!trail || isNaN(trail)) {
        console.warn("Invalid trailing stop value");
        return;
      }
      console.log(`Setting trailing stop for ${order.symbol} with amount ${trail}`);
      fetch(`http://${window.location.hostname}:8000/api/setStopLoss`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          symbol: order.symbol,
          order_id: order.order_id,
          trail_amount: trail,
          indicator: "trailing"
        }),
      })
      .then((response) => {
        if (!response.ok) throw new Error(`API error: ${response.statusText}`);
        return response.json();
      })
      .then((data) => {
        console.log("Trailing StopLoss set:", data);
      })
      .catch((err) => {
        console.error("Error setting trailing stoploss:", err);
      });
    },
  },
};
</script>

<style scoped>
.orders-panel {
  font-family: Verdana, Geneva, sans-serif;
}
table {
  width: 100%;
  border-collapse: collapse;
}
th, td {
  border: 1px solid #ccc;
  padding: 0.5rem;
  text-align: right;
}
th:first-child, td:first-child {
  text-align: left;
  width: 8%;
}
th:nth-child(2), td:nth-child(2) {
  width: 8%;
}
th:nth-child(3), td:nth-child(3) {
  width: 4%;
}
th:nth-child(4), td:nth-child(4) {
  width: 7%;
}
th:nth-child(5), td:nth-child(5) {
  width: 12%;
}
th:nth-child(6), td:nth-child(6) {
  width: 6%;
}
.current-symbol {
  font-weight: bold;
}
.pnl-green {
  color: green;
  font-weight: bold;
  font-size: larger;
}
.pnl-red {
  color: red;
  font-weight: bold;
  font-size: larger;
}
/* Base button style + interactive cues */
.button {
  font-size: large;
  font-weight: bold;
  margin: 0 0.25rem;
  cursor: pointer;
  transition: filter 0.2s ease, transform 0.1s ease;
}
.button:hover:not(:disabled) {
  filter: brightness(0.9);
}
.button:active:not(:disabled) {
  transform: translateY(1px);
  box-shadow: inset 0 1px 2px rgba(0,0,0,0.2);
}
.button:focus-visible {
  outline: 2px solid #3182ce;
  outline-offset: 2px;
}
.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}
/* stop-controls float right, cancel stays left */
.stop-controls {
  float: right;
  margin-left: 1rem;
}
.closed-position {
  color: gray;
  opacity: 0.6;
}
.positive {
  color: green;
  font-size: larger;   
}
.negative {
  color: red;
  font-size: larger;   
}
.breakeven {
  color: black;
}

.actions-cell {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn-cancel   {   float:inline;
  color: white;  background: #ff9f00; /* amber */ }
.btn-exit     { background: #ff9f00; }
.btn-flatten  { margin-left:6rem;  color: white;  background: #e53e3e; }
.btn-save-90  { color: white;  background: #2f855a; }
.btn-save-75  { color: white;  background: #38a169; }
.btn-save-50  { color: white;  background: #48bb78; }
.btn-stop-ema-6  { background: #90cdf4; }
.btn-stop-ema-9  { background: #63b3ed; }
.btn-stop-ema-21 { background: #3182ce; }
.btn-breakeven   { background: #fff; color: #000; }
.btn-split       { background: #805ad5; }

/* Orders action layout */
.orders-action-layout {
  text-align: left;
}

.button-table {
  border-collapse: separate;
  border-spacing: 0.5rem;
  margin-bottom: 0.5rem;
}

.button-table td {
  padding: 0;
  border: none;
}

.button-table .button {
  width: 100%;
