<template>
  <div class="single-ticker-page" ref="root">
    <!-- Left pane: candle chart (hidden for now, but still in DOM) -->
    <div
      class="left-pane"
      v-show="showLeftPane"
      :style="{ width: leftWidth + 'px', flex: '0 0 auto' }"
    >
      <!-- CandleChart removed - using TradingView instead -->
    </div>
    <!-- Divider to drag (also hidden) -->
    <div
      class="divider"
      v-show="showLeftPane"
      @mousedown.prevent="startDrag"
    ></div>

    <!-- Right pane: everything else -->
    <div class="right-pane">
      <!-- Ticker Info row -->
      <div class="ticker-info-row">
        <TickerInfo :info="tickerInfo" />

        <!-- Show Signal Button (when hidden) -->
        <button v-if="persistentSignal && signalHidden" class="show-signal-btn" @click="showSignal" title="Show signal">
          Show Signal
        </button>

        <!-- Persistent Signal Display -->
        <div v-if="persistentSignal && !signalHidden" class="persistent-signal-top" :class="getSignalBoxClass(persistentSignal.direction)">
          <button class="hide-btn" @click="hideSignal" title="Hide signal"></button>
          <button class="dismiss-btn" @click="dismissSignal" title="Dismiss signal"></button>
          <table class="signal-table">
            <tr>
              <td class="signal-ticker">{{ persistentSignal.ticker }}</td>
              <td class="signal-direction">
                <button
                  class="direction-btn"
                  :style="{ color: getDirectionColor(persistentSignal.direction) }"
                  @click="fillOrderEntry"
                  :title="'Click to fill order entry with ' + persistentSignal.direction + ' values'">
                  {{ persistentSignal.direction }}
                </button>
              </td>
              <td colspan="4"></td>
              <td class="signal-time-text" colspan="2" :style="{ backgroundColor: getTimeBackgroundColor(persistentSignal.receivedAt) }">{{ formatSignalTime(persistentSignal.receivedAt) }} ({{ getMinutesElapsed(persistentSignal.receivedAt) }}m ago)</td>
            </tr>
            <tr>
              <td class="signal-label">Entry:</td>
              <td class="signal-value">${{ formatPrice(persistentSignal.entry_price) }}</td>
              <td class="signal-label">Stop:</td>
              <td class="signal-value">${{ formatPrice(persistentSignal.stop_loss) }}</td>
              <td class="signal-label">Risk:</td>
              <td class="signal-value">${{ formatPrice(persistentSignal.risk_per_share) }}</td>
              <td class="signal-label">Range:</td>
              <td class="signal-value">${{ formatPrice(persistentSignal.opening_range_size) }}</td>
            </tr>
            <tr>
              <td class="signal-label">T1:</td>
              <td class="signal-value">${{ formatPrice(persistentSignal.targets?.target_75pct) }}</td>
              <td class="signal-label">T2:</td>
              <td class="signal-value">${{ formatPrice(persistentSignal.targets?.target_100pct) }}</td>
              <td class="signal-label">Watch:</td>
              <td class="signal-value">{{ persistentSignal.minutes_since_watch }}m</td>
              <td class="signal-label">Vol:</td>
              <td class="signal-value">{{ formatVolume(persistentSignal.raw_details?.volume) }} {{ persistentSignal.range_coverage_percent }}</td>
            </tr>
          </table>
        </div>
      </div>
      <!-- Top row: compressed Quote display -->
      <div class="top-row">
        <QuoteDisplay :quote="latestQuote" />
      </div>
      <!-- Middle row: Level2/News and Tape -->
      <div class="middle-row">
        <div class="left-column">
          <div class="level2-container">
            <Level2
              ref="level2"
              :symbol="symbol"
              :latestQuote="latestQuote"
              @priceSelected="handlePriceSelected"
            />
          </div>
          <div class="news-container">
            <NewsPanel :ticker="symbol" />
          </div>
        </div>
        <div class="tape-container">
          <Tape :trades="trades" />
        </div>
      </div>
      <!-- Bottom row: Order Entry, Positions, Orders -->
      <div class="bottom-row">
        <OrderEntryPanel
          ref="orderEntryPanel"
          :currentSymbol="symbol"
          :selectedPrice="selectedPrice"
          :selectedSide="selectedSide"
          :is-quote-live="isQuoteLive"
          :currentQuote="latestQuote"
          :level2Ref="$refs.level2"
          @view-mode-changed="handleViewModeChanged"
        />
        <IndicatorsPanel :symbol="symbol" :position="currentPosition" />
        <PositionsPanel :currentSymbol="symbol" />
        <OrdersPanel :currentSymbol="symbol" />
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-unused-vars */
import TickerInfo from "./TickerInfo.vue";
import QuoteDisplay from "./QuoteDisplay.vue";
import Tape from "./Tape.vue";
import Level2 from "./Level2.vue";
import NewsPanel from "./NewsPanel.vue";
import PositionsPanel from "./PositionsPanel.vue";
import OrdersPanel from "./OrdersPanel.vue";
import OrderEntryPanel from "./OrderEntryPanel.vue";
import IndicatorsPanel from "./IndicatorsPanel.vue";

export default {
  name: "SingleTicker",
  components: {
    TickerInfo,
    QuoteDisplay,
    Tape,
    Level2,
    NewsPanel,
    PositionsPanel,
    OrdersPanel,
    OrderEntryPanel,
    IndicatorsPanel,
  },
  data() {
    return {
      //  existing state 
      tickerInfo: {},
      latestQuote: {}, // Initialize as an empty object
      trades: [],
      isQuoteLive: false,
      ws: null,
      maxTrades: 40,
      symbol: this.$route.params.symbol,
      timeZone: "America/New_York",
      isMounted: false,
      selectedPrice: null,
      selectedSide: "Buy",
      currentPosition: "LONG", // Track current position for indicators
      timer: null,
      //  drag state 
      leftWidth: 600,
      dragging: false,
      containerLeft: 0,
      //  NEW: hide/show left pane
      showLeftPane: false,
      persistentSignal: null, // Persistent signal display
      signalHidden: false, // Track if signal is hidden
      signalTimeUpdateInterval: null, // Timer to update elapsed time
      currentTimeForSignal: Date.now() // Reactive time for signal elapsed calculation
    };
  },
  mounted() {
    this.isMounted = true;
    // Re-enabled trades_quotes WebSocket to restore watchlist functionality
    this.initWebSocket();
    this.fetchTickerInfo();

    // measure wrapper's left edge
    this.$nextTick(() => {
      const rect = this.$refs.root.getBoundingClientRect();
      this.containerLeft = rect.left;
    });
    // global drag listeners
    window.addEventListener("mousemove", this.onDrag);
    window.addEventListener("mouseup", this.stopDrag);

    // Add cleanup on page refresh/close
    this.handleBeforeUnload = () => {
      if (this.ws && this.ws.readyState === WebSocket.OPEN) {
        console.log('Closing trades_quotes WebSocket on page unload');
        this.ws.close();
      }
    };
    window.addEventListener('beforeunload', this.handleBeforeUnload);

    // Load signal for current symbol if exists
    this.loadSignalForCurrentSymbol();

    // Start timer to update signal elapsed time every minute
    this.signalTimeUpdateInterval = setInterval(() => {
      this.currentTimeForSignal = Date.now();
    }, 60000); // Update every minute

    // Expose loadSignal method globally for direct calls
    window.loadSignal = (signal) => {
      console.log('window.loadSignal called with:', signal);

      // Use the signal's original timestamp (when it was generated)
      // Check multiple possible timestamp fields
      let signalTimestamp = signal.timestamp || signal.generated_at || signal.event_time || signal.signal_time || signal.receivedAt;

      console.log('Original timestamp:', signalTimestamp, 'Type:', typeof signalTimestamp);

      // Convert to proper milliseconds timestamp
      if (signalTimestamp) {
        // Handle array format: [year, month, day, hour, minute]
        if (Array.isArray(signalTimestamp)) {
          const [year, month, day, hour, minute] = signalTimestamp;
          signalTimestamp = new Date(year, month - 1, day, hour, minute).getTime();
        }
        // Handle ISO string format: '2025-10-17T10:00'
        else if (typeof signalTimestamp === 'string' && signalTimestamp.includes('-')) {
          signalTimestamp = new Date(signalTimestamp).getTime();
        }
        // Handle numeric timestamps
        else {
          if (typeof signalTimestamp === 'string') {
            signalTimestamp = parseFloat(signalTimestamp);
          }
          // Nanoseconds (19 digits): divide by 1,000,000
          if (signalTimestamp > 10000000000000000) {
            signalTimestamp = signalTimestamp / 1000000;
          }
          // Microseconds (16 digits): divide by 1,000
          else if (signalTimestamp > 10000000000000) {
            signalTimestamp = signalTimestamp / 1000;
          }
          // Seconds (10 digits): multiply by 1,000
          else if (signalTimestamp < 10000000000) {
            signalTimestamp = signalTimestamp * 1000;
          }
          // Otherwise assume milliseconds (13 digits)
        }
        console.log('Converted timestamp:', signalTimestamp, 'Date:', new Date(signalTimestamp));
      }

      // Date check - filter out signals from previous days
      const signalDate = this.getSignalDate(signalTimestamp || Date.now());
      const today = new Date();
      today.setHours(0, 0, 0, 0);

      if (signalDate < today) {
        console.log('Ignoring old signal from', signalDate);
        return;
      }

      // Load signal into this window's memory
      const signalWithTime = {
        ...signal,
        receivedAt: signalTimestamp || Date.now()
      };
      this.persistentSignal = signalWithTime;

      // Save to localStorage keyed by window + symbol
      const windowNumber = new URLSearchParams(window.location.search).get('windowNumber') || 'default';
      const ticker = signal.ticker ? signal.ticker.toUpperCase() : this.symbol.toUpperCase();
      const storageKey = `signal_w${windowNumber}_${ticker}`;
      localStorage.setItem(storageKey, JSON.stringify(signalWithTime));
      console.log('Signal saved to localStorage:', storageKey);

      // Also load into OrderEntryPanel to populate fields
      if (this.$refs.orderEntryPanel) {
        this.$refs.orderEntryPanel.loadSignal(signal);
      } else {
        console.warn('OrderEntryPanel ref not available yet, retrying...');
        setTimeout(() => {
          if (this.$refs.orderEntryPanel) {
            this.$refs.orderEntryPanel.loadSignal(signal);
          }
        }, 500);
      }
    };

    // Check for pending signal in sessionStorage (from Electron window)
    this.$nextTick(() => {
      setTimeout(() => {
        const pendingSignal = sessionStorage.getItem('pendingSignal');
        if (pendingSignal) {
          try {
            const signal = JSON.parse(pendingSignal);
            console.log('Found pending signal in sessionStorage:', signal);

            // Clear the pending signal
            sessionStorage.removeItem('pendingSignal');

            // Load the signal into the order entry panel
            if (this.$refs.orderEntryPanel) {
              this.$refs.orderEntryPanel.loadSignal(signal);
            }
          } catch (e) {
            console.error('Error parsing pending signal:', e);
            sessionStorage.removeItem('pendingSignal');
          }
        }
      }, 500); // Wait for refs to be available
    });
  },
  beforeUnmount() {
    this.isMounted = false;
    if (this.ws) try { this.ws.close() } catch { /* ignore */ }
    if (this.timer) clearTimeout(this.timer);
    window.removeEventListener("mousemove", this.onDrag);
    window.removeEventListener("mouseup", this.stopDrag);
    
    // Remove the beforeunload listener
    if (this.handleBeforeUnload) {
      window.removeEventListener('beforeunload', this.handleBeforeUnload);
    }

    // Stop signal time update timer
    if (this.signalTimeUpdateInterval) {
      clearInterval(this.signalTimeUpdateInterval);
      this.signalTimeUpdateInterval = null;
    }
  },
  watch: {
    latestQuote(newVal) {
      if (
        newVal &&
        this.tickerInfo &&
        this.tickerInfo.float &&
        !this.tickerInfo.market_cap && // Only calculate if not already set
        newVal.Last
      ) {
        // Ensure reactivity when updating a nested property
        this.tickerInfo = {
          ...this.tickerInfo,
          market_cap: Number(this.tickerInfo.float) * Number(newVal.Last),
        };
      }
    },
    symbol(newSymbol) {
      // Load signal for new symbol
      console.log('Symbol changed to:', newSymbol);
      this.signalHidden = false; // Reset hidden state when symbol changes
      this.loadSignalForCurrentSymbol();
    }
  },
  methods: {
    // ---- pane drag handlers ----
    startDrag() {
      this.dragging = true;
    },
    onDrag(e) {
      if (!this.dragging) return;
      const min = 200;
      const max = window.innerWidth - 200;
      const x = e.clientX - this.containerLeft;
      this.leftWidth = Math.max(min, Math.min(x, max));
    },
    stopDrag() {
      this.dragging = false;
    },
    
    // Handle view mode change from OrderEntryPanel (LONG vs SHORT perspective)
    handleViewModeChanged(viewMode) {
      console.log('View mode changed to:', viewMode);
      this.currentPosition = viewMode;
    },

    // ---- WebSocket (largely unchanged) ----
    initWebSocket() {
      const wsUrl = `ws://${window.location.hostname}:8000/ws/trades_quotes?symbol=${this.symbol}`;
      this.ws = new WebSocket(wsUrl);
      this.ws.onopen = () =>
        console.log("WebSocket connected for trades_quotes:", this.symbol);
      this.ws.onmessage = (evt) => {
        try {
          const msg = JSON.parse(evt.data);
          if (msg.rawEventType === "Q") {
            // Add timestamp to track quote freshness
            this.latestQuote = {
              ...msg,
              receivedTimestamp: Date.now()
            };
            // Set isQuoteLive to true only if bidPrice and askPrice are available
            if (this.latestQuote.bidPrice != null && this.latestQuote.askPrice != null) {
              this.isQuoteLive = true;
            }
            // If they are not available, isQuoteLive remains false, keeping the button disabled
          } else if (msg.rawEventType === "T") {
            this.trades.push(msg);
            if (this.trades.length > this.maxTrades) {
              this.trades = this.trades.slice(-this.maxTrades);
            }
          }
        } catch (e) {
          console.error("Error processing message", evt.data, e);
        }
      };
      this.ws.onerror = (err) => console.error("WebSocket error:", err);
      this.ws.onclose = (evt) => {
        console.log("WebSocket closed:", evt.code, evt.reason);
        if (this.isMounted) {
          this.timer = setTimeout(() => this.initWebSocket(), 5000);
        }
      };
    },
    async fetchTickerInfo() {
      const apiKey = "p78rx6L5WW0JQ37zj4Y8J2BypjbLdd0r";
      const overviewUrl = `https://api.polygon.io/v3/reference/tickers/${this.symbol}?apiKey=${apiKey}`;
      const shortInterestUrl = `https://api.polygon.io/stocks/v1/short-interest?ticker=${this.symbol}&sort=settlement_date.desc&limit=10&apiKey=${apiKey}`;
      // Financials URL is not directly used by TickerInfo, can be added back if needed elsewhere.
      // const financialsUrl = `https://api.polygon.io/v1/reference/financials?ticker=${this.symbol}&order=asc&sort=filing_date&limit=10&apiKey=${apiKey}`;

      // Initialize with base data and nulls for fields that might not be fetched or fail
      let newTickerData = {
        ticker: this.symbol,
        name: null,
        market_cap: null,
        float: null,
        shortInterest: null,
        daysToCover: null,
      };

      try {
        const results = await Promise.allSettled([
          fetch(overviewUrl),
          fetch(shortInterestUrl),
          // fetch(financialsUrl), // If re-added, handle its result below
        ]);

        const [overviewResult, shortInterestResult] = results;

        // Process Overview Data
        if (overviewResult.status === 'fulfilled' && overviewResult.value.ok) {
          const overviewData = await overviewResult.value.json();
          if (overviewData && overviewData.results) {
            newTickerData.name = overviewData.results.name;
            if (overviewData.results.market_cap) {
              newTickerData.market_cap = Number(overviewData.results.market_cap);
            }
            // Polygon's share_class_shares_outstanding is often used for float
            newTickerData.float = overviewData.results.weighted_shares_outstanding;
          } else {
            console.warn(`No results in overview data for ${this.symbol}`);
          }
        } else if (overviewResult.status === 'fulfilled' && !overviewResult.value.ok) {
          console.error(`Ticker overview fetch failed for ${this.symbol}: ${overviewResult.value.status} ${overviewResult.value.statusText}`);
        } else if (overviewResult.status === 'rejected') {
          console.error(`Ticker overview fetch rejected for ${this.symbol}:`, overviewResult.reason);
        }

        // Process Short Interest Data
        if (shortInterestResult.status === 'fulfilled' && shortInterestResult.value.ok) {
          const shortInterestData = await shortInterestResult.value.json();
          const siArray = shortInterestData.results || [];
          if (siArray.length > 0) {
            // Since the API returns data sorted by settlement_date ascending,
            // the last item is the most recent
            const latestSi = siArray[0];
            
            console.log('Latest short interest record:', latestSi); // Debug log
            
            if (latestSi) {
              newTickerData.shortInterest = latestSi.short_interest;
              newTickerData.daysToCover = latestSi.days_to_cover;
            } else {
              console.warn(`No latest short interest record found for ${this.symbol} from results.`);
            }
          } else {
            console.warn(`No short interest results array for ${this.symbol}`);
          }
        } else if (shortInterestResult.status === 'fulfilled' && !shortInterestResult.value.ok) {
          // This handles 404s for short interest gracefully
          console.warn(`Short interest fetch failed for ${this.symbol}: ${shortInterestResult.value.status} ${shortInterestResult.value.statusText}. Data will be unavailable.`);
          // newTickerData.shortInterest and daysToCover remain null
        } else if (shortInterestResult.status === 'rejected') {
          console.error(`Short interest fetch rejected for ${this.symbol}:`, shortInterestResult.reason);
          // newTickerData.shortInterest and daysToCover remain null
        }

        // Assign the processed data (even if partial)
        if (this.isMounted) {
          this.tickerInfo = newTickerData;
        }
      } catch (e) {
        // This catch is for unexpected errors not caught by Promise.allSettled individual promise states
        // (e.g., error within .json() parsing if not handled above, though .json() itself returns a promise)
        console.error("Generic error in fetchTickerInfo processing:", e);
        // Fallback: ensure tickerInfo is at least the initialized newTickerData with nulls
        if (this.isMounted) {
            this.tickerInfo = newTickerData;
        }
      }
    },
    handlePriceSelected({ price, side }) {
      this.selectedPrice = price;
      this.selectedSide = side;
    },

    loadSignalForCurrentSymbol() {
      const windowNumber = new URLSearchParams(window.location.search).get('windowNumber') || 'default';
      const ticker = this.symbol.toUpperCase();
      const storageKey = `signal_w${windowNumber}_${ticker}`;

      const storedSignal = localStorage.getItem(storageKey);
      if (storedSignal) {
        try {
          const signal = JSON.parse(storedSignal);

          // Date check - remove signals from previous days
          const signalDate = this.getSignalDate(signal.receivedAt);
          const today = new Date();
          today.setHours(0, 0, 0, 0);

          if (signalDate < today) {
            // Signal is from a previous day, remove it
            console.log('Removing old signal for', ticker, 'from', signalDate);
            localStorage.removeItem(storageKey);
            this.persistentSignal = null;
          } else {
            this.persistentSignal = signal;
            console.log('Loaded signal for', ticker, ':', this.persistentSignal);
          }
        } catch (e) {
          console.error('Error parsing stored signal:', e);
          localStorage.removeItem(storageKey);
        }
      } else {
        this.persistentSignal = null;
        console.log('No signal found for', ticker);
      }
    },

    getSignalDate(timestamp) {
      if (!timestamp) return new Date(0);

      let ts = timestamp;

      // Handle array format: [year, month, day, hour, minute]
      if (Array.isArray(ts)) {
        const [year, month, day] = ts;
        const date = new Date(year, month - 1, day);
        date.setHours(0, 0, 0, 0);
        return date;
      }
      // Handle ISO string format: '2025-10-17T10:00'
      else if (typeof ts === 'string' && ts.includes('-')) {
        const date = new Date(ts);
        date.setHours(0, 0, 0, 0);
        return date;
      }
      // Handle numeric timestamps
      else {
        if (typeof ts === 'string') {
          ts = parseFloat(ts);
        }
        // Nanoseconds (19 digits): divide by 1,000,000
        if (ts > 10000000000000000) {
          ts = ts / 1000000;
        }
        // Microseconds (16 digits): divide by 1,000
        else if (ts > 10000000000000) {
          ts = ts / 1000;
        }
        // Seconds (10 digits): multiply by 1,000
        else if (ts < 10000000000) {
          ts = ts * 1000;
        }
        // Otherwise assume milliseconds (13 digits)

        const date = new Date(ts);
        date.setHours(0, 0, 0, 0);
        return date;
      }
    },

    dismissSignal() {
      // Remove from localStorage for current symbol
      const windowNumber = new URLSearchParams(window.location.search).get('windowNumber') || 'default';
      const ticker = this.symbol.toUpperCase();
      const storageKey = `signal_w${windowNumber}_${ticker}`;
      localStorage.removeItem(storageKey);

      this.persistentSignal = null;
      this.signalHidden = false; // Reset hidden state when dismissed
      console.log('Dismissed signal for', ticker);
    },

    hideSignal() {
      this.signalHidden = true;
      console.log('Signal hidden');
    },

    showSignal() {
      this.signalHidden = false;
      console.log('Signal shown');
    },

    fillOrderEntry() {
      if (this.persistentSignal && this.$refs.orderEntryPanel) {
        this.$refs.orderEntryPanel.loadSignal(this.persistentSignal);
        console.log('Filled order entry with signal:', this.persistentSignal);
      } else {
        console.warn('Cannot fill order entry - signal or panel not available');
      }
    },

    getDirectionColor(direction) {
      const dir = (direction || '').toUpperCase();
      return (dir === 'LONG' || dir === 'BUY') ? '#28a745' : '#dc3545';
    },

    getSignalBoxClass(direction) {
      const dir = (direction || '').toUpperCase();
      return (dir === 'LONG' || dir === 'BUY') ? 'signal-long' : 'signal-short';
    },

    formatPrice(price) {
      if (price === null || price === undefined) return '-';
      return parseFloat(price).toFixed(2);
    },

    formatVolume(volume) {
      if (!volume) return '-';
      return `${(volume / 1000).toFixed(1)}K`;
    },

    formatSignalTime(timestamp) {
      if (!timestamp) return '-';
      let ts = timestamp;

      console.log('[formatSignalTime] Input timestamp:', timestamp, 'Type:', typeof timestamp);

      // Handle array format: [year, month, day, hour, minute]
      if (Array.isArray(ts)) {
        const [year, month, day, hour, minute] = ts;
        ts = new Date(year, month - 1, day, hour, minute).getTime();
        console.log('[formatSignalTime] Array format converted to:', ts, new Date(ts).toLocaleString());
      }
      // Handle ISO string format: '2025-10-17T10:00'
      else if (typeof ts === 'string' && ts.includes('-')) {
        ts = new Date(ts).getTime();
        console.log('[formatSignalTime] ISO string converted to:', ts, new Date(ts).toLocaleString());
      }
      // Handle numeric timestamps
      else {
        if (typeof ts === 'string') {
          ts = parseFloat(ts);
        }
        const originalTs = ts;
        // Nanoseconds (19 digits): divide by 1,000,000
        if (ts > 10000000000000000) {
          ts = ts / 1000000;
          console.log('[formatSignalTime] Nanoseconds converted:', originalTs, '->', ts);
        }
        // Microseconds (16 digits): divide by 1,000
        else if (ts > 10000000000000) {
          ts = ts / 1000;
          console.log('[formatSignalTime] Microseconds converted:', originalTs, '->', ts);
        }
        // Seconds (10 digits): multiply by 1,000
        else if (ts < 10000000000) {
          ts = ts * 1000;
          console.log('[formatSignalTime] Seconds converted:', originalTs, '->', ts);
        }
        // Otherwise assume milliseconds (13 digits)
        console.log('[formatSignalTime] Numeric timestamp:', ts, new Date(ts).toLocaleString());
      }

      const date = new Date(ts);
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const seconds = String(date.getSeconds()).padStart(2, '0');
      console.log('[formatSignalTime] Formatted result:', `${hours}:${minutes}:${seconds}`, 'from date:', date.toLocaleString());
      return `${hours}:${minutes}:${seconds}`;
    },

    getMinutesElapsed(timestamp) {
      if (!timestamp) return 0;
      let ts = timestamp;

      console.log('[getMinutesElapsed] Input timestamp:', timestamp, 'Type:', typeof timestamp);

      // Handle array format: [year, month, day, hour, minute]
      if (Array.isArray(ts)) {
        const [year, month, day, hour, minute] = ts;
        ts = new Date(year, month - 1, day, hour, minute).getTime();
        console.log('[getMinutesElapsed] Array format converted to:', ts, new Date(ts).toLocaleString());
      }
      // Handle ISO string format: '2025-10-17T10:00'
      else if (typeof ts === 'string' && ts.includes('-')) {
        ts = new Date(ts).getTime();
        console.log('[getMinutesElapsed] ISO string converted to:', ts, new Date(ts).toLocaleString());
      }
      // Handle numeric timestamps
      else {
        if (typeof ts === 'string') {
          ts = parseFloat(ts);
        }
        const originalTs = ts;
        // Nanoseconds (19 digits): divide by 1,000,000
        if (ts > 10000000000000000) {
          ts = ts / 1000000;
          console.log('[getMinutesElapsed] Nanoseconds converted:', originalTs, '->', ts);
        }
        // Microseconds (16 digits): divide by 1,000
        else if (ts > 10000000000000) {
          ts = ts / 1000;
          console.log('[getMinutesElapsed] Microseconds converted:', originalTs, '->', ts);
        }
        // Seconds (10 digits): multiply by 1,000
        else if (ts < 10000000000) {
          ts = ts * 1000;
          console.log('[getMinutesElapsed] Seconds converted:', originalTs, '->', ts);
        }
        // Otherwise assume milliseconds (13 digits)
        console.log('[getMinutesElapsed] Numeric timestamp:', ts, new Date(ts).toLocaleString());
      }

      console.log('[getMinutesElapsed] Current time:', this.currentTimeForSignal, new Date(this.currentTimeForSignal).toLocaleString());
      console.log('[getMinutesElapsed] Signal time:', ts, new Date(ts).toLocaleString());
      const elapsed = this.currentTimeForSignal - ts;
      const minutes = Math.floor(elapsed / 60000);
      console.log('[getMinutesElapsed] Elapsed ms:', elapsed, '-> Minutes:', minutes);
      return minutes;
    },

    getTimeBackgroundColor(timestamp) {
      const minutesElapsed = this.getMinutesElapsed(timestamp);
      if (minutesElapsed <= 5) {
        return '#90EE90'; // Light green
      } else if (minutesElapsed <= 10) {
        return '#FFFF99'; // Light yellow
      } else {
        return '#FFB6C6'; // Light red
      }
    }
  },
};
</script>

