<template>
  <div class="positions-panel">
    <h3>Positions</h3>
    <table>
      <thead>
        <tr>
          <th>Symbol</th>
          <th>Qty</th>
          <th>Avg Cost Basis</th>
          <th>Market Value</th>
          <th>P/L</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr 
          v-for="(pos, index) in sortedPositions" 
          :key="index" 
          :class="{'closed-position': Number(pos.Quantity) === 0}"
        >
          <td>
            <span :class="{ 'current-symbol': pos.Symbol === currentSymbol }">
              {{ pos.Symbol }}
            </span>
          </td>
          <td>{{ pos.Quantity }}</td>
          <td>{{ formatPrice(pos.AveragePrice) }}</td>
          <td>{{ formatPrice(pos.MarketValue) }}</td>
          <td :class="{ 'current-symbol': pos.Symbol === currentSymbol }">
            <span :class="Number(pos.UnrealizedProfitLoss) >= 0 ? 'pnl-green' : 'pnl-red'">
            {{ formatPrice(pos.UnrealizedProfitLoss) }}
            </span>
          </td>
          <td>
            <div v-if="pos.Symbol === currentSymbol" class="action-grid">
              <!-- Column 1: Exit buttons -->
              <div class="action-column">
                <button class="button btn-exit" @click="closePosition(pos.Symbol, 0.1)" :disabled="Number(pos.Quantity) === 0">
                  Exit 10%
                </button>
                <button class="button btn-exit" @click="closePosition(pos.Symbol, 0.15)" :disabled="Number(pos.Quantity) === 0">
                  Exit 15%
                </button>
                <button class="button btn-exit" @click="closePosition(pos.Symbol, 0.2)" :disabled="Number(pos.Quantity) === 0">
                  Exit 20%
                </button>
                <button class="button btn-exit" @click="closePosition(pos.Symbol, 0.25)" :disabled="Number(pos.Quantity) === 0">
                  Exit 25%
                </button>
                <button class="button btn-exit" @click="closePosition(pos.Symbol, 0.33)" :disabled="Number(pos.Quantity) === 0">
                  Exit 33%
                </button>
                <button class="button btn-exit" @click="closePosition(pos.Symbol, 0.5)" :disabled="Number(pos.Quantity) === 0">
                  Exit 50%
                </button>
                <button class="button btn-flatten" @click="closePosition(pos.Symbol, 1)" :disabled="Number(pos.Quantity) === 0">
                  Flatten
                </button>
              </div>

              <!-- Column 2: Breakeven + Save buttons + input fields -->
              <div class="action-column">
                <button class="button btn-breakeven" @click="setEmaTrail(pos.Symbol, 'breakeven')" :disabled="Number(pos.Quantity) === 0">
                  Breakeven
                </button>
                <button
                  class="button btn-save-25"
                  @click="saveProfit(pos.Symbol, 0.25)"
                  :disabled="Number(pos.Quantity) === 0 || Number(pos.UnrealizedProfitLoss) <= 0"
                >
                  Save 25%
                </button>
                <button
                  class="button btn-save-50"
                  @click="saveProfit(pos.Symbol, 0.5)"
                  :disabled="Number(pos.Quantity) === 0 || Number(pos.UnrealizedProfitLoss) <= 0"
                >
                  Save 50%
                </button>
                <button
                  class="button btn-save-75"
                  @click="saveProfit(pos.Symbol, 0.75)"
                  :disabled="Number(pos.Quantity) === 0 || Number(pos.UnrealizedProfitLoss) <= 0"
                >
                  Save 75%
                </button>
                <button
                  class="button btn-save-90"
                  @click="saveProfit(pos.Symbol, 0.9)"
                  :disabled="Number(pos.Quantity) === 0 || Number(pos.UnrealizedProfitLoss) <= 0"
                >
                  Save 90%
                </button>
                <input
                  type="number"
                  v-model.number="manualStopLoss[pos.Symbol]"
                  :placeholder="`SL for ${pos.Symbol}`"
                  class="manual-input-field"
                />
                <input
                  type="number"
                  v-model.number="trailingStopLoss[pos.Symbol]"
                  :placeholder="`Trail $ for ${pos.Symbol}`"
                  class="manual-input-field"
                />
              </div>

              <!-- Column 3: EMA stops + Split + Manual buttons -->
              <div class="action-column">
                <button class="button btn-stop-ema-6" @click="setEmaTrail(pos.Symbol, 'ema6')" :disabled="Number(pos.Quantity) === 0">
                  SL: 6ema
                </button>
                <button class="button btn-stop-ema-9" @click="setEmaTrail(pos.Symbol, 'ema9')" :disabled="Number(pos.Quantity) === 0">
                  SL: 9ema
                </button>
                <button class="button btn-stop-ema-21" @click="setEmaTrail(pos.Symbol, 'ema21')" :disabled="Number(pos.Quantity) === 0">
                  SL: 21ema
                </button>
                <button
                  class="button btn-split"
                  @click="splitStopLoss(pos.Symbol, 2)"
                  :disabled="Number(pos.Quantity) === 0"
                >
                  Split SL 2
                </button>
                <button
                  class="button btn-split"
                  @click="splitStopLoss(pos.Symbol, 3)"
                  :disabled="Number(pos.Quantity) === 0"
                >
                  Split SL 3
                </button>
                <button
                  class="button manual-input-btn"
                  @click="submitManualStopLoss(pos.Symbol)"
                  :disabled="Number(pos.Quantity) === 0 || !manualStopLoss[pos.Symbol]"
                >
                  Set-SL
                </button>
                <button
                  class="button manual-input-btn"
                  @click="submitTrailingStopLoss(pos.Symbol)"
                  :disabled="Number(pos.Quantity) === 0 || !trailingStopLoss[pos.Symbol]"
                >
                  Trl-SL
                </button>
              </div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "PositionsPanel",
  props: {
    currentSymbol: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      positions: [],
      isMounted: true,
      timer: null,
      positionsWs: null,
      manualStopLoss: {},
      trailingStopLoss: {}
    };
  },
  computed: {
    sortedPositions() {
      return this.positions
        // .filter(p => p.Symbol !== 'AMZN' && p.Symbol !== 'GLD') // Hide AMZN and GLD from display
        .slice()
        .sort((a, b) => {
          if (a.Symbol === this.currentSymbol && b.Symbol !== this.currentSymbol) {
            return -1;
          } else if (a.Symbol !== this.currentSymbol && b.Symbol === this.currentSymbol) {
            return 1;
          }
          const aClosed = Number(a.Quantity) === 0;
          const bClosed = Number(b.Quantity) === 0;
          if (aClosed && !bClosed) return 1;
          if (!aClosed && bClosed) return -1;
          return 0;
        });
    },
    filteredPositions() {
      // if a symbol is selected, show only that one; otherwise show all
      if (this.currentSymbol) {
        return this.sortedPositions.filter(p => p.Symbol === this.currentSymbol);
      }
      return this.sortedPositions;
    }    
  },
  mounted() {
    this.isMounted = true;
    this.connectWebSocket();
    
    // Add cleanup on page refresh/close
    this.handleBeforeUnload = () => {
      if (this.positionsWs && this.positionsWs.readyState === WebSocket.OPEN) {
        this.positionsWs.close();
      }
    };
    window.addEventListener('beforeunload', this.handleBeforeUnload);
  },
  beforeUnmount() {
    this.isMounted = false;
    if (this.positionsWs) {
      this.positionsWs.close();
    }
    if (this.timer) {
      clearTimeout(this.timer);
    }
    
    // Remove the beforeunload listener
    if (this.handleBeforeUnload) {
      window.removeEventListener('beforeunload', this.handleBeforeUnload);
    }
  },
  methods: {
    connectWebSocket() {
      const wsUrl = `ws://${window.location.hostname}:8000/ws/positions`;
      this.positionsWs = new WebSocket(wsUrl);
      this.positionsWs.onopen = () => {
        console.log("Positions websocket connected");
        if (this.timer) {
          clearTimeout(this.timer);
          this.timer = null;
        }
      };
      this.positionsWs.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          if (data.positions) {
            this.positions = data.positions;
          }
        } catch (e) {
          console.error("Error processing positions message", event.data, e);
        }
      };
      this.positionsWs.onerror = (error) => {
        console.error("Positions websocket error:", error);
      };
      this.positionsWs.onclose = () => {
        console.log("Positions websocket closed");
        if (this.isMounted) {
          this.timer = setTimeout(() => {
            console.log("Reconnecting positions websocket...");
            this.connectWebSocket();
          }, 5000);
        }
      };
    },
    formatPrice(val) {
      return val != null ? Number(val).toFixed(2) : "-";
    },
    closePosition(symbol, portion) {
      const pos = this.positions.find(p => p.Symbol === symbol);
      if (!pos) {
        console.warn(`No position found for ${symbol}`);
        return;
      }
      const totalQty      = Number(pos.Quantity);
      const absQty        = Math.abs(totalQty);
      const qtyToFlatten  = Math.floor(absQty * portion);
      if (qtyToFlatten < 1) {
        console.warn(`Nothing to flatten for ${symbol}`);
        return;
      }
      console.log(`Closing ${qtyToFlatten} of ${totalQty} (${portion * 100}%) shares of ${symbol}`);
      fetch(`http://${window.location.hostname}:8000/api/flattenPosition`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          symbol: symbol,
          flatten_type: "panic",
          quantity: qtyToFlatten
        })
      })
      .then(response => {
        if (!response.ok) throw new Error(`API error: ${response.statusText}`);
        return response.json();
      })
      .then(data => {
        console.log('Flatten response:', data);
      })
      .catch(err => {
        console.error('Error flattening position:', err);
      });
    },
    splitStopLoss(symbol, tranches) {
      console.log(`Splitting SL for ${symbol} into ${tranches} parts`);
      fetch(`http://${window.location.hostname}:8000/api/splitStopLoss`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ symbol, tranches })
      })
      .then(res => {
        if (!res.ok) throw new Error(`API error: ${res.statusText}`);
        return res.json();
      })
      .then(data => {
        console.log('Split SL response:', data);
      })
      .catch(err => {
        console.error('Error splitting SL:', err);
      });
    },
    setEmaTrail(symbol, indicator) {
      console.log(`Setting EMA-based trailing stop for ${symbol}`);
      fetch(`http://${window.location.hostname}:8000/api/setStopLoss`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          symbol: symbol,
          indicator: indicator,
        })
      })
      .then(response => {
        if (!response.ok) {
          throw new Error(`API error: ${response.statusText}`);
        }
        return response.json();
      })
      .then(data => {
        console.log('StopLoss set:', data);
      })
      .catch(err => {
        console.error('Error setting EMA-based stoploss:', err);
      });
    },
    submitManualStopLoss(symbol) {
      const price = this.manualStopLoss[symbol];
      if (!price || isNaN(price)) {
        console.warn("Invalid stop loss price");
        return;
      }
      console.log(`Setting manual stop loss for ${symbol} at ${price}`);
      fetch(`http://${window.location.hostname}:8000/api/setStopLoss`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          symbol,
          stop_price: price,
          indicator: "manual"
        }),
      })
      .then((response) => {
        if (!response.ok) throw new Error(`API error: ${response.statusText}`);
        return response.json();
      })
      .then((data) => {
        console.log("Manual StopLoss set:", data);
      })
      .catch((err) => {
        console.error("Error setting manual stoploss:", err);
      });
    },
    submitTrailingStopLoss(symbol) {
      const trail = this.trailingStopLoss[symbol];
      if (!trail || isNaN(trail)) {
        console.warn("Invalid trailing stop value");
        return;
      }
      console.log(`Setting trailing stop for ${symbol} with amount ${trail}`);
      fetch(`http://${window.location.hostname}:8000/api/setStopLoss`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          symbol,
          trail_amount: trail,
          indicator: "trailing"
        }),
      })
      .then((response) => {
        if (!response.ok) throw new Error(`API error: ${response.statusText}`);
        return response.json();
      })
      .then((data) => {
        console.log("Trailing StopLoss set:", data);
      })
      .catch((err) => {
        console.error("Error setting trailing stoploss:", err);
      });
    },
    async saveProfit(symbol, pct) {
      const pos = this.positions.find(p => p.Symbol === symbol);
      if (!pos) return console.warn(`No position for ${symbol}`);

      const qty = Math.abs(Number(pos.Quantity));
      if (qty === 0) return;

      const entry = Number(pos.AveragePrice);
      const current = Number(pos.MarketValue) / qty;
      const targetStop = entry + (current - entry) * pct;

      console.log(`Setting save-${pct*100}% stop for ${symbol} at $${targetStop.toFixed(2)}`);

      // reuse your setStopLoss endpoint
      try {
        const res = await fetch(`http://${window.location.hostname}:8000/api/setStopLoss`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            symbol,
            stop_price: Number(targetStop.toFixed(2)),
            indicator: 'manual'   // or 'saveProfit' if you want to distinguish
          })
        });
        if (!res.ok) throw new Error(res.statusText);
        const data = await res.json();
        console.log('StopLoss set:', data);
      } catch (err) {
        console.error('Error setting save-profit stop:', err);
      }
    },
  },
};
</script>

<style scoped>
.positions-panel {
  font-family: Verdana, Geneva, sans-serif;
}
table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}
th, td {
  border: 1px solid #ccc;
  padding: 0.5rem;
  text-align: right;
  overflow: hidden;
}
th:first-child, td:first-child {
  text-align: left;
  width: 12%;
}
th:nth-child(2), td:nth-child(2) {
  width: 6%;
}
th:nth-child(3), td:nth-child(3) {
  width: 10%;
}
th:nth-child(4), td:nth-child(4) {
  width: 10%;
}
th:nth-child(5), td:nth-child(5) {
  width: 8%;
}
th:last-child, td:last-child {
  width: 50%;
}
.current-symbol {
  font-weight: bold;
}
.pnl-green {
  color: green;
  font-size: larger;   
}
.pnl-red {
  color: red;
  font-size: larger;   
}
/* Base button style + interactive cues */
.button {
  font-size: large;
  font-weight: bold;
  margin: 0 0.25rem;

  /* indicate clickability */
  cursor: pointer;
  transition: filter 0.2s ease, transform 0.1s ease;
}
.button:hover:not(:disabled) {
  /* subtle hover dim */
  filter: brightness(0.9);
}
.button:active:not(:disabled) {
  /* pressed-in effect */
  transform: translateY(1px);
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.2);
}
.button:focus-visible {
  /* keyboard focus outline */
  outline: 2px solid #3182ce;
  outline-offset: 2px;
}
/* disabled state */
.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}

.closed-position {
  color: gray;
  opacity: 0.6;
}
.btn-exit { background: #ff9f00; /* amber */ }
.btn-flatten { color: white; background: #e53e3e; /* red */ }
.btn-save-25 { color: white; background: #68d391; /* lightest green */ }
.btn-save-50 { color: white; background: #48bb78; /* light green */ }
.btn-save-75 { color: white; background: #38a169; /* medium green */ }
.btn-save-90 { color: white; background: #2f855a; /* darkest green */ }
.btn-stop-ema-6  { background: #90cdf4; /* light blue */ }
.btn-stop-ema-9  { background: #63b3ed; /* mid blue */ }
.btn-stop-ema-21 { background: #3182ce; /* dark blue */ }
.btn-breakeven   { background: #fff; color: #000; }
.btn-split       { background: #805ad5; /* purple */ }

/* Grid layout for action buttons */
.action-grid {
  display: flex;
  gap: 0.5rem;
  align-items: start;
  justify-content: flex-start;
  width: 100%;
  overflow-x: auto;
}

.action-column {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  min-width: fit-content;
  flex-shrink: 0;
}

.action-column .button {
  margin: 0;
  font-size: large;
  font-weight: bold;
  padding: 0.25rem 0.5rem;
  white-space: nowrap;
  width: 100%;
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  box-sizing: border-box;
}

.manual-input-field {
  width: 120px;
  min-width: 0;
  font-size: small;
  padding: 0.25rem 0.5rem;
  margin: 0;
  box-sizing: border-box;
}

.manual-input-btn {
  font-size: small !important;
  padding: 0.25rem 0.5rem !important;
  white-space: nowrap;
}
</style>
