            <td class="col-price">{{ getPrice(ticker) }}</td>
            <td class="col-percent" :class="getPriceClass(ticker)">{{ getPercent(ticker) }}</td>
            <td class="col-macd-bar">
              <div class="macd-horizontal-bar">
                <div
                  v-for="(period, index) in indicatorPeriods"
                  :key="'macd-segment-' + period"
                  class="macd-segment"
                  :class="getMacdDotClass(ticker, period)"
                  :style="{ width: '20%', left: (index * 20) + '%' }"
