            <td class="col-compact" :class="getBBClass(ticker, '3min')">{{ getBB(ticker, '3min') }}</td>
            <td class="col-compact" :class="getBBClass(ticker, '5min')">{{ getBB(ticker, '5min') }}</td>
            <td class="col-compact" :class="getBBClass(ticker, '15min')">{{ getBB(ticker, '15min') }}</td>
