<script>
import axios from 'axios';
import CryptoJS from 'crypto-js';

export default {
  name: "WatchList",
  data() {
    return {
      watchlist: [],
      signals: {}, // { ticker: [signals] }
      indicators: {}, // { ticker: { price, percent, etc } }
      indicatorsWs: null,
      apiBaseUrl: `http://${window.location.hostname}:8000/api`,
      newSymbol: '',
      indicatorPeriods: ['1min', '2min', '3min', '5min', '15min'],
      alertSoundEnabled: true,
      selectedChime: 'double-beep',
      audioContext: null,
      lastSignalCount: {},
      newlyDetectedSignals: {}, // Track timestamps of newly detected signals: { ticker: timestamp }
      windowTickerMap: {}, // Track which ticker is in each window: { windowNumber: ticker }
      signalDate: new Date().toISOString().split('T')[0], // Today's date in YYYY-MM-DD format
      viewMode: 'active-signals', // 'both', 'watchlist', 'smb', or 'active-signals'
      sortByTime: true, // Sort by alert timing (descending - newest first)
      currentTime: Date.now(), // For recalculating ages in real-time
      // Encryption settings (for remote mode only)
      encryptionSalt: 'trading_watchlist_salt_2025', // Public salt
      decryptionPassword: null, // Stored in localStorage, retrieved on mount
      isPasswordPromptShown: false
    };
  },
  computed: {
    sortedWatchlist() {
      // Return sorted or unsorted watchlist based on sortByTime checkbox
      if (!this.sortByTime) {
        return this.watchlist;
      }

      // Sort by latest alert time (descending - newest first)
      const sorted = [...this.watchlist].sort((a, b) => {
        const aSignals = this.signals[a] || [];
        const bSignals = this.signals[b] || [];

        // Get the latest signal timestamp for each ticker
        const getLatestTimestamp = (signals) => {
          if (signals.length === 0) return -Infinity; // No signals go to the end
          // Find the signal with the highest signalTime (newest)
          const timestamps = signals.map(s => s.signalTime).filter(t => t != null && !isNaN(t));
          if (timestamps.length === 0) return -Infinity;
          return Math.max(...timestamps);
        };

        const aLatest = getLatestTimestamp(aSignals);
        const bLatest = getLatestTimestamp(bSignals);

        return bLatest - aLatest; // Descending: newest first (higher timestamp first)
      });

      // Log the sorted order with timestamps
      const sortedWithTimes = sorted.map(ticker => {
        const signals = this.signals[ticker] || [];
        const timestamps = signals.map(s => s.signalTime).filter(t => t != null && !isNaN(t));
        const latest = timestamps.length > 0 ? Math.max(...timestamps) : -Infinity;
        return `${ticker}: ${latest === -Infinity ? 'No Signal' : new Date(latest).toLocaleTimeString()}`;
      });
      console.log('Final sorted order (newest first):', sortedWithTimes);
      return sorted;
    },
    watchlistWithActiveSignals() {
      // Get only watchlist tickers that have active signals (not grayed out)
      return this.sortedWatchlist.filter(ticker => {
        const signals = this.signals[ticker] || [];
        // Check if any signal is still active (is_active: true means < 30 minutes old)
        return signals.some(signal => signal.is_active);
      });
    },
    smbOnlyTickers() {
      // Get all tickers that have signals but are not in the watchlist
      // Include both SMB signals AND VWAP Consolidation signals
      const allTickersWithSignals = Object.keys(this.signals);
      const smbTickers = allTickersWithSignals.filter(ticker => {
        // Check if ticker is not in watchlist
        if (this.watchlist.includes(ticker)) {
          return false;
        }
        // Check if ticker has at least one SMB signal OR VWAP Consolidation signal
        const signals = this.signals[ticker] || [];
        const hasRelevantSignal = signals.some(signal =>
          signal.pattern === 'Back$ide' ||
          signal.pattern === 'Second Chance' ||
          signal.pattern === 'Rubberband' ||
          signal.pattern === 'Fashionably Late' ||
          signal.pattern === 'VWAP Cons'
        );

        if (!hasRelevantSignal) {
          return false;
        }

        // If in 'active-signals' mode, only show tickers with active signals
        if (this.viewMode === 'active-signals') {
          return signals.some(signal => signal.is_active);
        }

        return true;
      });

      // Sort by most recent signal time (reverse chronological)
      return smbTickers.sort((a, b) => {
        const aSignals = this.signals[a] || [];
        const bSignals = this.signals[b] || [];

        // Get the most recent signal timestamp for each ticker
        const getLatestTimestamp = (signals) => {
          if (signals.length === 0) return 0;
          // Find the signal with the highest signalTime (most recent)
          return Math.max(...signals.map(s => s.signalTime || 0));
        };

        const aLatest = getLatestTimestamp(aSignals);
        const bLatest = getLatestTimestamp(bSignals);

        return bLatest - aLatest; // Descending: newest first (higher timestamp first)
      });
    }
  },
  mounted() {
    console.log('WatchList mounted, sortByTime:', this.sortByTime);

    // Check if running on localhost or local network
    const hostname = window.location.hostname;
    const isLocalhost = hostname === 'localhost' ||
                       hostname === '127.0.0.1' ||
                       hostname.startsWith('192.') ||
                       hostname.startsWith('10.');

    if (isLocalhost) {
      // Local mode: use WebSocket and API
      console.log('Running in LOCAL mode (hostname:', hostname + ')');
      this.initIndicatorsWebSocket();
      this.fetchWatchlist();
      this.fetchSignals();

      // Poll for new signals every 5 seconds
      setInterval(() => {
        this.fetchSignals();
      }, 5000);
    } else {
      // Remote mode: poll data.json
      console.log('Running in REMOTE mode - polling data.json (hostname:', hostname + ')');
      this.pollDataJson();

      // Poll data.json every 10 seconds
      setInterval(() => {
        this.pollDataJson();
      }, 10000);
    }

    // Update currentTime every second to recalculate ages
    setInterval(() => {
      this.currentTime = Date.now();
    }, 1000);

    // Load audio preferences
    const savedAlertSound = localStorage.getItem('alertSoundEnabled');
    if (savedAlertSound !== null) {
      this.alertSoundEnabled = savedAlertSound === 'true';
    }

    const savedChime = localStorage.getItem('selectedChime');
    if (savedChime) {
      this.selectedChime = savedChime;
    }

    // Initialize audio context on user interaction
    document.addEventListener('click', () => {
      if (!this.audioContext) {
        this.audioContext = new (window.AudioContext || window.webkitAudioContext)();
      }
    }, { once: true });
  },
  beforeUnmount() {
    if (this.indicatorsWs) {
      this.indicatorsWs.close();
    }
  },
  watch: {
    sortByTime(newVal) {
      console.log('sortByTime changed to:', newVal);
    },
    watchlist(newVal) {
      console.log('watchlist changed, length:', newVal.length);
    },
    signals(newVal) {
      console.log('signals changed, tickers:', Object.keys(newVal));
    }
  },
  methods: {
    decryptData(encryptedBase64, password) {
      try {
        // Decode base64
        const encryptedBytes = CryptoJS.enc.Base64.parse(encryptedBase64);

        // Extract IV (first 16 bytes) and ciphertext (rest)
        const iv = CryptoJS.lib.WordArray.create(encryptedBytes.words.slice(0, 4));
        const ciphertext = CryptoJS.lib.WordArray.create(encryptedBytes.words.slice(4));

        // Derive key using PBKDF2 (matching Python's implementation)
        const key = CryptoJS.PBKDF2(password, this.encryptionSalt, {
          keySize: 256/32,
          iterations: 100000,
          hasher: CryptoJS.algo.SHA256
        });

        // Decrypt
        const decrypted = CryptoJS.AES.decrypt(
          { ciphertext: ciphertext },
          key,
          {
            iv: iv,
            mode: CryptoJS.mode.CBC,
            padding: CryptoJS.pad.Pkcs7
          }
        );

        // Convert to string and parse JSON
        const decryptedString = decrypted.toString(CryptoJS.enc.Utf8);
        if (!decryptedString) {
          throw new Error('Decryption failed - wrong password?');
        }

        return JSON.parse(decryptedString);
      } catch (error) {
        console.error('Decryption error:', error);
        throw error;
      }
    },
    async fetchWatchlist() {
      try {
        const response = await axios.get(`${this.apiBaseUrl}/watchlist/daily`);
        this.watchlist = response.data || [];
      } catch (error) {
        console.error('Error fetching watchlist:', error);
      }
    },
    async fetchSignals() {
      try {
        const response = await axios.get(`${this.apiBaseUrl}/signals?date=${this.signalDate}`);
        const signalsData = response.data || [];

        // Group signals by ticker
        const signalsByTicker = {};
        signalsData.forEach(signal => {
          // Determine ticker and direction based on signal type
          let ticker, direction, strategy;

          // Handle different signal formats
          if (signal.ticker) {
            ticker = signal.ticker;
          } else if (signal.symbol) {
            ticker = signal.symbol;
          } else {
            console.warn('Skipping signal without ticker/symbol:', signal);
            return;
          }

          // Get direction from various possible fields
          if (signal.direction) {
            direction = signal.direction;
          } else if (signal.manipulation_direction) {
            direction = signal.manipulation_direction;
          } else if (signal.signal) {
            direction = signal.signal; // SMB signals use 'signal' field
          } else {
            console.warn('Skipping signal without direction:', signal);
            return;
          }

          if (!signal.timestamp && !signal.time) {
            console.warn('Skipping signal without timestamp:', signal);
            return;
          }

          // Determine strategy/pattern name
          if (signal.strategy) {
            strategy = signal.strategy === 'VWAP' ? 'VWAP Cons' : signal.strategy;
          } else if (signal.scalp) {
            strategy = signal.scalp; // SMB scalp type (Back$ide, Second Chance, etc.)
          } else if (signal.signal_type) {
            if (signal.signal_type.includes('MANIPULATION_REVERSAL_WATCH')) {
              strategy = 'MR Watch';
            } else if (signal.signal_type.includes('MANIPULATION_REVERSAL')) {
              strategy = 'ManipulationReversal';
            } else if (signal.signal_type.includes('VWAP')) {
              strategy = 'VWAP Cons';
            } else {
              strategy = signal.signal_type;
            }
          } else {
            strategy = 'Unknown';
          }

          if (!signalsByTicker[ticker]) {
            signalsByTicker[ticker] = [];
          }

          // Calculate age and format signal
          const timestamp = signal.timestamp || signal.time;
          let signalTime;

          // Parse timestamp based on format
          if (timestamp.includes('T') && timestamp.endsWith('Z')) {
            // ISO format with Z: 2025-10-29T14:30:00Z (already UTC)
            signalTime = new Date(timestamp);
          } else if (timestamp.includes('T')) {
            // ISO format without Z: 2025-10-29T14:30:00 or 2025-10-29T13:30
            // These are in Eastern Time (from Java backend VWAP signals)
            // Parse similar to SMB signals
            const [datePart, timePart] = timestamp.split('T');
            const [hours, minutes, seconds] = timePart.split(':').map(Number);
            const dateStr = `${datePart}T${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(seconds || 0).padStart(2, '0')}`;

            const utcDate = new Date(dateStr + 'Z');
            const etOffset = new Date().toLocaleString('en-US', {
              timeZone: 'America/New_York',
              timeZoneName: 'short'
            }).includes('EDT') ? -4 : -5;
            signalTime = new Date(utcDate.getTime() - (etOffset * 60 * 60 * 1000));
          } else if (timestamp.includes(':')) {
            // Time only format: 14:30:00 - SMB signals are stored in Eastern Time
            // Create a date string that forces Eastern Time interpretation
            // We'll parse it by manually extracting hours/minutes and creating ET date
            const [hours, minutes, seconds] = timestamp.split(':').map(Number);
            const dateStr = `${this.signalDate}T${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(seconds || 0).padStart(2, '0')}`;

            // Parse as if it's in Eastern timezone using Intl DateTimeFormat
            // Create the date in UTC first, then adjust for ET offset
            const utcDate = new Date(dateStr + 'Z'); // Parse as UTC

            // Get current ET offset (EDT = UTC-4, EST = UTC-5)
            const etOffset = new Date().toLocaleString('en-US', {
              timeZone: 'America/New_York',
              timeZoneName: 'short'
            }).includes('EDT') ? -4 : -5;

            // The time is already in ET format, so we need to subtract the offset to get UTC
            // then create a Date object from that UTC time
            signalTime = new Date(utcDate.getTime() - (etOffset * 60 * 60 * 1000));
          } else {
            console.warn('Unknown timestamp format:', timestamp);
            signalTime = new Date();
          }

          const now = new Date();
          const ageSeconds = Math.abs((now - signalTime) / 1000);

          let age, isNew;
          if (!isFinite(ageSeconds)) {
            age = '';
            isNew = false;
          } else if (ageSeconds < 120) {
            age = `${Math.floor(ageSeconds)}s`;
            isNew = true;
          } else if (ageSeconds < 3600) {
            age = `${Math.floor(ageSeconds / 60)}m`;
            isNew = ageSeconds < 300; // New if less than 5 minutes (300 seconds)
          } else {
            age = `${Math.floor(ageSeconds / 3600)}h`;
            isNew = false;
          }

          // Format time in Eastern Time
          let displayTime;
          displayTime = signalTime.toLocaleTimeString('en-US', {
            hour: '2-digit',
            minute: '2-digit',
            hour12: false,
            timeZone: 'America/New_York'
          });

          signalsByTicker[ticker].push({
            pattern: strategy,
            timeframe: '1m',
            time: displayTime,
            direction: direction,
            entry_price: signal.entry_price || '',
            stop_loss: signal.stop_loss || signal.stop_loss_price || '',
            risk_per_share: signal.risk_per_share || '',
            targets: signal.targets || {},
            age: age,
            is_new: isNew,
            is_active: ageSeconds < 1800, // Active for 30 minutes
            timestamp: timestamp, // Store original for sorting
            signalTime: signalTime.getTime(), // Store parsed time for sorting
            raw_signal: signal // Store full signal for card display
          });
        });

        // Check for new signals and play sound - but only for visible tickers
        // Visible tickers are those in watchlist OR those with SMB/VWAP signals (shown in bottom section)
        for (const ticker in signalsByTicker) {
          const currentCount = signalsByTicker[ticker].length;
          const previousCount = this.lastSignalCount[ticker] || 0;

          if (currentCount > previousCount) {
            // Check if this ticker is visible (in watchlist OR has SMB/VWAP Cons signals)
            const isInWatchlist = this.watchlist.includes(ticker);
            const signals = signalsByTicker[ticker] || [];
            const hasVisibleSignals = signals.some(signal =>
              signal.pattern === 'Back$ide' ||
              signal.pattern === 'Second Chance' ||
              signal.pattern === 'Rubberband' ||
              signal.pattern === 'Fashionably Late' ||
              signal.pattern === 'VWAP Cons'
            );

            if (isInWatchlist || hasVisibleSignals) {
              // New signal detected for a visible ticker
              this.playAlertSound();
              // Track when we detected this new signal
              this.newlyDetectedSignals[ticker] = Date.now();
              break; // Only play once per fetch
            }
          }
        }

        // Update last signal counts
        for (const ticker in signalsByTicker) {
          this.lastSignalCount[ticker] = signalsByTicker[ticker].length;
        }

        this.signals = signalsByTicker;
      } catch (error) {
        console.error('Error fetching signals:', error);
      }
    },
    async pollDataJson() {
      try {
        // Check if password is available (stored in localStorage)
        if (!this.decryptionPassword) {
          this.decryptionPassword = localStorage.getItem('watchlist_password');
        }

        // If no password, prompt user
        if (!this.decryptionPassword && !this.isPasswordPromptShown) {
          this.isPasswordPromptShown = true;
          const password = prompt('Enter password to view watchlist data:');
          if (!password) {
            console.log('No password provided');
            return;
          }
          this.decryptionPassword = password;
          localStorage.setItem('watchlist_password', password);
        }

        // Fetch encrypted data.json.enc with cache busting
        const response = await fetch(`/data.json.enc?t=${Date.now()}`);
        if (!response.ok) {
          console.error('Failed to fetch data.json.enc:', response.status);
          return;
        }

        const encryptedData = await response.text();

        // Decrypt the data
        let data;
        try {
          data = this.decryptData(encryptedData, this.decryptionPassword);
        } catch (decryptError) {
          console.error('Decryption failed:', decryptError);
          // Clear password and prompt again
          localStorage.removeItem('watchlist_password');
          this.decryptionPassword = null;
          this.isPasswordPromptShown = false;
          alert('Decryption failed. Wrong password?');
          return;
        }

        // Update watchlist
        if (data.watchlist) {
          this.watchlist = data.watchlist;
        }

        // Update indicators
        if (data.indicators) {
          this.indicators = { ...data.indicators };
        }

        // Update signals - process like fetchSignals does
        if (data.signals) {
          const signalsByTicker = {};

          for (const signal of data.signals) {
            const ticker = signal.ticker || signal.symbol;
            if (!ticker) continue;

            if (!signalsByTicker[ticker]) {
              signalsByTicker[ticker] = [];
            }

            const strategy = signal.strategy || signal.signal_type || 'Signal';
            const direction = (signal.direction || '').toUpperCase();
            let timestamp = signal.timestamp || signal.time || '';

            // Parse timestamp for signal time
            let signalTime;
            if (typeof timestamp === 'string' && timestamp) {
              // Determine current ET offset (EDT = -04:00, EST = -05:00)
              const now = new Date();
              const etOffset = now.toLocaleString('en-US', {
                timeZone: 'America/New_York',
                timeZoneName: 'short'
              }).includes('EDT') ? '-04:00' : '-05:00';

              // Handle different timestamp formats
              let fullTimestamp;
              if (timestamp.includes('T')) {
                // Full ISO format: "2025-10-30T14:02" or "2025-10-30T14:02:00"
                fullTimestamp = timestamp;
              } else if (timestamp.match(/^\d{2}:\d{2}(:\d{2})?$/)) {
                // Time only: "14:27:00" or "14:27" - add today's date
                const today = data.date || new Date().toISOString().split('T')[0];
                fullTimestamp = `${today}T${timestamp}`;
              } else {
                // Invalid format, use current time
                signalTime = new Date();
                fullTimestamp = null;
              }

              if (fullTimestamp) {
                // Add :00 for seconds if not present, then add offset
                const timestampWithSeconds = fullTimestamp.match(/T\d{2}:\d{2}$/)
                  ? fullTimestamp + ':00'
                  : fullTimestamp;
                const etTimestamp = timestampWithSeconds + (timestampWithSeconds.includes('Z') || timestampWithSeconds.includes('-0') || timestampWithSeconds.includes('+0') ? '' : etOffset);
                signalTime = new Date(etTimestamp);
              }
            } else {
              signalTime = new Date();
            }

            const now = new Date();
            const ageSeconds = Math.abs((now - signalTime) / 1000);

            let age, isNew;
            if (!isFinite(ageSeconds)) {
              age = '';
              isNew = false;
            } else if (ageSeconds < 120) {
              age = `${Math.floor(ageSeconds)}s`;
              isNew = true;
            } else if (ageSeconds < 3600) {
              age = `${Math.floor(ageSeconds / 60)}m`;
              isNew = ageSeconds < 300;
            } else {
              age = `${Math.floor(ageSeconds / 3600)}h`;
              isNew = false;
            }

            let displayTime;
            displayTime = signalTime.toLocaleTimeString('en-US', {
              hour: '2-digit',
              minute: '2-digit',
              hour12: false,
              timeZone: 'America/New_York'
            });

            signalsByTicker[ticker].push({
              pattern: strategy,
              timeframe: '1m',
              time: displayTime,
              direction: direction,
              entry_price: signal.entry_price || '',
              stop_loss: signal.stop_loss || signal.stop_loss_price || '',
              risk_per_share: signal.risk_per_share || '',
              targets: signal.targets || {},
              age: age,
              is_new: isNew,
              is_active: ageSeconds < 1800,
              timestamp: timestamp,
              signalTime: signalTime.getTime(),
              raw_signal: signal
            });
          }

          // Check for new signals (for audio alerts)
          for (const ticker in signalsByTicker) {
            const currentCount = signalsByTicker[ticker].length;
            const lastCount = this.lastSignalCount[ticker] || 0;

            if (currentCount > lastCount) {
              console.log(`New signal detected for ${ticker}`);
              this.newlyDetectedSignals[ticker] = Date.now();
              this.playAlertSound();
            }
          }

          // Update last signal counts
          for (const ticker in signalsByTicker) {
            this.lastSignalCount[ticker] = signalsByTicker[ticker].length;
          }

          this.signals = signalsByTicker;
        }

        console.log('Data updated from data.json');
      } catch (error) {
        console.error('Error polling data.json:', error);
      }
    },
    initIndicatorsWebSocket() {
      const wsUrl = `ws://${window.location.hostname}:8000/ws/indicators`;
      this.indicatorsWs = new WebSocket(wsUrl);

      this.indicatorsWs.onmessage = (event) => {
        try {
          const msg = JSON.parse(event.data);

          if (msg.type === "INDICATORS_UPDATE") {
            const newData = msg.data || {};
            this.indicators = { ...newData };
          }

        } catch (error) {
          console.error('Error processing WebSocket message:', error);
        }
      };

      this.indicatorsWs.onerror = (error) => {
        console.error('WebSocket error:', error);
      };

      this.indicatorsWs.onclose = () => {
        setTimeout(() => {
          this.initIndicatorsWebSocket();
        }, 5000);
      };
    },
    calculateAge(signal) {
      // Recalculate age in real-time based on signal timestamp
      if (!signal.signalTime) return signal.age || '';

      // Force recalculation by using this.currentTime (updates every second)
      const now = this.currentTime;
      const ageSeconds = Math.abs((now - signal.signalTime) / 1000);

      if (!isFinite(ageSeconds)) return '';
      if (ageSeconds < 120) return `${Math.floor(ageSeconds)}s`;
      if (ageSeconds < 3600) return `${Math.floor(ageSeconds / 60)}m`;
      return `${Math.floor(ageSeconds / 3600)}h`;
    },
    getSignalsForTicker(ticker) {
      const allSignals = this.signals[ticker] || [];

      // If "Active Signals Only" is selected, filter out inactive signals
      if (this.viewMode === 'active-signals') {
        return allSignals.filter(signal => signal.is_active);
      }

      return allSignals;
    },
    hasNewSignal(ticker) {
      // Check if we detected a new signal for this ticker within the last 10 minutes
      const detectionTime = this.newlyDetectedSignals[ticker];
      if (!detectionTime) {
        return false;
      }
      const ageMs = Date.now() - detectionTime;
      const tenMinutesMs = 10 * 60 * 1000; // 10 minutes in milliseconds
      return ageMs < tenMinutesMs;
    },
    formatSignalText(signal) {
      // Format: "VWAP Cons 1m 10:34 LONG @445.23"
      const direction = signal.direction || 'N/A';
      const entryPrice = signal.entry_price || '';
      return `${signal.pattern} ${signal.timeframe} ${signal.time} ${direction} @${entryPrice}`;
    },
    getSignalClass(signal) {
      const direction = signal.direction ? signal.direction.toLowerCase() : 'unknown';
      const baseClass = `pattern-signal ${direction}`;
      const statusClass = signal.is_active ? 'active' : 'inactive';
      const newClass = signal.is_new ? 'new-pattern' : '';
      return `${baseClass} ${statusClass} ${newClass}`.trim();
    },
    async addSymbol(ticker = null) {
      // If ticker is provided (from + button), use it; otherwise use newSymbol input
      let symbol = null;

      if (ticker && typeof ticker === 'string') {
        symbol = ticker.toUpperCase();
      } else if (this.newSymbol && this.newSymbol.trim()) {
        symbol = this.newSymbol.trim().toUpperCase();
      }

      if (symbol) {
        try {
          await axios.post(`${this.apiBaseUrl}/watchlist/daily`, { symbols: symbol });
          await this.fetchWatchlist();
          if (!ticker || typeof ticker !== 'string') {
            this.newSymbol = ''; // Only clear input if adding from form
          }
        } catch (error) {
          console.error('Error adding symbol:', error);
        }
      }
    },
    async removeSymbol(ticker) {
      try {
        await axios.delete(`${this.apiBaseUrl}/watchlist/daily/${ticker}`);
        await this.fetchWatchlist();
      } catch (error) {
        console.error('Error removing symbol:', error);
      }
    },
    getPrice(ticker) {
      const price = this.indicators[ticker]?.periods?.['1min']?.indicators?.bar?.close;
      return price ? `$${parseFloat(price).toFixed(2)}` : '';
    },
    getPercent(ticker) {
      // Calculate percent from price change
      const current = this.indicators[ticker]?.periods?.['1min']?.indicators?.bar?.close;
      const open = this.indicators[ticker]?.periods?.['1min']?.indicators?.bar?.open;
      if (!current || !open) return '';
      const percent = ((current - open) / open) * 100;
      return `${percent > 0 ? '+' : ''}${percent.toFixed(1)}%`;
    },
    getPriceClass(ticker) {
      const current = this.indicators[ticker]?.periods?.['1min']?.indicators?.bar?.close;
      const open = this.indicators[ticker]?.periods?.['1min']?.indicators?.bar?.open;
      if (!current || !open) return '';
      return current > open ? 'price-up' : 'price-down';
    },
    getRSI(ticker, period) {
      const rsiValue = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.oscillators?.rsi14?.value;
      return rsiValue ? parseFloat(rsiValue).toFixed(1) : '';
    },
    getRSIClass(ticker, period) {
      const rsiValue = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.oscillators?.rsi14?.value;
      if (!rsiValue) return 'rsi-neutral';
      const rsi = parseFloat(rsiValue);
      if (rsi < 30) return 'rsi-oversold';
      if (rsi > 70) return 'rsi-overbought';
      return 'rsi-neutral';
    },
    getBB(ticker, period) {
      // Calculate BB% from bands
      const close = this.indicators[ticker]?.periods?.[period]?.indicators?.bar?.close;
      const upper = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.bands?.bollinger20_2?.upper;
      const lower = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.bands?.bollinger20_2?.lower;

      if (!close || !upper || !lower) return '';

      const bb_percent = ((parseFloat(close) - parseFloat(lower)) / (parseFloat(upper) - parseFloat(lower))) * 100;
      return bb_percent.toFixed(0);
    },
    getBBClass(ticker, period) {
      const close = this.indicators[ticker]?.periods?.[period]?.indicators?.bar?.close;
      const upper = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.bands?.bollinger20_2?.upper;
      const lower = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.bands?.bollinger20_2?.lower;

      if (!close || !upper || !lower) return 'bb-mid';

      const bb_percent = ((parseFloat(close) - parseFloat(lower)) / (parseFloat(upper) - parseFloat(lower))) * 100;
      if (bb_percent < 20) return 'bb-low';
      if (bb_percent > 80) return 'bb-high';
      return 'bb-mid';
    },
    getRVOL(ticker) {
      const volume = this.indicators[ticker]?.periods?.['1min']?.indicators?.bar?.volume;
      if (!volume || isNaN(volume)) return '';
      return parseFloat(volume).toFixed(1);
    },
    getMacdDotClass(ticker, period) {
      const macd = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.oscillators?.macd12_26_9_calc?.macd;
      const signal = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.oscillators?.macd12_26_9_calc?.signal;

      if (macd !== undefined && signal !== undefined) {
        return parseFloat(macd) > parseFloat(signal) ? 'dot-green' : 'dot-red';
      }
      return 'dot-black';
    },
    getEmaDotClass(ticker, period, ema1, ema2) {
      const ema1Val = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.emas?.[`ema${ema1}`];
      const ema2Val = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.emas?.[`ema${ema2}`];

      if (ema1Val !== undefined && ema2Val !== undefined) {
        return parseFloat(ema1Val) > parseFloat(ema2Val) ? 'dot-green' : 'dot-red';
      }
      return 'dot-black';
    },
    getSqueezeSegmentStyle(ticker, period, index) {
      const squeeze = this.indicators[ticker]?.periods?.[period]?.indicators?.indicator?.ttmSqueeze;

      let backgroundColor;
      if (squeeze) {
        if (squeeze.doubleSqueezeOn) {
          backgroundColor = '#ff3860';  // Red for double squeeze (very tight)
        } else if (squeeze.squeezeOn) {
          backgroundColor = '#ffdd57';  // Yellow for single squeeze (tight)
        } else {
          backgroundColor = '#adb5bd';  // Neutral gray for no squeeze (normal)
        }
      } else {
        backgroundColor = '#6c757d';    // Dark gray for no data
      }

      return {
        backgroundColor,
        width: '20%',
        left: (index * 20) + '%'
      };
    },
    saveAlertSoundPreference() {
      localStorage.setItem('alertSoundEnabled', this.alertSoundEnabled);
    },
    saveChimePreference() {
      localStorage.setItem('selectedChime', this.selectedChime);
    },
    playAlertSound() {
      if (!this.alertSoundEnabled) return;

      try {
        if (!this.audioContext) {
          this.audioContext = new (window.AudioContext || window.webkitAudioContext)();
        }

        switch (this.selectedChime) {
          case 'beep':
            this.playBeep(this.audioContext);
            break;
          case 'ding':
            this.playDing(this.audioContext);
            break;
          case 'chime':
            this.playChime(this.audioContext);
            break;
          case 'bell':
            this.playBell(this.audioContext);
            break;
          case 'notification':
            this.playNotification(this.audioContext);
            break;
          case 'sonar':
            this.playSonar(this.audioContext);
            break;
          case 'ascending':
            this.playAscending(this.audioContext);
            break;
          case 'descending':
            this.playDescending(this.audioContext);
            break;
          case 'double-beep':
            this.playDoubleBeep(this.audioContext);
            break;
          case 'triple-tone':
            this.playTripleTone(this.audioContext);
            break;
          case 'urgent':
            this.playUrgent(this.audioContext);
            break;
          case 'subtle':
            this.playSubtle(this.audioContext);
            break;
        }
      } catch (error) {
        console.error('Error playing alert sound:', error);
      }
    },
    playBeep(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 800;
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.3, audioContext.currentTime + 0.01);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.2);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.2);
    },
    playDing(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 1200;
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.4, audioContext.currentTime + 0.005);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.3);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.3);
    },
    playChime(audioContext) {
      [1000, 800].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.15);
        oscillator.frequency.value = freq;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.3, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.25);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.25);
      });
    },
    playBell(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 1500;
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.5, audioContext.currentTime + 0.002);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.5);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.5);
    },
    playNotification(audioContext) {
      [0, 0.15, 0.3].forEach((delay) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + delay;
        oscillator.frequency.value = 900;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.25, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.1);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.1);
      });
    },
    playSonar(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 600;
      oscillator.type = 'sine';
      oscillator.frequency.setValueAtTime(600, audioContext.currentTime);
      oscillator.frequency.exponentialRampToValueAtTime(300, audioContext.currentTime + 0.3);

      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.4, audioContext.currentTime + 0.01);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.3);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.3);
    },
    playAscending(audioContext) {
      [600, 800, 1000].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.1);
        oscillator.frequency.value = freq;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.25, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.12);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.12);
      });
    },
    playDescending(audioContext) {
      [1200, 900, 700].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.1);
        oscillator.frequency.value = freq;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.25, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.12);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.12);
      });
    },
    playDoubleBeep(audioContext) {
      [0, 0.2].forEach((delay) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + delay;
        oscillator.frequency.value = 850;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.3, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.15);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.15);
      });
    },
    playTripleTone(audioContext) {
      [523, 659, 784].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.15);
        oscillator.frequency.value = freq;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.25, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.2);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.2);
      });
    },
    playUrgent(audioContext) {
      [1400, 1600, 1400, 1600].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.08);
        oscillator.frequency.value = freq;
        oscillator.type = 'square';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.2, startTime + 0.005);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.07);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.07);
      });
    },
    playSubtle(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 700;
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.15, audioContext.currentTime + 0.01);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.15);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.15);
    },
    async sendToWindow(windowNumber, ticker) {
      const port = 10000 + windowNumber;

      // Build signal payload with ticker info
      const payload = {
        ticker: ticker,
        timestamp: new Date().toISOString()
      };

      try {
        const response = await fetch(`http://localhost:${port}/signal`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const result = await response.json();
        console.log(`Ticker ${ticker} sent to window ${windowNumber}:`, result);

        // Track that this window is now showing this ticker
        this.windowTickerMap[windowNumber] = ticker;

        // Focus the window
        try {
          await fetch(`http://localhost:${port}/focus`, {
            method: 'POST',
          });
        } catch (focusErr) {
          console.warn(`Could not focus window ${windowNumber}:`, focusErr);
        }
      } catch (err) {
        console.error(`Failed to send ticker to window ${windowNumber}:`, err);
        alert(`Failed to send to window ${windowNumber}: ${err.message}`);
      }
    },
    isTickerInWindow(ticker, windowNumber) {
      return this.windowTickerMap[windowNumber] === ticker;
    },
    async bringAllWindowsForward() {
      // Focus all windows from 1 to 6
      for (let windowNumber = 1; windowNumber <= 6; windowNumber++) {
        const port = 10000 + windowNumber;
        try {
          await fetch(`http://localhost:${port}/focus`, {
            method: 'POST',
          });
        } catch (err) {
          console.warn(`Could not focus window ${windowNumber}:`, err);
        }
      }
    },
    parseSignalTimestamp(signal) {
      // Helper method to parse signal timestamp into a comparable number
      // Used for sorting signals by time
      const timestamp = signal.timestamp || signal.time;
      if (!timestamp) return 0;

      let signalTime;
      if (timestamp.includes('T')) {
        const timestampWithZ = timestamp.endsWith('Z') ? timestamp : timestamp + 'Z';
        signalTime = new Date(timestampWithZ);
      } else if (timestamp.includes(':')) {
        // SMB signals are stored in Eastern Time, not UTC
        signalTime = new Date(`${this.signalDate}T${timestamp}`);
      } else {
        return 0;
      }

      return signalTime.getTime();
    }
  }
};
