<template>
  <div class="order-entry-panel" :style="panelStyle">
    <table class="layout-table">
      <tr>
        <td class="main-content">
          <div class="main-order-controls">
      <!-- Additional preset buttons for side override and stop loss -->
    <div class="preset-buttons">
      <div class="timeframe-selection">
        <span>Timeframe:</span>
        <button
          v-for="tf in timeframes"
          :key="tf"
          class="timeframe-button"
          :class="{ 'active': selectedTimeframe === tf }"
          @click="setTimeframe(tf)"
        >
          {{ tf }}
        </button>
        <!-- Clickable Status Message -->
        <div
          class="status-message-inline"
          :class="statusMessageType || 'placeholder'"
          @click="showFullStatusMessage"
          :title="statusMessage || 'Status messages will appear here'">
          {{ statusMessage || 'Status messages will appear here' }}
        </div>
      </div>
      <div class="stoploss-presets">
        <button class="side-arrow buy-arrow" @click="setSide('Buy')" title="Buy"></button>
        <button class="side-arrow sellshort-arrow" @click="setSide('SellShort')" title="Sell Short"></button>
        <button class="stoploss-button candle-based" @click="loadStopLoss(5)">
          <div class="button-label">SL 5 </div>
          <div v-if="stopLossValues[5]" class="button-value">
            <div class="price-line">${{ formatPrice(stopLossValues[5][side]) }}</div>
            <div class="risk-line">R: ${{ calculateRiskPerShare(stopLossValues[5][side]) }}</div>
          </div>
        </button>
        <button class="stoploss-button candle-based" @click="loadStopLoss(10)">
          <div class="button-label">SL 10 </div>
          <div v-if="stopLossValues[10]" class="button-value">
            <div class="price-line">${{ formatPrice(stopLossValues[10][side]) }}</div>
            <div class="risk-line">R: ${{ calculateRiskPerShare(stopLossValues[10][side]) }}</div>
          </div>
        </button>
        <button class="stoploss-button candle-based" @click="loadStopLoss(15)">
          <div class="button-label">SL 15 </div>
          <div v-if="stopLossValues[15]" class="button-value">
            <div class="price-line">${{ formatPrice(stopLossValues[15][side]) }}</div>
            <div class="risk-line">R: ${{ calculateRiskPerShare(stopLossValues[15][side]) }}</div>
          </div>
        </button>
        <div class="group-spacer"></div>
        <button class="stoploss-button atr-based" @click="loadStopLossATR(1)">
          <div class="button-label">SL 1 ATR</div>
          <div v-if="atrStopLossValues[1]" class="button-value">
            <div class="price-line">${{ formatPrice(atrStopLossValues[1][side]) }}</div>
            <div class="risk-line">R: ${{ calculateRiskPerShare(atrStopLossValues[1][side]) }}</div>
          </div>
        </button>
        <button class="stoploss-button atr-based" @click="loadStopLossATR(2)">
          <div class="button-label">SL 2 ATR</div>
          <div v-if="atrStopLossValues[2]" class="button-value">
            <div class="price-line">${{ formatPrice(atrStopLossValues[2][side]) }}</div>
            <div class="risk-line">R: ${{ calculateRiskPerShare(atrStopLossValues[2][side]) }}</div>
          </div>
        </button>
        <button class="stoploss-button atr-based" @click="loadStopLossATR(2.5)">
          <div class="button-label">SL 2.5 ATR</div>
          <div v-if="atrStopLossValues[2.5]" class="button-value">
            <div class="price-line">${{ formatPrice(atrStopLossValues[2.5][side]) }}</div>
            <div class="risk-line">R: ${{ calculateRiskPerShare(atrStopLossValues[2.5][side]) }}</div>
          </div>
        </button>
      </div>
    </div>
    
    <!-- Main order entry form -->
    <table class="order-table">
      <tr>
        <td>
          <input class="input_field" v-model="symbol" disabled size="8" />
        </td>
        <td>
          <label>Risk$:</label>
        </td>
        <td>
          <label>Price:</label>
        </td>
        <td>
          <label>Stop:</label>
        </td>
        <td>
          <label class="breakout-toggle">
            <input type="checkbox" v-model="isBreakoutOrder" />
            Breakout Order
          </label>
        </td>
      </tr>
      <tr>
        <td>
          <select class="input_field" v-model="side">
            <option value="Buy">Long</option>
            <option value="Sell">Sell</option>
            <option value="SellShort">Short</option>
            <option value="BuyToCover">BuyToCover</option>
          </select>
        </td>
        <td>
          <input class="input_field" type="number" v-model.number="risk" size="3" />
        </td>
        <td>
          <input class="input_field price-stop-field" type="number" step="0.01" v-model.number="price" size="10" />
        </td>
        <td>
          <input class="input_field price-stop-field" type="number" step="0.01" v-model.number="stopLoss" size="10" />
        </td>
        <td>
          <button class="input_field" @click="placeOrder(false)">{{ isBreakoutOrder ? 'Place Breakout' : 'Place Limit' }}</button>
          <button class="input_field" @click="placeFastOrder" :disabled="!isFreshDataAvailable || isBreakoutOrder" style="margin-left: 0.5rem;">
            {{ isBreakoutOrder ? 'N/A' : 'Scalp' }}
          </button>
        </td>
      </tr>
    </table>

    <div v-if="isBreakoutOrder" class="breakout-controls-wrapper">
      <div class="form-field">
        <label>Trigger:</label>
        <input class="input_field" type="number" step="0.01" v-model.number="triggerPrice" size="8" placeholder="Break price" />
        <span class="breakout-hint">{{ breakoutHint }}</span>
      </div>
      <div class="form-field breakout-controls">
        <label>Lookback:</label>
        <select class="input_field lookback-select" v-model.number="lookbackPeriod">
          <option value="5">5 candles</option>
          <option value="10">10 candles</option>
          <option value="15">15 candles</option>
          <option value="20">20 candles</option>
        </select>
        <button class="local-level-btn" @click="useLocalHigh" :disabled="!canUseLocalLevels">
          Use Local High
        </button>
        <button class="local-level-btn" @click="useLocalLow" :disabled="!canUseLocalLevels">
          Use Local Low
        </button>
      </div>
    </div>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: "OrderEntryPanel",
  props: {
    currentSymbol: {
      type: String,
      default: ""
    },
    selectedPrice: {
      type: Number,
      default: null
    },
    selectedSide: {
      type: String,
      default: "Buy"
    },
    currentQuote: {
      type: Object,
      default: () => ({ bid: null, ask: null })
    },
    isQuoteLive: {
      type: Boolean,
      default: false
    },
    level2Ref: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      side: "Buy",
      risk: 100,
      price: null,
      stopLoss: null,
      isBreakoutOrder: false,
      triggerPrice: null,
      lookbackPeriod: 10,
      stopLossValues: {},
      atrStopLossValues: {},
      currentMinute: new Date().getMinutes(),
      minuteTimer: null,
      statusMessage: null,
      statusMessageType: null,
      statusMessageTimer: null,
      showTestValues: false,
      ohlcWs: null,
      currentATR: null,
      currentPrice: null,
      selectedTimeframe: '1m',
      timeframes: ['1m', '2m', '3m', '5m', '15m'],
      historicalBars: [],
      currentTime: Date.now(), // Reactive timestamp for freshness checks
      freshnessCheckTimer: null, // Timer for updating currentTime
      // Track ATR and historical bars for each timeframe
      atrByTimeframe: {
        '1m': null,
        '2m': null,
        '3m': null,
        '5m': null,
        '15m': null
      },
      historicalBarsByTimeframe: {
        '1m': [],
        '2m': [],
        '3m': [],
        '5m': [],
        '15m': []
      },
      autoRefreshInterval: null
    };
  },
  computed: {
    symbol() {
      return this.currentSymbol;
    },
    panelStyle() {
      if (this.side === "Buy") {
        return { backgroundColor: "#ccffcc" };
      } else if (this.side === "SellShort") {
        return { backgroundColor: "#ffcccc" };
      } else {
        return {};
      }
    },
    breakoutHint() {
      if (!this.isBreakoutOrder) return '';
      if (this.side === 'Buy' || this.side === 'BuyToCover') {
        return 'Order executes when price breaks ABOVE trigger';
      } else {
        return 'Order executes when price breaks BELOW trigger';
      }
    },
    canUseLocalLevels() {
      return this.historicalBars && this.historicalBars.length >= Math.min(this.lookbackPeriod, 5);
    },
    // Check if fresh data is available from either Quote or Level2 for Scalp button
    isFreshDataAvailable() {
      const now = this.currentTime; // Use reactive timestamp instead of Date.now()
      const MAX_AGE_MS = 5000; // 5 seconds

      // Check Quote data
      if (this.currentQuote?.bidPrice && this.currentQuote?.askPrice && this.currentQuote?.receivedTimestamp) {
        const quoteAge = now - this.currentQuote.receivedTimestamp;
        if (quoteAge < MAX_AGE_MS) {
          return true;
        }
      }

      // Check Level2 data
      if (this.level2Ref) {
        const level2 = this.level2Ref;
        const level2Age = level2.lastHeartbeat ? now - level2.lastHeartbeat : null;

        if (level2Age !== null && level2Age < MAX_AGE_MS) {
          // Check if we have top of book data
          const topBid = level2.groupedBids?.[0];
          const topAsk = level2.groupedAsks?.[0];

          if (topBid?.Price && topAsk?.Price) {
            return true;
          }
        }
      }

      return false;
    }
  },
  watch: {
    selectedSide(newSide) {
      if (newSide) {
        this.side = newSide;
      }
    },
    selectedPrice(newPrice) {
      if (newPrice !== null) {
        this.price = newPrice;
      }
    },
    currentSymbol(newSymbol) {
      if (newSymbol) {
        if (this.ohlcWs) {
          this.ohlcWs.close();
        }
        // this.initOhlcWebSocket();
      }
    }
  },
  methods: {
    // Get the freshest bid/ask data from either Quote or Level2
    // Returns { bid, ask, source, age } or null if no fresh data available
    getFreshestBidAsk() {
      const now = this.currentTime; // Use reactive timestamp
      const MAX_AGE_MS = 5000; // 5 seconds

      let quoteBid = null, quoteAsk = null, quoteAge = null;
      let level2Bid = null, level2Ask = null, level2Age = null;

      // Check Quote data
      if (this.currentQuote?.bidPrice && this.currentQuote?.askPrice && this.currentQuote?.receivedTimestamp) {
        quoteAge = now - this.currentQuote.receivedTimestamp;
        if (quoteAge < MAX_AGE_MS) {
          quoteBid = this.currentQuote.bidPrice;
          quoteAsk = this.currentQuote.askPrice;
        }
      }

      // Check Level2 data
      if (this.level2Ref) {
        const level2 = this.level2Ref;
        level2Age = level2.lastHeartbeat ? now - level2.lastHeartbeat : null;

        if (level2Age !== null && level2Age < MAX_AGE_MS) {
          // Get top of book from Level2
          const topBid = level2.groupedBids?.[0];
          const topAsk = level2.groupedAsks?.[0];

          if (topBid?.Price && topAsk?.Price) {
            level2Bid = parseFloat(topBid.Price);
            level2Ask = parseFloat(topAsk.Price);
          }
        }
      }

      // Determine which source is fresher
      let selectedBid = null, selectedAsk = null, source = null, age = null;

      if (quoteBid !== null && level2Bid !== null) {
        // Both available, use fresher one
        if (quoteAge <= level2Age) {
          selectedBid = quoteBid;
          selectedAsk = quoteAsk;
          source = 'Quote';
          age = quoteAge;
        } else {
          selectedBid = level2Bid;
          selectedAsk = level2Ask;
          source = 'Level2';
          age = level2Age;
        }
      } else if (quoteBid !== null) {
        selectedBid = quoteBid;
        selectedAsk = quoteAsk;
        source = 'Quote';
        age = quoteAge;
      } else if (level2Bid !== null) {
        selectedBid = level2Bid;
        selectedAsk = level2Ask;
        source = 'Level2';
        age = level2Age;
      }

      if (selectedBid !== null && selectedAsk !== null) {
        console.log(` Using fresh ${source} data: bid=${selectedBid}, ask=${selectedAsk}, age=${(age/1000).toFixed(1)}s`);
        return { bid: selectedBid, ask: selectedAsk, source, age };
      }

      console.warn(' No fresh bid/ask data available (< 5 seconds)');
      return null;
    },

    // Handles placing a limit order with optional stop-loss
    async placeOrder(isFastOrder = false) {
      let priceToSend = this.price;
      let stopLossToSend = this.stopLoss;

      // Unconditional check for stop loss. It is now always mandatory.
      if (stopLossToSend === null || stopLossToSend === '' || stopLossToSend <= 0) {
        alert("A positive Stop price is required for all orders.");
        return;
      }

      // Breakout order validation
      if (this.isBreakoutOrder) {
        if (this.triggerPrice === null || this.triggerPrice === '' || this.triggerPrice <= 0) {
          alert("A trigger price is required for breakout orders.");
          return;
        }
        
        // Validate trigger price direction based on side
        if ((this.side === 'Buy' || this.side === 'BuyToCover') && this.triggerPrice <= (this.currentQuote?.bidPrice || this.currentPrice || 0)) {
          alert("For long breakout orders, trigger price must be ABOVE current price.");
          return;
        }
        if ((this.side === 'SellShort' || this.side === 'Sell') && this.triggerPrice >= (this.currentQuote?.askPrice || this.currentPrice || 999999)) {
          alert("For short breakout orders, trigger price must be BELOW current price.");
          return;
        }
      }

      const payload = {
        symbol:       this.symbol,
        trade_action: this.side.toUpperCase(),
        risk_value:   this.risk,
        stop_price:   stopLossToSend, // Stop loss is now always sent
        limit_price:  null,
        current_bid:  null,
        current_ask:  null,
        is_breakout_order: this.isBreakoutOrder,
        trigger_price: this.isBreakoutOrder ? this.triggerPrice : null,
      };

      if (isFastOrder) {
        // "Scalp" button: Price is optional.
        if (priceToSend === null || priceToSend === '' || priceToSend === 0) {
          // Price not specified, so we must use fresh bid/ask
          const freshData = this.getFreshestBidAsk();

          if (!freshData) {
            alert("Cannot place scalp order: No fresh price data available.\n\nBoth Quote and Level2 data are either unavailable or stale (>5 seconds old).\nPlease wait for fresh market data.");
            return;
          }

          // Show user which source we're using
          const ageSeconds = (freshData.age / 1000).toFixed(1);
          console.log(`Using ${freshData.source} data (${ageSeconds}s old) for scalp order`);

          payload.limit_price = null;
          payload.current_bid = freshData.bid;
          payload.current_ask = freshData.ask;
        } else {
          // Price was specified on a scalp order, use it
          payload.limit_price = priceToSend;
        }
      } else {
        // "Place Limit" button: Price is mandatory.
        if (priceToSend === null || priceToSend === '' || priceToSend === 0) {
          alert("Price is required for a regular limit order and cannot be zero.");
          return;
        }
        payload.limit_price = priceToSend;
      }

      console.log("Placing order with payload:", payload);
      try {
        const response = await fetch(`http://${window.location.hostname}:8000/api/placeOrderWithStopMarket`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(payload),
        });
        const result = await response.json();
        
        if (!response.ok) {
          const errorMsg = result.detail || result.message || `HTTP ${response.status}: ${response.statusText}`;
          this.showStatusMessage(`Order failed: ${errorMsg}`, 'error');
          console.error("Order placement failed:", result);
          return;
        }
        
        if (result.result && result.result.Orders) {
          const orders = result.result.Orders;
          const failedOrders = orders.filter(order => order.Error === 'FAILED');
          if (failedOrders.length > 0) {
            const errorMessages = failedOrders.map(order => order.Message).join(' ');
            this.showStatusMessage(`Order failed: ${errorMessages}`, 'error');
            console.error("Broker rejected order:", failedOrders);
            return;
          }
        }
        
        console.log("Place order result:", result);
        this.showStatusMessage("Order placed successfully!", 'success');
      } catch (err) {
        console.error("Error placing order:", err);
        this.showStatusMessage(`Network error: ${err.message}`, 'error');
      }
    },
    async placeFastOrder() {
      await this.placeOrder(true);
    },
    setSide(newSide) {
      console.log("Setting side to:", newSide);
      this.side = newSide;
      
      // Emit the view mode to parent for indicators panel (LONG vs SHORT perspective)
      const viewMode = newSide === 'SellShort' ? 'SHORT' : 'LONG';
      console.log('OrderEntryPanel emitting view-mode-changed:', viewMode);
      this.$emit('view-mode-changed', viewMode);
    },
    loadStopLoss(candleCount) {
      // Simply set the stop loss from existing calculated values
      if (this.stopLossValues[candleCount] && this.stopLossValues[candleCount][this.side]) {
        this.stopLoss = this.stopLossValues[candleCount][this.side];
        console.log(` Set stop loss to: ${this.stopLoss} for ${candleCount} candles, side: ${this.side}`);
      } else {
        console.warn(` No stop loss value available for ${candleCount} candles, side: ${this.side}`);
      }
    },
    formatPrice(val) {
      return val != null ? Number(val).toFixed(2) : "-";
    },
    showStatusMessage(message, type, timeout = 5000) {
      this.statusMessage = message;
      this.statusMessageType = type;

      if (this.statusMessageTimer) {
        clearTimeout(this.statusMessageTimer);
      }

      this.statusMessageTimer = setTimeout(() => {
        this.statusMessage = null;
        this.statusMessageType = null;
      }, timeout);
    },
    showFullStatusMessage() {
      if (this.statusMessage && this.statusMessage !== 'Status messages will appear here') {
        alert(this.statusMessage);
      }
    },
    toggleValues() {
      this.showTestValues = !this.showTestValues;
      // Debug: Log current state when toggling
      console.log(' Debug State:');
      console.log('Current timeframe:', this.selectedTimeframe);
      console.log('Current price:', this.currentPrice);
      console.log('Stop loss values:', JSON.stringify(this.stopLossValues));
      console.log('Side:', this.side);
    },
    calculateRiskPerShare(stopPrice) {
      if (!stopPrice) return '0.00';
      
      // Use bid/ask for dynamic pricing if available, otherwise fallback to current price
      let referencePrice;
      if (this.currentQuote && this.currentQuote.bidPrice && this.currentQuote.askPrice) {
        // Use bid for sell orders, ask for buy orders for more realistic risk calculation
        referencePrice = this.side === 'Buy' || this.side === 'BuyToCover' 
          ? this.currentQuote.askPrice 
          : this.currentQuote.bidPrice;
      } else {
        referencePrice = this.currentPrice || this.price || 50;
      }
      
      const riskPerShare = this.side === 'Buy' || this.side === 'BuyToCover' 
        ? referencePrice - stopPrice 
        : stopPrice - referencePrice;
      
      return Math.abs(riskPerShare).toFixed(2);
    },
    initOhlcWebSocket() {
      if (!this.symbol) return;
      
      this.ohlcWs = new WebSocket(
        `ws://${window.location.hostname}:8000/ws/ohlc?symbol=${this.symbol}`
      );
      
      this.ohlcWs.onmessage = ({ data }) => {
        const msg = JSON.parse(data);
        console.log('OHLC WebSocket message received:', msg);
        
        // Handle different message types
        if (msg.type === 'OHLC_UPDATE') {
          const { bar, indicator } = msg;
          
          // Convert timeframe format (1min -> 1m, 2min -> 2m, etc.)
          const expectedTimeframe = this.selectedTimeframe.replace('m', 'min');
          
          console.log('Bar timeframe:', bar?.timeframe, 'Expected:', expectedTimeframe);
          
          // Update current price from selected timeframe only
          if (bar && bar.timeframe === expectedTimeframe) {
            console.log('Updating current price with:', bar);
            this.currentPrice = bar.close;
          }
          
          // Always update bars for all timeframes
          if (bar) {
            console.log('Updating bars for timeframe:', bar.timeframe);
            this.updateHistoricalBarsForTimeframe(bar);
            
            // Update stop loss values only for selected timeframe using API
            if (bar.timeframe === expectedTimeframe) {
              this.autoUpdateStopLossValuesFromAPI();
            }
          }
          
          // Always update ATR for all timeframes
          if (indicator?.trendIndicators?.atr14) {
            const timeframeKey = bar?.timeframe?.replace('min', 'm') || this.selectedTimeframe;
            console.log('Updating ATR for timeframe:', timeframeKey, 'value:', indicator.trendIndicators.atr14);
            this.atrByTimeframe[timeframeKey] = parseFloat(indicator.trendIndicators.atr14);
            
            // Update current ATR if it matches selected timeframe
            if (timeframeKey === this.selectedTimeframe) {
              this.currentATR = parseFloat(indicator.trendIndicators.atr14);
              this.autoUpdateATRStopLossValues();
            }
          }
        } else {
          // Handle Redis pub/sub messages directly
          const { bar, indicator } = msg;
          
          if (bar) {
            // Convert timeframe format (1min -> 1m, 2min -> 2m, etc.)
            const expectedTimeframe = this.selectedTimeframe.replace('m', 'min');
            
            console.log('Redis Bar timeframe:', bar.timeframe, 'Expected:', expectedTimeframe);
            
            // Update current price from selected timeframe only
            if (bar.timeframe === expectedTimeframe) {
              console.log('Updating current price with Redis data:', bar);
              this.currentPrice = bar.close;
            }
            
            // Always update bars for all timeframes
            console.log('Updating bars for timeframe (Redis):', bar.timeframe);
            this.updateHistoricalBarsForTimeframe(bar);
            
            // Update stop loss values only for selected timeframe using API
            if (bar.timeframe === expectedTimeframe) {
              this.autoUpdateStopLossValuesFromAPI();
            }
          }
          
          if (indicator?.trendIndicators?.atr14) {
            const timeframeKey = bar?.timeframe?.replace('min', 'm') || this.selectedTimeframe;
            console.log('Updating ATR for timeframe (Redis):', timeframeKey, 'value:', indicator.trendIndicators.atr14);
            this.atrByTimeframe[timeframeKey] = parseFloat(indicator.trendIndicators.atr14);
            
            // Update current ATR if it matches selected timeframe
            if (timeframeKey === this.selectedTimeframe) {
              this.currentATR = parseFloat(indicator.trendIndicators.atr14);
              this.autoUpdateATRStopLossValues();
            }
          }
        }
      };
      
      this.ohlcWs.onopen = () => {
        console.log('OHLC WebSocket connected for OrderEntryPanel');
      };
      
      this.ohlcWs.onerror = (error) => {
        console.error('OHLC WebSocket error:', error);
      };
    },
    loadStopLossATR(atrMultiple) {
      // Simply set the stop loss from existing ATR calculations
      if (this.atrStopLossValues[atrMultiple] && this.atrStopLossValues[atrMultiple][this.side]) {
        this.stopLoss = this.atrStopLossValues[atrMultiple][this.side];
        console.log(` Set ATR stop loss to: ${this.stopLoss} for ${atrMultiple}x ATR, side: ${this.side}`);
      } else {
        console.warn(` No ATR stop loss value available for ${atrMultiple}x ATR, side: ${this.side}`);
      }
    },
    async setTimeframe(timeframe) {
      this.selectedTimeframe = timeframe;
      
      // Update current data from the selected timeframe
      this.historicalBars = this.historicalBarsByTimeframe[timeframe] || [];
      this.currentATR = this.atrByTimeframe[timeframe];
      
      console.log('Timeframe changed to:', timeframe);
      console.log('Historical bars for this timeframe:', this.historicalBars.length);
      console.log('ATR for this timeframe:', this.currentATR);
      
      // Clear ATR stop loss values when switching timeframes without ATR data
      if (!this.currentATR) {
        this.atrStopLossValues = {};
        console.log('Cleared ATR stop loss values - no ATR data for timeframe');
      }
      
      // Load initial data from API if we don't have enough bars
      if (this.historicalBars.length < 15) {
        await this.loadInitialCandles();
      }
      
      // Update stop loss values for new timeframe using API
      this.autoUpdateStopLossValuesFromAPI();
      if (this.currentATR) {
        this.autoUpdateATRStopLossValues();
      }
    },
    updateHistoricalBars(bar) {
      console.log('Adding bar to historical data:', bar);
      this.historicalBars.push(bar);
      // Keep only last 20 bars for efficiency
      if (this.historicalBars.length > 20) {
        this.historicalBars = this.historicalBars.slice(-20);
      }
      console.log('Total historical bars:', this.historicalBars.length);
    },
    async loadInitialCandles() {
      try {
        const timeframeBE = this.selectedTimeframe.replace('m', 'min');
        const response = await fetch(
          `http://${window.location.hostname}:8000/api/indicators/ohlc/${this.symbol}?timeframe=${timeframeBE}&limit=20`
        );
        
        if (!response.ok) {
          console.warn('Failed to load initial candles:', response.statusText);
          return;
        }
        
        const data = await response.json();
        console.log('Loaded initial candles:', data);
        
        if (data.success && data.candles) {
          // Update historical bars for this timeframe
          this.historicalBarsByTimeframe[this.selectedTimeframe] = data.candles.map(candle => ({
            open: candle.open,
            high: candle.high,
            low: candle.low,
            close: candle.close,
            volume: candle.volume,
            timeframe: candle.timeframe,
            startTimestamp: candle.timestamp
          }));
          
          this.historicalBars = this.historicalBarsByTimeframe[this.selectedTimeframe];
          
          // Update current price from most recent candle
          if (data.candles.length > 0) {
            this.currentPrice = data.candles[data.candles.length - 1].close;
          }
          
          // Update ATR if available in indicators
          if (data.indicators?.trendIndicators?.atr14) {
            this.atrByTimeframe[this.selectedTimeframe] = parseFloat(data.indicators.trendIndicators.atr14);
            this.currentATR = this.atrByTimeframe[this.selectedTimeframe];
          }
          
          console.log('Updated historical bars:', this.historicalBars.length);
          console.log('Updated ATR:', this.currentATR);
          
          // Recalculate stop loss values using API
          this.autoUpdateStopLossValuesFromAPI();
          if (this.currentATR) {
            this.autoUpdateATRStopLossValues();
          }
        }
      } catch (error) {
        console.error('Error loading initial candles:', error);
      }
    },
    updateHistoricalBarsForTimeframe(bar) {
      const timeframeKey = bar.timeframe?.replace('min', 'm');
      if (!timeframeKey || !this.historicalBarsByTimeframe[timeframeKey]) {
        return; // Unknown timeframe
      }
      
      console.log('Adding bar to historical data for timeframe:', timeframeKey, bar);
      this.historicalBarsByTimeframe[timeframeKey].push(bar);
      
      // Keep only last 20 bars for efficiency
      if (this.historicalBarsByTimeframe[timeframeKey].length > 20) {
        this.historicalBarsByTimeframe[timeframeKey] = this.historicalBarsByTimeframe[timeframeKey].slice(-20);
      }
      
      // Update current timeframe's historicalBars if it matches
      if (timeframeKey === this.selectedTimeframe) {
        this.historicalBars = this.historicalBarsByTimeframe[timeframeKey];
      }
      
      console.log('Total historical bars for', timeframeKey + ':', this.historicalBarsByTimeframe[timeframeKey].length);
    },
    async autoUpdateStopLossValuesFromAPI() {
      try {
        const timeframeBE = this.selectedTimeframe.replace('m', 'min');
        const apiUrl = `http://${window.location.hostname}:8000/api/indicators/ohlc/${this.symbol}?timeframe=${timeframeBE}&limit=15`;
        
        const response = await fetch(apiUrl);
        if (!response.ok) return;
        
        const data = await response.json();
        
        if (data.success && data.candles && data.candles.length >= 5) {
          const candles = data.candles;
          const candleCounts = [5, 10, 15];
          
          candleCounts.forEach(count => {
            if (candles.length >= count) {
              const recentCandles = candles.slice(-count);
              const buyStopLoss = Number((Math.min(...recentCandles.map(c => c.low)) - 0.03).toFixed(2));
              const sellStopLoss = Number((Math.max(...recentCandles.map(c => c.high)) + 0.03).toFixed(2));
              
              this.stopLossValues = {
                ...this.stopLossValues,
                [count]: {
                  Buy: buyStopLoss,
                  SellShort: sellStopLoss
                }
              };
            }
          });
        }
      } catch (error) {
        console.error('Error in autoUpdateStopLossValuesFromAPI:', error);
      }
    },
    autoUpdateStopLossValues() {
      console.log('autoUpdateStopLossValues called, bars:', this.historicalBars.length);
      
      // Get completed candles from selected timeframe
      const selectedTimeframeBars = this.historicalBars || [];
      
      // Get 1-minute candles (already being fetched)
      const oneMinuteBars = this.historicalBarsByTimeframe['1m'] || [];
      
      console.log(`Selected timeframe (${this.selectedTimeframe}) bars:`, selectedTimeframeBars.length);
      console.log('Available 1-minute bars:', oneMinuteBars.length);
      
      // Combine both datasets for more granular stop loss calculation
      const combinedBars = [...selectedTimeframeBars, ...oneMinuteBars];
      
      if (combinedBars.length < 5) {
        console.log('Not enough combined bars for stop loss calculation');
        return;
      }
      
      // Calculate stop loss values for 5, 10, 15 candles using combined data
      const candleCounts = [5, 10, 15];
      
      candleCounts.forEach(count => {
        if (combinedBars.length >= count) {
          const recentBars = combinedBars.slice(-count);
          const buyStopLoss = Number((Math.min(...recentBars.map(b => b.low)) - 0.03).toFixed(2));
          const sellStopLoss = Number((Math.max(...recentBars.map(b => b.high)) + 0.03).toFixed(2));
          
          this.stopLossValues[count] = {
            Buy: buyStopLoss,
            SellShort: sellStopLoss
          };
          
          console.log(`Updated stop loss ${count} using ${recentBars.length} combined bars:`, this.stopLossValues[count]);
        }
      });
    },
    autoUpdateATRStopLossValues() {
      if (!this.currentATR || !this.currentPrice) return;
      
      const atr = this.currentATR;
      const currentPrice = this.currentPrice;
      const atrMultiples = [1, 2, 2.5];
      
      atrMultiples.forEach(multiple => {
        const buyStopLoss = Number((currentPrice - (atr * multiple)).toFixed(2));
        const sellStopLoss = Number((currentPrice + (atr * multiple)).toFixed(2));
        
        if (!this.atrStopLossValues[multiple]) {
          this.atrStopLossValues[multiple] = {};
        }
        this.atrStopLossValues[multiple].Buy = buyStopLoss;
        this.atrStopLossValues[multiple].SellShort = sellStopLoss;
      });
    },
    startAutoRefresh() {
      // Schedule refresh to run 2 seconds after every minute
      const scheduleNextRefresh = () => {
        const now = new Date();
        const currentSeconds = now.getSeconds();
        const currentMilliseconds = now.getMilliseconds();
        
        // Calculate milliseconds until 2 seconds past the next minute
        let millisecondsUntilTarget;
        if (currentSeconds < 2) {
          // If we're before :02, wait until :02 of current minute
          millisecondsUntilTarget = (2 - currentSeconds) * 1000 - currentMilliseconds;
        } else {
          // If we're after :02, wait until :02 of next minute
          millisecondsUntilTarget = (62 - currentSeconds) * 1000 - currentMilliseconds;
        }
        
        console.log(` Next refresh scheduled in ${Math.round(millisecondsUntilTarget/1000)}s (at ${new Date(Date.now() + millisecondsUntilTarget).toLocaleTimeString()})`);
        
        this.autoRefreshInterval = setTimeout(() => {
          const refreshTime = new Date().toLocaleTimeString();
          console.log(` Auto-refresh triggered at ${refreshTime} (2s after minute)`);
          
          this.autoUpdateStopLossValuesFromAPI();
          if (this.currentATR) {
            this.autoUpdateATRStopLossValues();
          }
          
          // Schedule the next refresh (60 seconds from now)
          this.autoRefreshInterval = setInterval(() => {
            const intervalRefreshTime = new Date().toLocaleTimeString();
            console.log(` Auto-refresh triggered at ${intervalRefreshTime} (2s after minute)`);
            
            this.autoUpdateStopLossValuesFromAPI();
            if (this.currentATR) {
              this.autoUpdateATRStopLossValues();
            }
          }, 60000); // Every 60 seconds after the first aligned refresh
          
        }, millisecondsUntilTarget);
      };
      
      scheduleNextRefresh();
      console.log(' Started auto-refresh synchronized to 2 seconds after each minute');
    },
    stopAutoRefresh() {
      if (this.autoRefreshInterval) {
        clearInterval(this.autoRefreshInterval);
        this.autoRefreshInterval = null;
        console.log(' Stopped auto-refresh');
      }
    },
    async useLocalHigh() {
      try {
        // Get fresh candle data for analysis
        const timeframeBE = this.selectedTimeframe.replace('m', 'min');
        const response = await fetch(
          `http://${window.location.hostname}:8000/api/indicators/ohlc/${this.symbol}?timeframe=${timeframeBE}&limit=${Math.max(this.lookbackPeriod, 15)}`
        );
        
        if (!response.ok) {
          alert('Failed to fetch candle data for local high analysis');
          return;
        }
        
        const data = await response.json();
        
        if (data.success && data.candles && data.candles.length >= this.lookbackPeriod) {
          const recentCandles = data.candles.slice(-this.lookbackPeriod);
          const localHigh = Math.max(...recentCandles.map(c => c.high));
          
          // Add small buffer above the high for breakout trigger
          this.triggerPrice = Number((localHigh + 0.01).toFixed(2));
          
          this.showStatusMessage(`Set trigger to local high: $${this.triggerPrice} (${this.lookbackPeriod} candles)`, 'success');
        } else {
          alert(`Insufficient candle data. Need at least ${this.lookbackPeriod} candles.`);
        }
      } catch (error) {
        console.error('Error finding local high:', error);
        alert('Error analyzing local high levels');
      }
    },
    async useLocalLow() {
      try {
        // Get fresh candle data for analysis
        const timeframeBE = this.selectedTimeframe.replace('m', 'min');
        const response = await fetch(
          `http://${window.location.hostname}:8000/api/indicators/ohlc/${this.symbol}?timeframe=${timeframeBE}&limit=${Math.max(this.lookbackPeriod, 15)}`
        );

        if (!response.ok) {
          alert('Failed to fetch candle data for local low analysis');
          return;
        }

        const data = await response.json();

        if (data.success && data.candles && data.candles.length >= this.lookbackPeriod) {
          const recentCandles = data.candles.slice(-this.lookbackPeriod);
          const localLow = Math.min(...recentCandles.map(c => c.low));

          // Subtract small buffer below the low for breakout trigger
          this.triggerPrice = Number((localLow - 0.01).toFixed(2));

          this.showStatusMessage(`Set trigger to local low: $${this.triggerPrice} (${this.lookbackPeriod} candles)`, 'success');
        } else {
          alert(`Insufficient candle data. Need at least ${this.lookbackPeriod} candles.`);
        }
      } catch (error) {
        console.error('Error finding local low:', error);
        alert('Error analyzing local low levels');
      }
    },

    loadSignal(signal) {
      console.log('Loading signal into order entry panel:', signal);

      // Set price
      if (signal.entry_price) {
        this.price = Number(signal.entry_price);
      }

      // Set stop loss
      if (signal.stop_loss) {
        this.stopLoss = Number(signal.stop_loss);
      }

      // Set side based on direction
      const direction = (signal.direction || signal.manipulation_direction || '').toUpperCase();
      if (direction === 'LONG' || direction === 'BUY') {
        this.side = 'Buy';
      } else if (direction === 'SHORT' || direction === 'SELL' || direction === 'SELLSHORT') {
        this.side = 'SellShort';
      }
    }
  },
  async mounted() {
    // Temporarily disable OHLC WebSocket to test connection leak
    this.initOhlcWebSocket();
    // Load initial candles for the default timeframe
    await this.loadInitialCandles();
    // Start auto-refresh as backup to WebSocket triggers
    this.startAutoRefresh();

    // Start timer to update currentTime for reactive freshness checks
    this.freshnessCheckTimer = setInterval(() => {
      this.currentTime = Date.now();
    }, 500); // Update every 500ms

    // Add cleanup on page refresh/close
    this.handleBeforeUnload = () => {
      if (this.ohlcWs && this.ohlcWs.readyState === WebSocket.OPEN) {
        console.log('Closing OHLC WebSocket on page unload');
        this.ohlcWs.close();
      }
    };
    window.addEventListener('beforeunload', this.handleBeforeUnload);
  },
  beforeUnmount() {
    if (this.ohlcWs) {
      this.ohlcWs.close();
    }
    this.stopAutoRefresh();

    // Stop freshness check timer
    if (this.freshnessCheckTimer) {
      clearInterval(this.freshnessCheckTimer);
      this.freshnessCheckTimer = null;
    }

    // Remove the beforeunload listener
    if (this.handleBeforeUnload) {
      window.removeEventListener('beforeunload', this.handleBeforeUnload);
    }
  }
};
</script>

