// src/router/index.js
import { createRouter, createWebHistory } from 'vue-router';
import WatchList from '@/components/WatchList.vue';
import WatchListOld from '@/components/WatchListOld.vue';
import SingleTicker from '@/components/SingleTicker.vue';
import SignalsView from '@/components/SignalsView.vue';

const routes = [
  {
    path: '/',
    name: 'WatchList',
    component: WatchList,
  },
  {
    path: '/watchlist',
    name: 'WatchListOld',
    component: WatchListOld,
  },
  {
    path: '/ticker/:symbol',
    name: 'SingleTicker',
    component: SingleTicker,
  },
  {
    path: '/signals',
    name: 'Signals',
    component: SignalsView,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
