<script>
export default {
  name: "IndicatorsPanel",
  props: {
    symbol: {
      type: String,
      required: true
    },
    position: {
      type: String,
      default: 'LONG',
      validator: value => ['LONG', 'SHORT', 'NEUTRAL'].includes(value)
    }
  },
  data() {
    return {
      ws: null,
      indicatorData: {},
      lastUpdate: null,
      periods: ['1min', '2min', '3min', '5min', '15min'],
      isMounted: false,
      showingTooltip: null, // { content, x, y }
      tooltipTimeout: null
    };
  },
  computed: {
    tooltipStyle() {
      if (!this.showingTooltip) return {};
      // Center horizontally on screen, position above click point
      const centerX = window.innerWidth / 2;
      const topY = Math.max(20, this.showingTooltip.y - 20);
      return {
        left: `${centerX}px`,
        top: `${topY}px`
      };
    }
  },
  watch: {
    position(newPosition, oldPosition) {
      console.log('IndicatorsPanel position changed from', oldPosition, 'to', newPosition);
    }
  },
  mounted() {
    this.isMounted = true;
    console.log('IndicatorsPanel mounted with position:', this.position);
    this.fetchIndicators();
    this.connectOHLCWebSocket();
    
    // Add cleanup on page refresh/close
    this.handleBeforeUnload = () => {
      if (this.ws && this.ws.readyState === WebSocket.OPEN) {
        this.ws.close();
      }
    };
    window.addEventListener('beforeunload', this.handleBeforeUnload);
    
    // Add global click listener to hide tooltip when clicking outside
    this.handleClickOutside = (event) => {
      if (this.showingTooltip && !event.target.closest('.custom-tooltip') && !event.target.closest('.indicator-signal') && !event.target.closest('.timeframe-label')) {
        this.hideTooltip();
      }
    };
    document.addEventListener('click', this.handleClickOutside);
  },
  beforeUnmount() {
    this.isMounted = false;
    if (this.ws) this.ws.close();
    
    // Remove the beforeunload listener
    if (this.handleBeforeUnload) {
      window.removeEventListener('beforeunload', this.handleBeforeUnload);
    }
    
    // Remove the click outside listener
    if (this.handleClickOutside) {
      document.removeEventListener('click', this.handleClickOutside);
    }
    
    // Clear any pending tooltip timeout
    if (this.tooltipTimeout) {
      clearTimeout(this.tooltipTimeout);
    }
  },
  methods: {
    async fetchIndicators() {
      // Fetch indicators for all periods
      try {
        const promises = this.periods.map(async (period) => {
          const response = await fetch(
            `http://${window.location.hostname}:8000/api/indicators/ohlc/${this.symbol}?timeframe=${period}&limit=1`
          );
          if (response.ok) {
            const data = await response.json();
            return { period, data: data.indicators };
          }
          return { period, data: null };
        });
        
        const results = await Promise.all(promises);
        
        // Organize data by period
        this.indicatorData = {};
        this.indicatorData[this.symbol] = { periods: {} };
        
        results.forEach(({ period, data }) => {
          this.indicatorData[this.symbol].periods[period] = {
            indicators: data
          };
        });
        
        this.lastUpdate = new Date();
        console.log('Fetched indicators for', this.symbol, 'at', this.lastUpdate.toISOString());
        console.log('Raw indicator data:', this.indicatorData);
        
        // Debug: Log the raw API responses for comparison with TradingView
        results.forEach(({ period, data }) => {
          if (data) {
            console.log(`${this.symbol} ${period} indicators:`, {
              timestamp: data.timestamp,
              lastUpdated: data.last_updated,
              rsi: data.oscillators?.rsi14?.value,
              cmf: data.volumeIndicators?.cmf21,
              macd: data.oscillators?.macd12_26_9_calc,
              emas: data.emas
            });
          }
        });
      } catch (e) {
        console.error('Error fetching indicators:', e);
      }
    },

    connectOHLCWebSocket() {
      // Connect to OHLC WebSocket to get real-time indicator updates
      const wsUrl = `ws://${window.location.hostname}:8000/ws/ohlc?symbol=${this.symbol}`;
      this.ws = new WebSocket(wsUrl);
      
      this.ws.onopen = () => {
        console.log('OHLC WebSocket connected for indicators');
      };
      
      this.ws.onmessage = (evt) => {
        try {
          const msg = JSON.parse(evt.data);
          if (msg.type === 'OHLC_UPDATE' && msg.indicator) {
            // Update indicators for the current symbol's period
            if (!this.indicatorData[this.symbol]) {
              this.indicatorData[this.symbol] = { periods: {} };
            }
            
            // Determine period from bar data (assuming timeframe is in the message)
            const period = msg.bar?.timeframe || '1min';
            
            if (!this.indicatorData[this.symbol].periods[period]) {
              this.indicatorData[this.symbol].periods[period] = {};
            }
            
            this.indicatorData[this.symbol].periods[period].indicators = msg.indicator;
            this.lastUpdate = new Date();
          }
        } catch (e) {
          console.error('Error processing OHLC message:', e);
        }
      };
      
      this.ws.onerror = (err) => {
        console.error('OHLC WebSocket error:', err);
      };
      
      this.ws.onclose = () => {
        if (this.isMounted) {
          setTimeout(() => this.connectOHLCWebSocket(), 5000);
        }
      };
    },

    // CMF (Chaikin Money Flow) Methods
    getCMFValue(period) {
      const data = this.getIndicatorData(period);
      return data?.volumeIndicators?.cmf21 ? parseFloat(data.volumeIndicators.cmf21).toFixed(3) : '--';
    },
    
    getCMFSignal(period) {
      const data = this.getIndicatorData(period);
      if (!data?.volumeIndicators?.cmf21) return 'MISSING';
      
      const cmf = parseFloat(data.volumeIndicators.cmf21);
      if (cmf > 0.1) return 'STRONG BUY';
      if (cmf > 0.05) return 'BUY';
      if (cmf < -0.1) return 'STRONG SELL';
      if (cmf < -0.05) return 'SELL';
      return 'NEUTRAL';
    },

    // RSI Methods
    getRSIValue(period) {
      const data = this.getIndicatorData(period);
      return data?.oscillators?.rsi14?.value ? parseFloat(data.oscillators.rsi14.value).toFixed(1) : '--';
    },
    
    getRSISignal(period) {
      const data = this.getIndicatorData(period);
      if (!data?.oscillators?.rsi14?.value) return 'MISSING';
      
      const rsi = parseFloat(data.oscillators.rsi14.value);
      if (rsi > 80) return 'SELL';
      if (rsi > 70) return 'WEAK SELL';
      if (rsi < 20) return 'BUY';
      if (rsi < 30) return 'WEAK BUY';
      return 'NEUTRAL';
    },

    // MACD Methods
    getMACDValue(period) {
      const data = this.getIndicatorData(period);
      if (!data?.oscillators?.macd12_26_9_calc?.macd || !data?.oscillators?.macd12_26_9_calc?.signal) return '--';
      const macd = parseFloat(data.oscillators.macd12_26_9_calc.macd);
      const signal = parseFloat(data.oscillators.macd12_26_9_calc.signal);
      const diff = macd - signal;
      return diff.toFixed(3);
    },
    
    getMACDSignal(period) {
      const data = this.getIndicatorData(period);
      if (!data?.oscillators?.macd12_26_9_calc?.macd || !data?.oscillators?.macd12_26_9_calc?.signal) return 'MISSING';
      
      const line = parseFloat(data.oscillators.macd12_26_9_calc.macd);
      const signal = parseFloat(data.oscillators.macd12_26_9_calc.signal);
      const histogram = line - signal;
      
      if (line > signal && histogram > 0) return 'BUY';
      if (line < signal && histogram < 0) return 'SELL';
      return 'NEUTRAL';
    },

    // Stochastics Methods
    getStochValue(period) {
      const data = this.getIndicatorData(period);
      if (!data?.oscillators?.stochastic14_3?.k || !data?.oscillators?.stochastic14_3?.d) return '--';
      const k = parseFloat(data.oscillators.stochastic14_3.k);
      const d = parseFloat(data.oscillators.stochastic14_3.d);
      return `${k.toFixed(1)}/${d.toFixed(1)}`;
    },
    
    getStochSignal(period) {
      const data = this.getIndicatorData(period);
      if (!data?.oscillators?.stochastic14_3?.k || !data?.oscillators?.stochastic14_3?.d) return 'MISSING';
      
      const k = parseFloat(data.oscillators.stochastic14_3.k);
      const d = parseFloat(data.oscillators.stochastic14_3.d);
      
      if (k > 80 && d > 80) return 'SELL';
      if (k < 20 && d < 20) return 'BUY';
      if (k > d && k < 80) return 'WEAK BUY';
      if (k < d && k > 20) return 'WEAK SELL';
      return 'NEUTRAL';
    },

    // EMA Crossover Methods
    getEMA921Value(period) {
      const data = this.getIndicatorData(period);
      if (!data?.emas?.ema9 || !data?.emas?.ema21) return '--';
      const ema9 = parseFloat(data.emas.ema9);
      const ema21 = parseFloat(data.emas.ema21);
      return `${ema9.toFixed(2)}/${ema21.toFixed(2)}`;
    },
    
    getEMA921Signal(period) {
      const data = this.getIndicatorData(period);
      if (!data?.emas?.ema9 || !data?.emas?.ema21) return 'MISSING';
      
      const ema9 = parseFloat(data.emas.ema9);
      const ema21 = parseFloat(data.emas.ema21);
      return ema9 > ema21 ? 'BUY' : 'SELL';
    },

    getEMA2150Value(period) {
      const data = this.getIndicatorData(period);
      if (!data?.emas?.ema21 || !data?.emas?.ema50) return '--';
      const ema21 = parseFloat(data.emas.ema21);
      const ema50 = parseFloat(data.emas.ema50);
      return `${ema21.toFixed(2)}/${ema50.toFixed(2)}`;
    },
    
    getEMA2150Signal(period) {
      const data = this.getIndicatorData(period);
      if (!data?.emas?.ema21 || !data?.emas?.ema50) return 'MISSING';
      
      const ema21 = parseFloat(data.emas.ema21);
      const ema50 = parseFloat(data.emas.ema50);
      return ema21 > ema50 ? 'BUY' : 'SELL';
    },

    getEMA50200Value(period) {
      const data = this.getIndicatorData(period);
      if (!data?.emas?.ema50 || !data?.emas?.ema200) return '--';
      const ema50 = parseFloat(data.emas.ema50);
      const ema200 = parseFloat(data.emas.ema200);
      return `${ema50.toFixed(2)}/${ema200.toFixed(2)}`;
    },
    
    getEMA50200Signal(period) {
      const data = this.getIndicatorData(period);
      if (!data?.emas?.ema50 || !data?.emas?.ema200) return 'MISSING';
      
      const ema50 = parseFloat(data.emas.ema50);
      const ema200 = parseFloat(data.emas.ema200);
      return ema50 > ema200 ? 'BUY' : 'SELL';
    },

    // Helper Methods
    getIndicatorData(period) {
      const data = this.indicatorData[this.symbol]?.periods?.[period]?.indicators;
      return data;
    },

    getSignalClass(signal) {
      const positionAwareSignal = this.getPositionAwareSignal(signal);
      
      if (this.position === 'SHORT') {
        // For shorts: invert the colors (good=red, bad=green)
        switch (positionAwareSignal) {
          case 'STRONG BUY': return 'short-entry'; // Red for short entry
          case 'BUY': 
          case 'WEAK BUY': return 'short-entry';
          case 'STRONG SELL': return 'short-exit'; // Green for short exit
          case 'SELL':
          case 'WEAK SELL': return 'short-exit';
          case 'NEUTRAL': return 'neutral';
          case 'MISSING':
          default: return 'missing';
        }
      } else {
        // LONG position (default colors)
        switch (positionAwareSignal) {
          case 'STRONG BUY': return 'strong-buy';
          case 'BUY': 
          case 'WEAK BUY': return 'buy';
          case 'STRONG SELL': return 'strong-sell';
          case 'SELL':
          case 'WEAK SELL': return 'sell';
          case 'NEUTRAL': return 'neutral';
          case 'MISSING':
          default: return 'missing';
        }
      }
    },

    getSignalIcon(signal) {
      const positionAwareSignal = this.getPositionAwareSignal(signal);
      
      if (this.position === 'SHORT') {
        switch (positionAwareSignal) {
          case 'STRONG BUY':
          case 'BUY': 
          case 'WEAK BUY': 
            return '';  // Red down arrow for short entry
          case 'STRONG SELL':
          case 'SELL':
          case 'WEAK SELL': 
            return '';  // Green cross for short exit (exit signal/no-entry)
          case 'NEUTRAL':
            return '=';  // Yellow equals
          case 'MISSING':
          default: 
            return '';   // Empty gray box
        }
      } else {
        // LONG position (default)
        switch (positionAwareSignal) {
          case 'STRONG BUY':
          case 'BUY': 
          case 'WEAK BUY': 
            return '';  // Green up arrow for long buy
          case 'STRONG SELL':
          case 'SELL':
          case 'WEAK SELL': 
            return '';  // Red cross for long sell
          case 'NEUTRAL':
            return '=';  // Yellow equals
          case 'MISSING':
          default: 
            return '';   // Empty gray box
        }
      }
    },

    getOverallSignal(period) {
      const signals = [
        this.getCMFSignal(period),
        this.getRSISignal(period),
        this.getMACDSignal(period),
        this.getEMA921Signal(period),
        this.getEMA2150Signal(period)
      ];

      const buySignals = signals.filter(s => s.includes('BUY')).length;
      const sellSignals = signals.filter(s => s.includes('SELL')).length;

      if (buySignals >= 4) return 'STRONG BUY';
      if (buySignals >= 3) return 'BUY';
      if (sellSignals >= 4) return 'STRONG SELL';
      if (sellSignals >= 3) return 'SELL';
      return 'NEUTRAL';
    },

    formatTime(date) {
      return date ? date.toLocaleTimeString() : '--';
    },

    // Invert signal logic for short positions
    getPositionAwareSignal(signal) {
      if (this.position === 'SHORT') {
        switch (signal) {
          case 'STRONG BUY': return 'STRONG SELL';
          case 'BUY': return 'SELL';
          case 'WEAK BUY': return 'WEAK SELL';
          case 'STRONG SELL': return 'STRONG BUY';
          case 'SELL': return 'BUY';
          case 'WEAK SELL': return 'WEAK BUY';
          case 'NEUTRAL': return 'NEUTRAL';
          default: return signal;
        }
      }
      return signal;
    },

    getColoredSignal(signal) {
      const positionAwareSignal = this.getPositionAwareSignal(signal);
      const displayText = this.position === 'SHORT' ? this.getShortDisplayText(positionAwareSignal) : positionAwareSignal;
      
      switch (positionAwareSignal) {
        case 'STRONG BUY':
        case 'BUY': 
        case 'WEAK BUY': 
          return `<span style="color: #28a745; font-weight: bold; text-shadow: 0 1px 2px rgba(0,0,0,0.3);"> ${displayText}</span>`;
        case 'STRONG SELL':
        case 'SELL':
        case 'WEAK SELL': 
          return `<span style="color: #ff4757; font-weight: bold; text-shadow: 0 1px 2px rgba(0,0,0,0.3);"> ${displayText}</span>`;
        case 'NEUTRAL':
          return `<span style="color: #ffc107; font-weight: bold; text-shadow: 0 1px 2px rgba(0,0,0,0.3);">= ${displayText}</span>`;
        case 'MISSING':
        default: 
          return `<span style="color: #6c757d; font-weight: bold;"> NO DATA</span>`;
      }
    },

    getShortDisplayText(signal) {
      switch (signal) {
        case 'STRONG BUY': return 'STRONG ENTRY';
        case 'BUY': return 'ENTRY';
        case 'WEAK BUY': return 'WEAK ENTRY';
        case 'STRONG SELL': return 'STRONG EXIT';
        case 'SELL': return 'EXIT';
        case 'WEAK SELL': return 'WEAK EXIT';
        default: return signal;
      }
    },

    showTooltip(event, indicator, period) {
      // Clear any existing timeout
      if (this.tooltipTimeout) {
        clearTimeout(this.tooltipTimeout);
      }
      
      // Get the tooltip content with more detailed information
      let content;
      const data = this.getIndicatorData(period);
      
      switch (indicator) {
        case 'CMF':
          content = `<div style="text-align: center;"><strong style="color: #64b5f6; font-size: 1.3rem;">CMF-21 ${period} (${this.position}):</strong> <span style="color: #fff; font-weight: bold;">${this.getCMFValue(period)}</span> | ${this.getColoredSignal(this.getCMFSignal(period))} | Raw: <span style="color: #b0bec5;">${data?.volumeIndicators?.cmf21 || 'N/A'}</span></div>`;
          break;
        case 'RSI':
          content = `<div style="text-align: center;"><strong style="color: #64b5f6; font-size: 1.3rem;">RSI-14 ${period} (${this.position}):</strong> <span style="color: #fff; font-weight: bold;">${this.getRSIValue(period)}</span> | ${this.getColoredSignal(this.getRSISignal(period))} | Raw: <span style="color: #b0bec5;">${data?.oscillators?.rsi14?.value || 'N/A'}</span></div>`;
          break;
        case 'MACD': {
          const macd = data?.oscillators?.macd12_26_9_calc;
          content = `<div style="text-align: center;"><strong style="color: #64b5f6; font-size: 1.3rem;">MACD(12,26,9) ${period} (${this.position}):</strong> Line: <span style="color: #fff; font-weight: bold;">${macd?.macd ? parseFloat(macd.macd).toFixed(3) : 'N/A'}</span> | Signal: <span style="color: #fff; font-weight: bold;">${macd?.signal ? parseFloat(macd.signal).toFixed(3) : 'N/A'}</span> | Histogram: <span style="color: #fff; font-weight: bold;">${this.getMACDValue(period)}</span> | ${this.getColoredSignal(this.getMACDSignal(period))}</div>`;
          break;
        }
        case 'STOCH': {
          const stoch = data?.oscillators?.stochastic14_3;
          content = `<div style="text-align: center;"><strong style="color: #64b5f6; font-size: 1.3rem;">Stochastic(14,3) ${period} (${this.position}):</strong> %K: <span style="color: #fff; font-weight: bold;">${stoch?.k ? parseFloat(stoch.k).toFixed(1) : 'N/A'}</span> | %D: <span style="color: #fff; font-weight: bold;">${stoch?.d ? parseFloat(stoch.d).toFixed(1) : 'N/A'}</span> | ${this.getColoredSignal(this.getStochSignal(period))}</div>`;
          break;
        }
        case 'EMA921': {
          const ema9 = data?.emas?.ema9 ? parseFloat(data.emas.ema9) : null;
          const ema21 = data?.emas?.ema21 ? parseFloat(data.emas.ema21) : null;
          const crossover = ema9 && ema21 ? (ema9 > ema21 ? '<span style="color: #28a745; font-weight: bold;"> YES</span>' : '<span style="color: #ff4757; font-weight: bold;"> NO</span>') : '<span style="color: #6c757d;">N/A</span>';
          content = `<div style="text-align: center;"><strong style="color: #64b5f6; font-size: 1.3rem;">EMA 9/21 ${period} (${this.position}):</strong> EMA9: <span style="color: #fff; font-weight: bold;">${ema9 ? ema9.toFixed(2) : 'N/A'}</span> | EMA21: <span style="color: #fff; font-weight: bold;">${ema21 ? ema21.toFixed(2) : 'N/A'}</span> | 9>21: ${crossover} | ${this.getColoredSignal(this.getEMA921Signal(period))}</div>`;
          break;
        }
        case 'EMA2150': {
          const ema21val = data?.emas?.ema21 ? parseFloat(data.emas.ema21) : null;
          const ema50 = data?.emas?.ema50 ? parseFloat(data.emas.ema50) : null;
          const crossover = ema21val && ema50 ? (ema21val > ema50 ? '<span style="color: #28a745; font-weight: bold;"> YES</span>' : '<span style="color: #ff4757; font-weight: bold;"> NO</span>') : '<span style="color: #6c757d;">N/A</span>';
          content = `<div style="text-align: center;"><strong style="color: #64b5f6; font-size: 1.3rem;">EMA 21/50 ${period} (${this.position}):</strong> EMA21: <span style="color: #fff; font-weight: bold;">${ema21val ? ema21val.toFixed(2) : 'N/A'}</span> | EMA50: <span style="color: #fff; font-weight: bold;">${ema50 ? ema50.toFixed(2) : 'N/A'}</span> | 21>50: ${crossover} | ${this.getColoredSignal(this.getEMA2150Signal(period))}</div>`;
          break;
        }
        case 'EMA50200': {
          const ema50val = data?.emas?.ema50 ? parseFloat(data.emas.ema50) : null;
          const ema200 = data?.emas?.ema200 ? parseFloat(data.emas.ema200) : null;
          const crossover = ema50val && ema200 ? (ema50val > ema200 ? '<span style="color: #28a745; font-weight: bold;"> YES</span>' : '<span style="color: #ff4757; font-weight: bold;"> NO</span>') : '<span style="color: #6c757d;">N/A</span>';
          content = `<div style="text-align: center;"><strong style="color: #64b5f6; font-size: 1.3rem;">EMA 50/200 ${period} (${this.position}):</strong> EMA50: <span style="color: #fff; font-weight: bold;">${ema50val ? ema50val.toFixed(2) : 'N/A'}</span> | EMA200: <span style="color: #fff; font-weight: bold;">${ema200 ? ema200.toFixed(2) : 'N/A'}</span> | 50>200: ${crossover} | ${this.getColoredSignal(this.getEMA50200Signal(period))}</div>`;
          break;
        }
        default:
          content = 'No data';
      }
      
      // Calculate position near the clicked element
      const rect = event.target.getBoundingClientRect();
      const x = rect.left + rect.width / 2;
      const y = rect.top - 10;
      
      // Show custom tooltip
      this.showingTooltip = { content, x, y };
      
      // Auto-hide after 5 seconds
      this.tooltipTimeout = setTimeout(() => {
        this.showingTooltip = null;
      }, 5000);
    },

    showAllIndicators(event, period) {
      // Build comprehensive tooltip with all indicator values for the timeframe
      const data = this.getIndicatorData(period);
      const macd = data?.oscillators?.macd12_26_9_calc;
      const stoch = data?.oscillators?.stochastic14_3;
      const emas = data?.emas;
      
      const content = `
        <div class="comprehensive-tooltip">
          <h4 style="color: #64b5f6; font-size: 1.4rem; text-align: center; margin-bottom: 20px;">${period.toUpperCase()} INDICATORS (${this.position})</h4>
          
          <div class="indicator-section">
            <strong style="color: #64b5f6;">CMF-21:</strong> <span style="color: #fff; font-weight: bold;">${this.getCMFValue(period)}</span> | ${this.getColoredSignal(this.getCMFSignal(period))}
          </div>
          
          <div class="indicator-section">
            <strong style="color: #64b5f6;">RSI-14:</strong> <span style="color: #fff; font-weight: bold;">${this.getRSIValue(period)}</span> | ${this.getColoredSignal(this.getRSISignal(period))}
          </div>
          
          <div class="indicator-section">
            <strong style="color: #64b5f6;">MACD(12,26,9):</strong> Line: <span style="color: #fff; font-weight: bold;">${macd?.macd ? parseFloat(macd.macd).toFixed(3) : 'N/A'}</span> | Signal: <span style="color: #fff; font-weight: bold;">${macd?.signal ? parseFloat(macd.signal).toFixed(3) : 'N/A'}</span> | Histogram: <span style="color: #fff; font-weight: bold;">${this.getMACDValue(period)}</span> | ${this.getColoredSignal(this.getMACDSignal(period))}
          </div>
          
          <div class="indicator-section">
            <strong style="color: #64b5f6;">Stochastic(14,3):</strong> %K: <span style="color: #fff; font-weight: bold;">${stoch?.k ? parseFloat(stoch.k).toFixed(1) : 'N/A'}</span> | %D: <span style="color: #fff; font-weight: bold;">${stoch?.d ? parseFloat(stoch.d).toFixed(1) : 'N/A'}</span> | ${this.getColoredSignal(this.getStochSignal(period))}
          </div>
          
          <div class="indicator-section">
            <strong style="color: #64b5f6;">EMA 9/21:</strong> <span style="color: #fff; font-weight: bold;">${emas?.ema9 ? parseFloat(emas.ema9).toFixed(2) : 'N/A'}/${emas?.ema21 ? parseFloat(emas.ema21).toFixed(2) : 'N/A'}</span> | 9>21: ${emas?.ema9 && emas?.ema21 ? (parseFloat(emas.ema9) > parseFloat(emas.ema21) ? '<span style="color: #28a745;"> YES</span>' : '<span style="color: #ff4757;"> NO</span>') : '<span style="color: #6c757d;">N/A</span>'} | ${this.getColoredSignal(this.getEMA921Signal(period))}
          </div>
          
          <div class="indicator-section">
            <strong style="color: #64b5f6;">EMA 21/50:</strong> <span style="color: #fff; font-weight: bold;">${emas?.ema21 ? parseFloat(emas.ema21).toFixed(2) : 'N/A'}/${emas?.ema50 ? parseFloat(emas.ema50).toFixed(2) : 'N/A'}</span> | 21>50: ${emas?.ema21 && emas?.ema50 ? (parseFloat(emas.ema21) > parseFloat(emas.ema50) ? '<span style="color: #28a745;"> YES</span>' : '<span style="color: #ff4757;"> NO</span>') : '<span style="color: #6c757d;">N/A</span>'} | ${this.getColoredSignal(this.getEMA2150Signal(period))}
          </div>
          
          <div class="indicator-section">
            <strong style="color: #64b5f6;">EMA 50/200:</strong> <span style="color: #fff; font-weight: bold;">${emas?.ema50 ? parseFloat(emas.ema50).toFixed(2) : 'N/A'}/${emas?.ema200 ? parseFloat(emas.ema200).toFixed(2) : 'N/A'}</span> | 50>200: ${emas?.ema50 && emas?.ema200 ? (parseFloat(emas.ema50) > parseFloat(emas.ema200) ? '<span style="color: #28a745;"> YES</span>' : '<span style="color: #ff4757;"> NO</span>') : '<span style="color: #6c757d;">N/A</span>'} | ${this.getColoredSignal(this.getEMA50200Signal(period))}
          </div>
        </div>
      `;
      
      // Calculate position near the clicked element  
      const rect = event.target.getBoundingClientRect();
      const x = rect.left + rect.width / 2;
      const y = rect.top - 10;
      
      // Show custom tooltip
      this.showingTooltip = { content, x, y };
      
      // Auto-hide after 10 seconds for comprehensive view
      this.tooltipTimeout = setTimeout(() => {
        this.showingTooltip = null;
      }, 10000);
    },

    hideTooltip() {
      this.showingTooltip = null;
      if (this.tooltipTimeout) {
        clearTimeout(this.tooltipTimeout);
      }
    }
  }
};
</script>

