<template>
  <div class="signals-panel">
    <div class="header-container">
      <h1>Trading Signals</h1>

      <!-- Alert Sound Toggle -->
      <div class="alert-sound-toggle">
        <input
          type="checkbox"
          id="alert-sound"
          v-model="alertSoundEnabled"
          @change="saveAlertSoundPreference"
        />
        <label for="alert-sound">Alert sound</label>
        <select v-model="selectedChime" @change="saveChimePreference" class="chime-select">
          <option value="beep">Beep</option>
          <option value="ding">Ding</option>
          <option value="chime">Chime</option>
          <option value="bell">Bell</option>
          <option value="notification">Notification</option>
          <option value="sonar">Sonar</option>
          <option value="ascending">Ascending</option>
          <option value="descending">Descending</option>
          <option value="double-beep">Double Beep</option>
          <option value="triple-tone">Triple Tone</option>
          <option value="urgent">Urgent</option>
          <option value="subtle">Subtle</option>
        </select>
        <button @click="playAlertSound" class="test-sound-btn" title="Test alert sound"></button>
      </div>

      <!-- Window Status Display -->
      <div class="windows-display">
        <div class="window-pair">
          <div class="window-box" @click="focusWindow(1)">{{ windowTickers[1] || '-' }}</div>
          <div class="window-box" @click="focusWindow(2)">{{ windowTickers[2] || '-' }}</div>
        </div>
        <div class="window-pair">
          <div class="window-box" @click="focusWindow(3)">{{ windowTickers[3] || '-' }}</div>
          <div class="window-box" @click="focusWindow(4)">{{ windowTickers[4] || '-' }}</div>
        </div>
        <div class="window-pair">
          <div class="window-box" @click="focusWindow(5)">{{ windowTickers[5] || '-' }}</div>
          <div class="window-box" @click="focusWindow(6)">{{ windowTickers[6] || '-' }}</div>
        </div>
      </div>

      <button type="button" @click="bringAllWindowsForward" class="bring-forward-btn" title="Bring all windows to front">
         Bring All Forward
      </button>

      <div class="nav-links">
        <router-link to="/" class="nav-link"> WatchList</router-link>
      </div>
      <div class="date-selector">
        <label for="signal-date">Date:</label>
        <input
          type="date"
          id="signal-date"
          v-model="selectedDate"
          @change="fetchSignals"
          class="date-input"
        />
        <button @click="refreshSignals" class="refresh-btn" title="Refresh signals">
          
        </button>
      </div>
    </div>

    <div v-if="error" class="error">{{ error }}</div>

    <div v-else-if="signals.length === 0" class="no-data">
      No signals found for {{ selectedDate }}
    </div>

    <div v-else class="table-container">
      <table>
        <thead>
          <tr>
            <th>Strategy</th>
            <th>Ticker</th>
            <th>Time</th>
            <th>Dir</th>
            <th class="price-header">Entry</th>
            <th class="volume-header">Volume</th>
            <th>Opening Range</th>
            <th class="retraced-header">Retraced</th>
            <th class="send-to-header">Send To</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(signal, index) in signals"
            :key="index"
            :class="getRowClass(signal)"
          >
            <td class="strategy">
              <span :class="getStrategyClass(signal)">{{ formatStrategy(signal) }}</span>
            </td>
            <td class="ticker">{{ signal.ticker || '-' }}</td>
            <td class="time">{{ formatTime(signal) }}</td>
            <td class="direction">{{ formatDirection(signal) }}</td>
            <td class="price">{{ formatPrice(signal.entry_price) }}</td>
            <td class="volume">{{ formatVolume(signal) }}</td>
            <td class="range" v-html="formatRange(signal)"></td>
            <td class="retraced">{{ formatRetraced(signal) }}</td>
            <td class="window-buttons">
              <span class="button-pair">
                <button
                  @click="sendToWindow(1, signal, index)"
                  :class="['window-btn', { 'active': isSignalInWindow(index, 1) }]"
                  title="Send to Window 1"
                >1</button>
                <button
                  @click="sendToWindow(2, signal, index)"
                  :class="['window-btn', { 'active': isSignalInWindow(index, 2) }]"
                  title="Send to Window 2"
                >2</button>
              </span>
              <span class="button-pair">
                <button
                  @click="sendToWindow(3, signal, index)"
                  :class="['window-btn', { 'active': isSignalInWindow(index, 3) }]"
                  title="Send to Window 3"
                >3</button>
                <button
                  @click="sendToWindow(4, signal, index)"
                  :class="['window-btn', { 'active': isSignalInWindow(index, 4) }]"
                  title="Send to Window 4"
                >4</button>
              </span>
              <span class="button-pair">
                <button
                  @click="sendToWindow(5, signal, index)"
                  :class="['window-btn', { 'active': isSignalInWindow(index, 5) }]"
                  title="Send to Window 5"
                >5</button>
                <button
                  @click="sendToWindow(6, signal, index)"
                  :class="['window-btn', { 'active': isSignalInWindow(index, 6) }]"
                  title="Send to Window 6"
                >6</button>
              </span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="connection-status">
      <span :class="connectionStatusClass">
        {{ connectionStatus }}
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: "SignalsView",
  data() {
    return {
      signals: [],
      selectedDate: '',
      loading: false,
      error: null,
      connectionStatus: 'Ready',
      autoRefreshInterval: null,
      windowStatusInterval: null,
      windowSignalMap: {}, // Track which signal is in each window: { windowNumber: signalIndex }
      windowTickers: {}, // Track which ticker is loaded in each window: { windowNumber: ticker }
      alertSoundEnabled: true, // Alert sound enabled by default
      lastSignalCount: 0, // Track number of signals to detect new ones
      selectedChime: 'beep', // Default chime type
      audioContext: null, // Shared AudioContext instance
      isInitialLoad: true, // Track if this is the first load to avoid alert sound on page load
      audioKeepAliveInterval: null, // Interval to keep AudioContext active
    };
  },
  computed: {
    connectionStatusClass() {
      return {
        'status-ready': this.connectionStatus === 'Ready',
        'status-loading': this.connectionStatus === 'Loading...',
        'status-error': this.connectionStatus.includes('Error')
      };
    }
  },
  mounted() {
    // Set to today's date by default
    this.selectedDate = this.getTodaysDate();

    // Load alert sound preference from localStorage
    const savedPreference = localStorage.getItem('alertSoundEnabled');
    if (savedPreference !== null) {
      this.alertSoundEnabled = savedPreference === 'true';
    }

    // Load chime preference
    const savedChime = localStorage.getItem('selectedChime');
    if (savedChime) {
      this.selectedChime = savedChime;
    }

    this.fetchSignals(); // Initial load

    // Auto-refresh every 15 seconds for today's date
    this.setupAutoRefresh();

    // Start polling window status every 2 seconds
    this.windowStatusInterval = setInterval(() => {
      this.syncWindowHighlights();
    }, 2000);

    // Start AudioContext keep-alive (play silent tone every 30 seconds to prevent suspension)
    this.audioKeepAliveInterval = setInterval(() => {
      this.keepAudioContextAlive();
    }, 30000); // Every 30 seconds
  },
  beforeUnmount() {
    if (this.autoRefreshInterval) {
      clearInterval(this.autoRefreshInterval);
    }
    if (this.windowStatusInterval) {
      clearInterval(this.windowStatusInterval);
    }
    if (this.audioKeepAliveInterval) {
      clearInterval(this.audioKeepAliveInterval);
    }
  },
  methods: {
    getTodaysDate() {
      const today = new Date();
      const year = today.getFullYear();
      const month = String(today.getMonth() + 1).padStart(2, '0');
      const day = String(today.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },

    setupAutoRefresh() {
      // Calculate time until next refresh mark (:03, :08, :13, :18, :23, :28, :33, :38, :43, :48, :53, :58)
      const now = new Date();
      const currentSeconds = now.getSeconds();
      const currentMilliseconds = now.getMilliseconds();

      // Calculate next refresh time (every 5 seconds starting at :03)
      const refreshMarks = [3, 8, 13, 18, 23, 28, 33, 38, 43, 48, 53, 58];
      let nextMark = refreshMarks.find(mark => mark > currentSeconds);
      if (!nextMark) {
        // If past :58, next mark is :03 of next minute
        nextMark = 3 + 60;
      }
      const msUntilNextMark = (nextMark - currentSeconds) * 1000 - currentMilliseconds;

      // Set initial timeout to align with the refresh mark
      setTimeout(() => {
        // First refresh at the mark
        if (this.selectedDate === this.getTodaysDate()) {
          this.fetchSignals({ background: true }); // Background refresh - no loading UI jitter, but alert sound enabled
        }

        // Then set up interval to refresh every 5 seconds
        this.autoRefreshInterval = setInterval(() => {
          if (this.selectedDate === this.getTodaysDate()) {
            this.fetchSignals({ background: true }); // Background refresh - no loading UI jitter, but alert sound enabled
          }
        }, 5000); // 5 seconds
      }, msUntilNextMark);
    },

    async fetchSignals(options = {}) {
      // Options: silent (no alert sound)
      const silent = options.silent ?? options === true; // Support old boolean API

      this.error = null;

      try {
        const response = await fetch(
          `http://${window.location.hostname}:8000/api/signals?date=${this.selectedDate}`
        );

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const newSignals = await response.json();

        // Check for new signals and play alert sound if enabled
        // Don't play sound on initial page load
        if (!silent && !this.isInitialLoad && this.alertSoundEnabled && newSignals.length > this.lastSignalCount) {
          console.log(' New signal detected - playing alert sound');
          this.playAlertSound();
        }

        this.signals = newSignals;
        this.lastSignalCount = this.signals.length;
        this.isInitialLoad = false; // Mark that initial load is complete

        // Sync window highlights by checking what's loaded in each window
        await this.syncWindowHighlights();

        this.connectionStatus = 'Ready';
      } catch (err) {
        console.error('Error fetching signals:', err);
        this.error = `Failed to load signals: ${err.message}`;
        this.connectionStatus = `Error: ${err.message}`;
      }
    },

    async syncWindowHighlights() {
      // Query all 6 windows to see what ticker they're displaying
      const windowQueries = [];
      for (let i = 1; i <= 6; i++) {
        const port = 10000 + i;

        // Create timeout promise
        const timeoutPromise = new Promise((_, reject) =>
          setTimeout(() => reject(new Error('Timeout')), 1000)
        );

        const fetchPromise = fetch(`http://localhost:${port}/status`, {
          method: 'GET'
        })
          .then(res => {
            if (res.ok) {
              return res.json();
            }
            throw new Error('Not OK');
          })
          .then(data => ({ windowNumber: i, symbol: data.symbol, success: true }));

        windowQueries.push(
          Promise.race([fetchPromise, timeoutPromise])
            .catch(() => {
              // Silently handle - windows might not be running
              return { windowNumber: i, symbol: null, success: false };
            })
        );
      }

      const windowStatuses = await Promise.all(windowQueries);

      // Build new highlight map and ticker map (don't clear the old one until we have new data)
      const newWindowSignalMap = {};
      const newWindowTickers = {};

      // For each window, find the matching signal by ticker and highlight it
      windowStatuses.forEach(({ windowNumber, symbol, success }) => {
        if (success && symbol) {
          // Store the ticker for the window display
          newWindowTickers[windowNumber] = symbol;

          // Find the signal index that matches this symbol
          const signalIndex = this.signals.findIndex(
            sig => sig.ticker && sig.ticker.toUpperCase() === symbol.toUpperCase()
          );

          if (signalIndex !== -1) {
            newWindowSignalMap[windowNumber] = signalIndex;
          }
        } else if (!success) {
          // Window not responding, keep existing highlight and ticker if any
          if (this.windowSignalMap[windowNumber] !== undefined) {
            newWindowSignalMap[windowNumber] = this.windowSignalMap[windowNumber];
          }
          if (this.windowTickers[windowNumber]) {
            newWindowTickers[windowNumber] = this.windowTickers[windowNumber];
          }
        }
      });

      // Only update if we got some successful responses
      const successfulResponses = windowStatuses.filter(s => s.success).length;
      if (successfulResponses > 0) {
        this.windowSignalMap = newWindowSignalMap;
        this.windowTickers = newWindowTickers;
        // console.log('Synced window highlights:', this.windowSignalMap, `(${successfulResponses}/6 windows responded)`);
      } else {
        // console.log('No windows responded, keeping existing highlights');
      }
    },

    refreshSignals() {
      this.fetchSignals(); // Manual refresh
    },

    formatTime(signal) {
      // For SMB scalp signals, use timestamp field directly (format: "HH:MM:SS")
      if (signal.timestamp && typeof signal.timestamp === 'string' && signal.timestamp.includes(':')) {
        // Extract HH:MM from "HH:MM:SS" format
        const parts = signal.timestamp.split(':');
        if (parts.length >= 2) {
          return `${parts[0]}:${parts[1]}`;
        }
      }

      // For manipulation reversal signals, extract from event_timestamp array
      const eventTimestamp = signal.raw_details?.event_timestamp;
      if (eventTimestamp && Array.isArray(eventTimestamp)) {
        // event_timestamp is [year, month, day, hour, minute]
        const [, , , hour, minute] = eventTimestamp;
        if (hour !== undefined && minute !== undefined) {
          const hourStr = String(hour).padStart(2, '0');
          const minuteStr = String(minute).padStart(2, '0');
          return `${hourStr}:${minuteStr}`;
        }
      }

      return '-';
    },

    formatPrice(price) {
      if (price === null || price === undefined) {
        return '-';
      }
      return parseFloat(price).toFixed(2);
    },

    formatStrategy(signal) {
      // Display strategy type
      return signal.strategy || 'Unknown';
    },

    formatDirection(signal) {
      return signal.direction || signal.manipulation_direction || '-';
    },

    formatVolume(signal) {
      // Extract volume data from signal (check raw_details first, then top level)
      const reversalVolPerMin = signal.raw_details?.reversal_volume_per_min || signal.reversal_volume_per_min;
      const sessionVolume = signal.raw_details?.session_volume || signal.session_volume;

      if (!reversalVolPerMin || !sessionVolume) {
        // Fallback to old format if new fields not available
        const volume = signal.raw_details?.volume || signal.volume;
        if (volume === null || volume === undefined) {
          return '-';
        }
        const volumeInK = (volume / 1000).toFixed(1);
        return `${volumeInK}K`;
      }

      // Format as "reversalVol / sessionVol" - always in K (thousands)
      const formatVolK = (vol) => {
        return `${(vol / 1000).toFixed(0)}K`;
      };

      return `${formatVolK(reversalVolPerMin)} / ${formatVolK(sessionVolume)}`;
    },

    formatRange(signal) {
      const high = signal.opening_range_high;
      const low = signal.opening_range_low;
      const diff = signal.opening_range_high - signal.opening_range_low;

      if (high && low) {
        return `<strong>${diff.toFixed(2)}</strong> (${parseFloat(low).toFixed(2)} - ${parseFloat(high).toFixed(2)})`;
      }
      return '-';
    },

    formatRetraced(signal) {
      const coverage = signal.range_coverage_percent;
      const interval = signal.timeframe_interval || signal.raw_details?.timeframe_interval;

      if (!coverage) return '-';

      // Format: "37.6%, 1m"
      let result = coverage;
      if (interval) {
        // Convert interval to short format (e.g., "MINUTE_1" -> "1m")
        const intervalShort = this.formatInterval(interval);
        result += `, ${intervalShort}`;
      }

      return result;
    },

    formatInterval(interval) {
      // Convert interval enum/string to short format
      if (typeof interval === 'string') {
        if (interval.includes('SECOND_10')) return '10s';
        if (interval.includes('MINUTE_1')) return '1m';
        if (interval.includes('MINUTE_2')) return '2m';
        if (interval.includes('MINUTE_3')) return '3m';
        if (interval.includes('MINUTE_5')) return '5m';
        if (interval.includes('MINUTE_15')) return '15m';
      }
      return interval || '';
    },

    getRowClass(signal) {
      const signalType = signal.signal_type;
      const direction = (signal.direction || '').toUpperCase();

      // Row background based on direction (LONG = green, SHORT = red)
      if (direction === 'LONG') {
        return 'row-long';
      } else if (direction === 'SHORT') {
        return 'row-short';
      }

      // Manipulation reversal watch signals
      if (signalType === 'MANIPULATION_REVERSAL_WATCH') {
        return 'row-watch';
      }

      return '';
    },

    getStrategyClass(signal) {
      const strategy = signal.strategy;

      // Return strategy-specific class for the strategy cell
      if (strategy === 'ManipulationReversal') {
        return 'strategy-manipulation';
      } else if (strategy === 'Back$ide') {
        return 'strategy-backside';
      } else if (strategy === 'Hitchhiker') {
        return 'strategy-hitchhiker';
      } else if (strategy === 'Rubberband') {
        return 'strategy-rubberband';
      } else if (strategy === 'Second Chance') {
        return 'strategy-secondchance';
      } else if (strategy === 'Fashionably Late') {
        return 'strategy-fashionablylate';
      }

      return '';
    },

    async sendToWindow(windowNumber, signal, signalIndex) {
      const port = 10000 + windowNumber;
      try {
        const response = await fetch(`http://localhost:${port}/signal`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(signal),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const result = await response.json();
        console.log(`Signal sent to window ${windowNumber}:`, result);

        // Track that this window is now showing this signal (only one signal per window)
        this.windowSignalMap[windowNumber] = signalIndex;

        // Focus the window
        try {
          await fetch(`http://localhost:${port}/focus`, {
            method: 'POST',
          });
        } catch (focusErr) {
          console.warn(`Could not focus window ${windowNumber}:`, focusErr);
        }
      } catch (err) {
        console.error(`Failed to send signal to window ${windowNumber}:`, err);
        alert(`Failed to send signal to window ${windowNumber}: ${err.message}`);
      }
    },

    isSignalInWindow(signalIndex, windowNumber) {
      return this.windowSignalMap[windowNumber] === signalIndex;
    },

    async focusWindow(windowNumber) {
      const port = 10000 + windowNumber;
      try {
        await fetch(`http://localhost:${port}/focus`, {
          method: 'POST',
        });
        console.log(`Focused window ${windowNumber}`);
      } catch (err) {
        console.warn(`Could not focus window ${windowNumber}:`, err);
      }
    },

    async bringAllWindowsForward() {
      // Call /focus endpoint on all 6 window servers (ports 10001-10006)
      const focusPromises = [];

      for (let windowNum = 1; windowNum <= 6; windowNum++) {
        const port = 10000 + windowNum;
        const promise = fetch(`http://localhost:${port}/focus`, {
          method: 'POST'
        }).catch(err => {
          console.error(`Failed to focus window ${windowNum}:`, err);
        });
        focusPromises.push(promise);
      }

      // Wait for all focus requests to complete
      await Promise.all(focusPromises);
      console.log('Brought all windows to front');
    },

    async playAlertSound() {
      try {
        // Initialize AudioContext on first call (must be from user gesture)
        if (!this.audioContext) {
          this.initAudioContext();
        }

        // If still no context, user hasn't interacted yet
        if (!this.audioContext) {
          console.log('AudioContext not initialized - waiting for user interaction');
          return;
        }

        // Resume AudioContext if it's suspended (Chrome autoplay policy)
        if (this.audioContext.state === 'suspended') {
          console.log('Resuming suspended AudioContext...');
          await this.audioContext.resume();
          console.log('AudioContext resumed, state:', this.audioContext.state);
        }

        switch (this.selectedChime) {
          case 'beep':
            this.playBeep(this.audioContext);
            break;
          case 'ding':
            this.playDing(this.audioContext);
            break;
          case 'chime':
            this.playChime(this.audioContext);
            break;
          case 'bell':
            this.playBell(this.audioContext);
            break;
          case 'notification':
            this.playNotification(this.audioContext);
            break;
          case 'sonar':
            this.playSonar(this.audioContext);
            break;
          case 'ascending':
            this.playAscending(this.audioContext);
            break;
          case 'descending':
            this.playDescending(this.audioContext);
            break;
          case 'double-beep':
            this.playDoubleBeep(this.audioContext);
            break;
          case 'triple-tone':
            this.playTripleTone(this.audioContext);
            break;
          case 'urgent':
            this.playUrgent(this.audioContext);
            break;
          case 'subtle':
            this.playSubtle(this.audioContext);
            break;
        }

        console.log('Alert sound played:', this.selectedChime);
      } catch (error) {
        console.error('Error playing alert sound:', error);
      }
    },

    playBeep(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 800;
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.3, audioContext.currentTime + 0.01);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.2);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.2);
    },

    playDing(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 1200;
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.4, audioContext.currentTime + 0.005);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.3);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.3);
    },

    playChime(audioContext) {
      // Two-tone chime
      [1000, 800].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.15);
        oscillator.frequency.value = freq;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.3, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.25);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.25);
      });
    },

    playBell(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 1500;
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.5, audioContext.currentTime + 0.002);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.5);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.5);
    },

    playNotification(audioContext) {
      // Three quick beeps
      [0, 0.15, 0.3].forEach((delay) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + delay;
        oscillator.frequency.value = 900;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.25, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.1);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.1);
      });
    },

    playSonar(audioContext) {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 600;
      oscillator.type = 'sine';

      // Frequency sweep (sonar ping)
      oscillator.frequency.setValueAtTime(600, audioContext.currentTime);
      oscillator.frequency.exponentialRampToValueAtTime(300, audioContext.currentTime + 0.3);

      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.4, audioContext.currentTime + 0.01);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.3);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.3);
    },

    playAscending(audioContext) {
      // Rising tone sequence
      [600, 800, 1000].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.1);
        oscillator.frequency.value = freq;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.25, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.12);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.12);
      });
    },

    playDescending(audioContext) {
      // Falling tone sequence
      [1200, 900, 700].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.1);
        oscillator.frequency.value = freq;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.25, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.12);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.12);
      });
    },

    playDoubleBeep(audioContext) {
      // Two quick identical beeps
      [0, 0.2].forEach((delay) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + delay;
        oscillator.frequency.value = 850;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.3, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.15);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.15);
      });
    },

    playTripleTone(audioContext) {
      // Three different tones (musical)
      [523, 659, 784].forEach((freq, i) => { // C, E, G notes
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.15);
        oscillator.frequency.value = freq;
        oscillator.type = 'sine';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.25, startTime + 0.01);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.2);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.2);
      });
    },

    playUrgent(audioContext) {
      // Fast alternating high tones
      [1400, 1600, 1400, 1600].forEach((freq, i) => {
        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();
        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        const startTime = audioContext.currentTime + (i * 0.08);
        oscillator.frequency.value = freq;
        oscillator.type = 'square';
        gainNode.gain.setValueAtTime(0, startTime);
        gainNode.gain.linearRampToValueAtTime(0.2, startTime + 0.005);
        gainNode.gain.exponentialRampToValueAtTime(0.01, startTime + 0.07);

        oscillator.start(startTime);
        oscillator.stop(startTime + 0.07);
      });
    },

    playSubtle(audioContext) {
      // Very soft, short tone
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 700;
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0, audioContext.currentTime);
      gainNode.gain.linearRampToValueAtTime(0.15, audioContext.currentTime + 0.01);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.15);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.15);
    },

    initAudioContext() {
      // Initialize AudioContext on user interaction to satisfy browser autoplay policy
      if (!this.audioContext) {
        this.audioContext = new (window.AudioContext || window.webkitAudioContext)();
        console.log('AudioContext initialized on user interaction');
      }
    },

    keepAudioContextAlive() {
      // Play a silent tone periodically to keep AudioContext from being suspended
      if (!this.audioContext || this.audioContext.state === 'closed') {
        return;
      }

      // Resume if suspended
      if (this.audioContext.state === 'suspended') {
        this.audioContext.resume().catch(err => {
          console.warn('Failed to resume AudioContext in keep-alive:', err);
        });
      }

      // Play an extremely quiet, short tone (inaudible but keeps context active)
      try {
        const oscillator = this.audioContext.createOscillator();
        const gainNode = this.audioContext.createGain();

        oscillator.connect(gainNode);
        gainNode.connect(this.audioContext.destination);

        oscillator.frequency.value = 20000; // Very high frequency (inaudible to most humans)
        gainNode.gain.value = 0.001; // Extremely quiet

        oscillator.start(this.audioContext.currentTime);
        oscillator.stop(this.audioContext.currentTime + 0.001); // 1ms duration
      } catch (err) {
        console.warn('Failed to play keep-alive tone:', err);
      }
    },

    saveAlertSoundPreference() {
      this.initAudioContext(); // Initialize on first interaction
      localStorage.setItem('alertSoundEnabled', this.alertSoundEnabled.toString());
      console.log('Alert sound preference saved:', this.alertSoundEnabled);
    },

    saveChimePreference() {
      this.initAudioContext(); // Initialize on first interaction
      localStorage.setItem('selectedChime', this.selectedChime);
      console.log('Chime preference saved:', this.selectedChime);
    }
  }
};
</script>

