<template>
  <div class="level2-container" :class="{ dimmed: !isInWatchlist }" :style="{ backgroundColor: freshnessColor }">
    <div>
      <h4 class="header-row">
        Level 2: {{ symbol }}
        <span class="heartbeat">
          Time: {{ lastHeartbeatFormatted }} ({{ heartbeatElapsed }}s old)
        </span>
        <span class="watchlist-inline-controls">
          <button v-if="!isInWatchlist" @click="addToWatchlist">
            Add to Level 2 watchlist
          </button>
          <button v-else @click="removeFromWatchlist">
            Remove from Level 2 watchlist
          </button>
          <span v-if="watchlistCount > 10" class="warning">
            Too many symbols for Level 2 data ({{ watchlistCount }} vs. 10 allowed)
          </span>
        </span>
      </h4>
    </div>
    <div class="order-book">
      <!-- Bids column -->
      <div class="bids">
        <div class="order-row" style="font-weight: bold;">
          <span class="order-maker">Bid</span>
          <span class="order-size"></span>
          <span class="order-price"></span>
        </div>        
        <div 
          v-for="(bid, index) in groupedBids" 
          :key="'bid-' + index" 
          class="order-row" 
          :style="getRowStyle(bid.groupIndex, bid.Size)"
        >
          <span class="order-maker">{{ bid.Name }}</span>
          <span class="order-size">{{ formatSize(bid.Size) }}</span>
          <!-- pass $event to selectPrice to detect Option key -->
          <span class="order-price" @click="selectPrice(bid.Price, $event)">
            {{ formatPrice(bid.Price) }}
          </span>
        </div>
      </div>
      <!-- Asks column -->
      <div class="asks">
        <div class="order-row" style="font-weight: bold;">
          <span class="order-maker">Ask</span>
          <span class="order-size"></span>
          <span class="order-price"></span>
        </div>        <div 
          v-for="(ask, index) in groupedAsks" 
          :key="'ask-' + index" 
          class="order-row" 
          :style="getRowStyle(ask.groupIndex, ask.Size)"
        >
          <!-- pass $event to selectPrice to detect Option key -->
          <span class="order-price" @click="selectPrice(ask.Price, $event)" style="text-align: left;">
            {{ formatPrice(ask.Price) }}
          </span>
          <span class="order-size">{{ formatSize(ask.Size) }}</span>
          <span class="order-maker" style="text-align: right;">{{ ask.Name }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "OrderBookLevel2",
  props: {
    symbol: {
      type: String,
      default: "TSLA"
    },
    // property to receive the latest quote with bid/ask info.
    latestQuote: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      ws: null,
      depthData: null,
      lastHeartbeat: 0,
      currentTime: Date.now(),
      // Timer variable used for both time updates and reconnection.
      // (We now use it for reconnection with setTimeout.)
      timer: null,
      reconnectThreshold: 100000000,
      levelColors: ["#d0f0d0", "#ff9999", "#ffff99", "#72bcd4"],
      isInWatchlist: false,
      watchlistCount: 0,
      isMounted: false,
      // Level2 data automatically (un)subscribed on page (un)load
      autoSubscribeEnabled: true,
    };
  },
  computed: {
    groupedBids() {
      if (this.depthData && this.depthData.Bids) {
        const currentBid = parseFloat(this.latestQuote?.bidPrice);
        // Sort descending
        const sorted = [...this.depthData.Bids].sort((a, b) => parseFloat(b.Price) - parseFloat(a.Price));
        // Filter out orders that are significantly lower than the current bid (stuck orders)
        const filtered = currentBid
          ? sorted.filter(order => parseFloat(order.Price) <= currentBid * 1.01)
          : sorted;
        let groups = [];
        let currentPrice = null;
        let groupIndex = -1;
        filtered.forEach(order => {
          const price = parseFloat(order.Price).toFixed(2);
          if (price !== currentPrice) {
            groupIndex++;
            currentPrice = price;
          }
          groups.push({ ...order, groupIndex });
        });
        return groups;
      }
      return [];
    },
    groupedAsks() {
      if (this.depthData && this.depthData.Asks) {
        const currentAsk = this.latestQuote?.askPrice;
        // Sort ascending
        const sorted = [...this.depthData.Asks].sort((a, b) => parseFloat(a.Price) - parseFloat(b.Price));
        // Filter out orders that are significantly higher than the current ask (stuck orders)
        const filtered = currentAsk
          ? sorted.filter(order => parseFloat(order.Price) >= currentAsk * 0.99)
          : sorted;
        let groups = [];
        let currentPrice = null;
        let groupIndex = -1;
        filtered.forEach(order => {
          const price = parseFloat(order.Price).toFixed(2);
          if (price !== currentPrice) {
            groupIndex++;
            currentPrice = price;
          }
          groups.push({ ...order, groupIndex });
        });
        return groups;
      }
      return [];
    },
    heartbeatElapsed() {
      if (this.lastHeartbeat > 0) {
        let elapsed = Math.max(0, (this.currentTime - this.lastHeartbeat) / 1000);
        elapsed = Math.floor(elapsed * 2) / 2;
        return elapsed.toFixed(1);
      }
      return "0.0";
    },
    lastHeartbeatFormatted() {
      return this.lastHeartbeat > 0
        ? new Date(this.lastHeartbeat).toLocaleTimeString()
        : "No heartbeat yet";
    },
    freshnessColor() {
      if (this.lastHeartbeat === 0) return "#f0f0f0"; // default gray
      const elapsed = (this.currentTime - this.lastHeartbeat) / 1000;
      if (elapsed > 10) return "#ffcccc"; // red - stale
      if (elapsed > 5) return "#ffff99";  // yellow - aging
      return "#f0f0f0";                   // default gray - fresh
    }
  },
  mounted() {
    this.isMounted = true;
    this.initWebSocket();
    this.timer = setInterval(() => {
      this.currentTime = Date.now();
    }, 500);
    
    // 1) figure out if already in watchlist,
    // 2) then auto-subscribe if not
    this.checkWatchlist().then(() => {
      if (!this.isInWatchlist) {
        this.addToWatchlist();
      }
    });

    window.addEventListener("beforeunload", this._cleanup);
    window.addEventListener("pagehide",    this._cleanup);    
  },
  beforeUnmount() {
    this.isMounted = false;
    if (this.ws) this.ws.close();
    if (this.timeInterval) clearInterval(this.timeInterval);
    if (this.reconnectTimeout) clearTimeout(this.reconnectTimeout);

    // remove handlers when Vue *does* unmount
    window.removeEventListener("beforeunload", this._cleanup);
    window.removeEventListener("pagehide",    this._cleanup);    
  },
  methods: {
    initWebSocket() {
      const wsUrl = `ws://${window.location.hostname}:8000/ws/market_depth?symbol=${this.symbol}`;
      this.ws = new WebSocket(wsUrl);
      this.ws.onopen = () => {
        console.log("WebSocket connected for market_depth:", this.symbol);
        this.lastHeartbeat = Date.now();
        if (this.reconnectTimeout) {
          clearTimeout(this.reconnectTimeout);
          this.reconnectTimeout = null;
        }        
      };
      this.ws.onmessage = (evt) => {
        try {
          this.lastHeartbeat = Date.now();
          const msg = JSON.parse(evt.data);
          // Update depthData if the message is not a heartbeat
          if (msg.Heartbeat === undefined) {
            this.depthData = msg;
          }
        } catch (e) {
          console.error("Error processing market depth message", evt.data, e);
        }
      };
      this.ws.onerror = (err) => {
        console.error("Market depth WebSocket error:", err);
      };
      this.ws.onclose = (evt) => {
        console.log("WebSocket closed: market_depth: ", evt.code, evt.reason);
        if (this.isMounted) {
          this.timer = setTimeout(() => {
            this.reconnectWebSocket();
          }, 5000);
        }
      };
    },
    reconnectWebSocket() {
      if (this.ws) {
        console.log(`reconnectWebSocket(${this.symbol}): Closing websocket.`);
        this.ws.close();
      }
      this.initWebSocket();
    },
    getRowStyle(groupIndex, size) {
      let style = {};
      if (groupIndex < this.levelColors.length) {
        style.backgroundColor = this.levelColors[groupIndex];
      } else {
        style.backgroundColor = "#ffffff";
      }
      if (parseInt(size, 10) >= 1000) {
        style.fontWeight = "bold";
      }
      if (groupIndex === 0) {
        style.backgroundColor = "#00ff00";
      }
      return style;
    },
    formatPrice(price) {
      return parseFloat(price).toFixed(2);
    },
    formatSize(size) {
      const qty = Math.max(parseInt(size, 10) / 100, 1);
      return qty.toFixed(2);
    },
    async checkWatchlist() {
      try {
        const res = await fetch(`http://${window.location.hostname}:8000/api/marketdepth_watchlist/${this.symbol}`);
        if (res.ok) {
          const data = await res.json();
          this.isInWatchlist = data.inWatchlist;
          this.watchlistCount = data.count;
        }
      } catch (e) {
        console.error("Error checking market depth watchlist:", e);
      }
    },
    async addToWatchlist() {
      try {
        const res = await fetch(`http://${window.location.hostname}:8000/api/marketdepth_watchlist/${this.symbol}`, { method: "POST" });
        if (res.ok) {
          const data = await res.json();
          this.isInWatchlist = data.inWatchlist;
          this.watchlistCount = data.count;
        }
      } catch (e) {
        console.error("Error adding to market depth watchlist:", e);
      }
    },
    // centralized unsubscribe logic
    _cleanup() {
      if (this.isInWatchlist) {
        // use keepalive so browser will fire it on unload
        fetch(
          `http://${window.location.hostname}:8000/api/marketdepth_watchlist/${this.symbol}`,
          { method: "DELETE", keepalive: true }
        ).catch(() => {
          /* ignore: best-effort */
        });
      }
    },
    async removeFromWatchlist() {
      try {
        const res = await fetch(`http://${window.location.hostname}:8000/api/marketdepth_watchlist/${this.symbol}`, { method: "DELETE" });
        if (res.ok) {
          const data = await res.json();
          this.isInWatchlist = data.inWatchlist;
          this.watchlistCount = data.count;
        }
      } catch (e) {
        console.error("Error removing from market depth watchlist:", e);
      }
    },
    // this method emits the selected price and side based on the Option (Alt) key.
    selectPrice(price, event) {
      const side = event.altKey ? "SellShort" : "Buy";
      this.$emit("priceSelected", { price: Number(price), side });
    }
  }
};
</script>

<style scoped>
.level2-container {
  font-family: Verdana, Geneva, sans-serif;
  transition: background-color 0.3s ease;
}
.dimmed {
  opacity: 0.5;
}
.header-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: Verdana, Geneva, sans-serif;
  font-size: large;
}
.heartbeat {
  font-size: 0.9em;
  color: #333;
  font-weight: normal;
}
.order-book {
  display: flex;
  justify-content: space-between;
}
.bids, .asks {
  width: 48%;
}
.order-row {
  display: flex;
  justify-content: space-between;
  padding: 0.2rem;
  margin-bottom: 0;
  font-size: 0.9em;
}
.order-maker {
  flex: 0 0 6rem;
}
.order-price, .order-size {
  flex: 0 0 4rem;
  text-align: right;
}
.watchlist-controls {
  margin-top: 0.5rem;
  text-align: center;
}
.watchlist-inline-controls button {
  font-size: 0.9em;
  cursor: pointer;
  margin-left: 1rem;
}
.warning {
  color: red;
  font-size: 0.8em;
  margin-left: 1rem;
}
.already-added {
  font-size: 0.9em;
  color: #333;
  margin-left: 1rem;
}
</style>