<template>
  <div class="tape-container" :style="{ backgroundColor: freshnessColor }">
    <h3 class="header">
      Time &amp; Sales (Tape)
      <span class="elapsed">| {{ elapsedTime }}s old</span>
    </h3>
    <div class="tape-header">
      <span v-if="SHOW_TIME_COLUMN" class="header-time">Time</span>
      <span class="header-price">Price</span>
      <span class="header-size">Size</span>
    </div>
    <div class="tape-list" ref="tapeList">
      <div 
        v-for="(trade, idx) in trades" 
        :key="(trade.tradeET || trade.timestamp) + '-' + trade.price + '-' + trade.tradeSize + '-' + idx" 
        class="trade-row" 
        :style="getTradeStyle(trade)"
      >
        <span v-if="SHOW_TIME_COLUMN" class="trade-time">{{ formatTime(trade.tradeET) }}</span>
        <span class="trade-price">{{ formatPrice(trade.price) }}</span>
        <span class="trade-size">{{ formatTradeSize(trade.tradeSize) }}</span>
      </div>
    </div>
  </div>
</template>

<script>
const SHOW_TIME_COLUMN = false;
export default {
  name: "TimeSalesTape",
  props: {
    trades: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      SHOW_TIME_COLUMN,
      tradeReceivedTime: 0,
      currentTime: Date.now(),
      timer: null,
    };
  },
  computed: {
    elapsedTime() {
      if (this.tradeReceivedTime > 0) {
        let elapsed = Math.max((this.currentTime - this.tradeReceivedTime) / 1000, 0);
        elapsed = Math.floor(elapsed * 2) / 2;
        return elapsed.toFixed(1);
      }
      return "0.0";
    },
    freshnessColor() {
      if (this.tradeReceivedTime === 0) return "#f9f9f9"; // default light gray
      const elapsed = (this.currentTime - this.tradeReceivedTime) / 1000;
      if (elapsed > 10) return "#ffcccc"; // red - stale
      if (elapsed > 5) return "#ffff99";  // yellow - aging
      return "#f9f9f9";                   // default light gray - fresh
    },
  },
  watch: {
    trades: {
      handler(newTrades) {
        if (newTrades && newTrades.length > 0) {
          this.tradeReceivedTime = Date.now();
          this.$nextTick(() => {
            const tapeList = this.$refs.tapeList;
            if (tapeList) {
              tapeList.scrollTop = tapeList.scrollHeight;
            }
          });
        }
      },
      deep: true,
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      this.currentTime = Date.now();
    }, 500);
  },
  beforeUnmount() {
    if (this.timer) clearInterval(this.timer);
  },
  methods: {
    formatTime(timeStr) {
      return timeStr;
    },
    formatPrice(price) {
      return parseFloat(price).toFixed(2);
    },
    formatTradeSize(size) {
      const qty = Math.max(parseInt(size, 10), 1);
      return qty.toFixed(0);
    },
    getTradeStyle(trade) {
      let color = "#000"; // default black/white
      if (trade.tradeSign === 1 || trade.aggressor === "Buyer") {
        color = "#00aa00"; // green for buys
      } else if (trade.tradeSign === -1 || trade.aggressor === "Seller") {
        color = "#cc0000"; // red for sells
      }
      const fontWeight = parseInt(trade.tradeSize, 10) >= 1000 ? "bold" : "normal";
      return { color, fontWeight, padding: "0.2rem", marginBottom: "0.1rem" };
    },
  },
};
</script>

<style scoped>
.tape-container {
  padding: 0.3rem;
  border: 1px solid #ccc;
  box-sizing: border-box;
  transition: background-color 0.3s ease;
}
.header {
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  margin-bottom: 0.5rem;
  font-family: Verdana, Geneva, sans-serif;  
}
.elapsed {
  font-size: 0.9em;
  color: #333;
  font-weight: normal;
  padding-left: 0.25rem;
}
.tape-header {
  display: flex;
  font-size: 1.1em;
  margin-bottom: 0.5rem;
}
.header-time {
  flex: 0 0 5rem;
}
.header-price,
.header-size {
  flex: 0 0 6rem;
  text-align: right;
  font-weight: bold;
}
/* tape-list fills remaining height */
.tape-list {
  height: calc(100% - 3rem); /* adjust based on header heights */
  overflow-y: auto;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.tape-list::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
}
.trade-row {
  display: flex;
  font-family: Verdana, Geneva, sans-serif;
  padding: 0.2rem;
  margin-bottom: 0.1rem;
}
.trade-time {
  flex: 0 0 5rem;
}
.trade-price,
.trade-size {
  flex: 0 0 6rem;
  text-align: right;
}
</style>