<template>
  <div class="news-panel">
    <div class="news-list">
      <span
        v-for="(news, index) in sortedNews"
        :key="index"
        class="news-item"
      >
        <span :class="{'news-date': true, 'recent': isRecent(news)}">
          {{ formatPublishedTime(news.published_utc) }}
        </span>
        <a
          :href="news.article_url"
          target="_blank"
          rel="noopener"
          class="news-link"
        >
          {{ clipTitle(news.title) }}
        </a>
      </span>
    </div>
  </div>
</template>

<script>
const MAX_TITLE_LENGTH = 80; // Maximum characters for news titles

export default {
  name: "NewsPanel",
  props: {
    ticker: {
      type: String,
      default: "PLTR"
    }
  },
  data() {
    return {
      newsItems: []
    };
  },
  computed: {
    // Sort news descending (latest first)
    sortedNews() {
      return [...this.newsItems].sort(
        (a, b) => new Date(b.published_utc) - new Date(a.published_utc)
      );
    }
  },
  mounted() {
    this.fetchNews();
  },
  methods: {
    async fetchNews() {
      const apiKey = "p78rx6L5WW0JQ37zj4Y8J2BypjbLdd0r";
      const url = `https://api.polygon.io/v2/reference/news?ticker=${this.ticker}&order=desc&limit=9&sort=published_utc&apiKey=${apiKey}`;
      try {
        const response = await fetch(url);
        if (!response.ok) {
          throw new Error("News fetch failed");
        }
        const data = await response.json();
        if (data.results) {
          this.newsItems = data.results;
        }
      } catch (e) {
        console.error("Error fetching news:", e);
      }
    },
    clipTitle(title) {
      if (!title) return "";
      return title.length <= MAX_TITLE_LENGTH
        ? title
        : title.substring(0, MAX_TITLE_LENGTH) + "...";
    },
    formatPublishedTime(publishedUtc) {
      if (!publishedUtc) return "";
      // Convert published_utc (UTC) to Eastern Time
      const options = {
        timeZone: "America/New_York",
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit"
      };
      return new Intl.DateTimeFormat("en-US", options).format(new Date(publishedUtc));
    },
    isRecent(news) {
      if (!news || !news.published_utc) return false;
      const publishedTime = new Date(news.published_utc).getTime();
      const oneWeekMs = 7 * 24 * 60 * 60 * 1000;
      return Date.now() - publishedTime < oneWeekMs;
    }
  }
};
</script>

<style scoped>
.news-panel {
  padding: 0.25rem;
  background: #fff;
  font-family: Arial, sans-serif;
}
.news-panel h3 {
  margin-bottom: 0.5rem;
  font-size: 1.2em;
}
/* Limit container height to about three lines */
.news-list {
  max-height: 6em; /* Adjust this value to fit about 3 lines */
  overflow-y: auto;
}
/* Each news item is inline */
.news-item {
  display: inline-block;
  margin-right: 1.5rem;
  white-space: nowrap;
}
.news-date {
  margin-right: 0.5rem;
  font-size: 0.9em;
  color: #666;
}
.recent {
  font-weight: bold;
}
.news-link {
  text-decoration: none;
  color: #337ab7;
}
.news-link:hover {
  text-decoration: underline;
}
</style>
