<template>
    <div class="ticker-info">
      <!-- Line 1: Ticker and Company Name -->
      <div class="line1">
        {{ info.ticker || '-' }}  {{ info.name || '-' }}
      </div>
      <!-- Line 2: Key metrics arranged inline -->
      <div class="line2">
        <div class="sub-line">
          <span class="item">Float: {{ formatHumanReadable(info.float) }}</span>
          <span class="separator">|</span>
          <span class="item" :class="shortInterestClass">
            Sh Int: {{ formatHumanReadable(info.shortInterest) }} ({{ safeShortInterestRatio }})
          </span>
        </div>
        <div class="sub-line">
          <span class="item" :class="daysToCoverClass">
            Days to Cover: {{ safeDaysToCover }}
          </span>
          <span class="separator">|</span>
          <span class="item">
            Market Cap: {{ formatHumanReadable(info.market_cap) }}
          </span>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: "TickerInfo",
    props: {
      info: {
        type: Object,
        default: () => ({})
      }
    },
    computed: {
      // Compute short interest ratio using the float (shares outstanding) rather than market cap.
      shortInterestRatio() {
        const fl = Number(this.info.float) || 0;
        const si = Number(this.info.shortInterest) || 0;
        return fl ? (si / fl) * 100 : 0;
      },
      safeShortInterestRatio() {
        const ratio = this.shortInterestRatio;
        return isNaN(ratio) ? "0.00%" : ratio.toFixed(2) + "%";
      },
      safeDaysToCover() {
        const d = Number(this.info.daysToCover) || 0;
        return d.toFixed(1);
      },
      daysToCoverClass() {
        const days = Number(this.info.daysToCover) || 0;
        if (days < 2) return "green";
        else if (days < 3) return "yellow";
        else return "red";
      },
      shortInterestClass() {
        const ratio = this.shortInterestRatio;
        if (ratio < 2) return "green";
        else if (ratio < 5) return "yellow";
        else return "red";
      }
    },
    methods: {
      formatHumanReadable(num) {
        const n = Number(num);
        if (!n || isNaN(n)) return "-";
        if (n >= 1e9) {
          // Show one decimal place for billions
          return (n / 1e9).toFixed(1) + "B";
        } else if (n >= 1e6) {
          // No decimals for millions
          return Math.round(n / 1e6).toLocaleString() + "M";
        } else if (n >= 1e3) {
          return Math.round(n / 1e3).toLocaleString() + "K";
        } else {
          return Math.round(n).toLocaleString();
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .ticker-info {
    padding: 1rem;
    font-family: Arial, sans-serif;
    background: #fff;
    line-height: 1.4;
    text-align: right; /* Default text-align for the component */
  }
  .line1 {
    font-size: 1.5em;
    font-weight: bold;
    text-align: right;
    margin-bottom: 0.25rem;
  }
  .line2 {
    font-size: 1em;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 0.2em;
  }
  .item {
    margin: 0 0.5rem;
  }
  .separator {
    margin: 0 0.25rem;
    color: #999;
  }
  .green {
    color: green;
  }
  .yellow {
    color: goldenrod;
  }
  .red {
    color: red;
  }
  .sub-line {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  </style>
  