// src/main.js
import { createApp } from 'vue';
import App from './App.vue';
import router from './router'; // Make sure this path is correct

// swallow ResizeObserver loop errors
if (window.ResizeObserver) {
  const RO = window.ResizeObserver;
  window.ResizeObserver = class SilentRO extends RO {
    constructor(callback) {
      super((entries, observer) => {
        try {
          callback(entries, observer);
        } catch (err) {
          // ignore the ResizeObserver loop error
          if (!/ResizeObserver loop/.test(err.message)) {
            throw err;
          }
        }
      });
    }
  };
}

createApp(App)
  .use(router)
  .mount('#app');
