<!-- src/components/QuoteDisplay.vue -->
<template>
  <div class="quote-display" :style="{ backgroundColor: freshnessColor }">
    <strong>Bid/Ask:</strong>
    {{ formatPrice(quote?.bidPrice) }}
    /
    {{ formatPrice(quote?.askPrice) }}
    <!-- Spread with dynamic background -->
    <span
      class="spread"
      :style="{ backgroundColor: spreadColor }"
    >
      ({{ spread }})
    </span>
    <span v-if="quote?.quoteET">
      | <strong>Time:</strong> {{ quote.quoteET }}
    </span>
    <span class="elapsed-timer">
      | Elapsed: {{ elapsedTime }}s
    </span>
  </div>
</template>

<script>
export default {
  name: "QuoteDisplay",
  props: {
    quote: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      quoteReceivedTime: 0,
      currentTime: Date.now(),
      timer: null,
    };
  },
  computed: {
    spread() {
      const b = parseFloat(this.quote?.bidPrice);
      const a = parseFloat(this.quote?.askPrice);
      if (isNaN(b) || isNaN(a)) return "-";
      return Math.abs(a - b).toFixed(2);
    },
    spreadColor() {
      // convert spread string to number
      const s = parseFloat(this.spread);
      if (isNaN(s)) return "transparent";
      if (s < 0.10) return "#d0f0d0";    // light green
      if (s < 0.20) return "#ffff99";    // light yellow
      return "#ffcccc";                  // light red
    },
    elapsedTime() {
      if (this.quoteReceivedTime > 0) {
        let elapsed = Math.max((this.currentTime - this.quoteReceivedTime) / 1000, 0);
        elapsed = Math.floor(elapsed * 2) / 2; // floor to 0.5 increments
        return elapsed.toFixed(1);
      }
      return "0.0";
    },
    freshnessColor() {
      if (this.quoteReceivedTime === 0) return "#eef"; // default light blue
      const elapsed = (this.currentTime - this.quoteReceivedTime) / 1000;
      if (elapsed > 10) return "#ffcccc"; // red - stale
      if (elapsed > 5) return "#ffff99";  // yellow - aging
      return "#eef";                      // default light blue - fresh
    },
  },
  watch: {
    quote: {
      immediate: true,
      handler(newVal) {
        if (newVal) {
          // Use receivedTimestamp if available (added by SingleTicker.vue)
          // Otherwise fall back to parsing quoteET, or use Date.now()
          if (newVal.receivedTimestamp) {
            this.quoteReceivedTime = newVal.receivedTimestamp;
          } else if (newVal.quoteET) {
            const ts = new Date(newVal.quoteET).getTime();
            this.quoteReceivedTime = !isNaN(ts) ? ts : Date.now();
          }
        }
      },
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      this.currentTime = Date.now();
    }, 500);
  },
  beforeUnmount() {
    if (this.timer) clearInterval(this.timer);
  },
  methods: {
    formatPrice(val) {
      if (!val) return "-";
      const num = parseFloat(val);
      return isNaN(num) ? "-" : num.toFixed(2);
    },
  },
};
</script>

<style scoped>
.quote-display {
  font-family: Verdana, Geneva, sans-serif;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: background-color 0.3s ease;
}
.spread {
  padding: 0.2rem 0.4rem;
  border-radius: 0.25rem;
  font-weight: bold;  
}
.elapsed-timer {
  margin-left: 0.5rem;
}
</style>
